import React, { useEffect, useState } from 'react'
import Modal, { modalInterface } from '../../ui/Modal'
import './style.scss'
import { ArrowDownIcon, CustomTokenIcon, QuestionIcon } from '../../ui/Icons'
import { Button } from '../../ui/Button'
import ReactTooltip from 'react-tooltip'
import { useSelector } from 'react-redux'
import { PriceImpact } from '../ImpactPrice'
import { EstimateFee } from '../EstimateFee'
import { useCurrentPair } from '../../../state/currentPair/hook'
import { formatWeiToDisplayNumber } from '../../../utils/formatBalance'
import { BigNumber } from 'ethers'
import {
  bn,
  detectDecimalFromPrice,
  numberToWei,
  weiToNumber
} from '../../../utils/helpers'
import { useSwapInfo } from '../../../state/marketSwap/hooks/useSwapInfo'
import { useMarketSwapAction } from '../../../state/marketSwap/hooks/useMarketSwapAction'
import { Checkbox } from '../../ui/Checkbox'

const HIGH_IMPACT_PRICE = 40
const HIGH_GAS_Amount = bn(numberToWei(2))

interface ConfirmSwapModalInterface extends Omit<modalInterface, 'children'> {
  inputAmount: BigNumber
  children?: any
  outputAmount: BigNumber
  estimateGasAmount: any
  confirmSwap: any
  dists: number[]
  routes: any
  steps: any
}

export const ConfirmSwapModal = ({
  setVisible,
  visible,
  inputAmount,
  outputAmount,
  estimateGasAmount,
  confirmSwap,
  dists,
  routes
}: // steps
ConfirmSwapModalInterface) => {
  const { theme } = useSelector((state: any) => ({
    theme: state.swap.theme
  }))
  // @ts-ignore
  const [confirmWarning, setConfirmWarning] = useState<boolean>(false)
  const { currentPriceImpact, getAmountOutFinished } = useSwapInfo()
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { checkSwapError } = useMarketSwapAction()
  const [error, setError] = useState<string>()

  const { inputToken, outputToken } = useCurrentPair()

  useEffect(() => {
    const checkError = async () => {
      const res = await checkSwapError({
        inputAddress: inputTokenAddress,
        amountIn: inputAmount,
        outputAddress: outputTokenAddress,
        routes
      })
      setError(res)
    }
    if (visible) {
      checkError()
    }
  }, [getAmountOutFinished, visible])

  const checkWarning = () => {
    return (
      currentPriceImpact > HIGH_IMPACT_PRICE ||
      estimateGasAmount.gt(HIGH_GAS_Amount)
    )
  }

  if (!inputToken) {
    return <div />
  }

  return (
    <Modal visible={visible} setVisible={setVisible} title='Confirm Swap'>
      <div className='confirm-swap-modal'>
        <div className='token-info'>
          <span className='icon-and-amount'>
            {inputToken && inputToken.icon ? (
              <img
                src={inputToken.icon}
                alt={inputToken?.symbol}
                className='token-icon'
                width='24'
                height='24'
              />
            ) : (
              <CustomTokenIcon />
            )}
            <span>
              {formatWeiToDisplayNumber(
                inputAmount.toString(),
                4,
                inputToken?.decimal || 18
              )}
            </span>
          </span>
          <span>{inputToken?.symbol}</span>
        </div>
        <div>
          <ArrowDownIcon />
        </div>
        <div className='token-info'>
          <span className='icon-and-amount'>
            {outputToken && outputToken.icon ? (
              <img
                src={outputToken.icon}
                alt={outputToken?.symbol}
                className='token-icon'
                width='24'
                height='24'
              />
            ) : (
              <CustomTokenIcon />
            )}
            <span>
              {formatWeiToDisplayNumber(
                outputAmount.toString(),
                detectDecimalFromPrice(
                  weiToNumber(outputAmount, outputToken?.decimal || 18)
                ),
                outputToken?.decimal || 18
              )}
            </span>
          </span>
          <span>{outputToken?.symbol}</span>
        </div>

        <div className='confirm-swap-modal__footer'>
          {error ? (
            <div className='confirm-swap-modal__error'>
              <span>The transaction cannot succeed due to error:</span>
              <span className='text-danger'>{error}</span>

              {outputAmount.isZero() ? (
                <p className='text-center text-danger'>
                  There may be an issue with the input token.
                </p>
              ) : (
                ''
              )}

              {error?.includes('INSUFFICIENT_OUTPUT_AMOUNT') ? (
                <span>Try increasing your slippage tolerance.</span>
              ) : (
                <span>Please try again</span>
              )}
            </div>
          ) : (
            <div>
              <div className='footer-info__row'>
                <span className='align-item-center'>
                  <span>Price Impact</span>
                  <a data-tip data-for='confirm-swap__impact-price'>
                    <QuestionIcon
                      stroke='#ffffff'
                      className='align-item-center ml-05'
                    />
                  </a>
                  <ReactTooltip
                    id='confirm-swap__impact-price'
                    type={theme}
                    place='bottom'
                  >
                    <p style={{ width: '250px' }} className='text-center'>
                      The influence that the transaction has over the market
                      price of the traded pair.
                    </p>
                  </ReactTooltip>
                </span>
                <PriceImpact
                  dists={dists}
                  // steps={steps}
                  amountIn={inputAmount}
                  amountOut={outputAmount}
                />
              </div>
              <div className='footer-info__row'>
                <span className='align-item-center'>
                  <span>Estimated Fee</span>
                  <a data-tip data-for='confirm-swap__estimate-fee'>
                    <QuestionIcon className='align-item-center ml-05' />
                  </a>
                  <ReactTooltip
                    id='confirm-swap__estimate-fee'
                    type={theme}
                    place='bottom'
                  >
                    <p style={{ width: '250px' }} className='text-center'>
                      Estimated gas fee of the current transaction.
                    </p>
                  </ReactTooltip>
                </span>
                <EstimateFee estimateGasAmount={estimateGasAmount} />
              </div>
              {checkWarning() && (
                <div className='warning-label'>
                  {currentPriceImpact > HIGH_IMPACT_PRICE && (
                    <p className='text-danger text-center'>
                      Price impact is too high
                    </p>
                  )}
                  {estimateGasAmount.gt(HIGH_GAS_Amount) && (
                    <p className='text-danger text-center'>
                      The estimated fee is unusually high. Please make sure the
                      tokens you are swapping work as intended.
                    </p>
                  )}
                  {outputAmount.isZero() ? (
                    <p className='text-danger text-center'>
                      There may be an issue with the input token.
                    </p>
                  ) : (
                    ''
                  )}
                  <div className='text-center'>
                    <label
                      htmlFor='confirm-high-fee'
                      className='confirm-warning__box'
                    >
                      <Checkbox
                        id='confirm-high-fee'
                        className='mr-05'
                        onChange={(e: any) =>
                          setConfirmWarning(e.target.checked)
                        }
                      />
                      Continue anyway?
                    </label>
                  </div>
                </div>
              )}
            </div>
          )}
          <Button
            className='confirm-button'
            disabled={
              !!error ||
              (checkWarning() && !confirmWarning) ||
              outputAmount.isZero()
            }
            onClick={() => {
              confirmSwap()
            }}
          >
            Confirm Swap
          </Button>
        </div>
      </div>
    </Modal>
  )
}
