import IERC20 from '../assets/abi/IERC20.json'
import PairDetail from '../assets/abi/PairDetail.json'
import { BigNumber, ethers } from 'ethers'
import config from '../config'
import _ from 'lodash'
import { div, numberToWei } from '../utils/helpers'
import { SERVICES, STABLECOIN_ADDRESSES } from '../utils/constant'
import { store } from '../state'
import BalanceChecker from '../assets/abi/BalanceChecker.json'
import { getTopReservePoolByAddress } from './tokenService'

export const getBalance = async ({
  tokenAddress,
  account
}: {
  tokenAddress: any
  account: any
}) => {
  try {
    const tokenContract = new ethers.Contract(
      tokenAddress,
      IERC20,
      config.provider
    )
    const res = await tokenContract.functions.balanceOf(account)
    return res.balance || BigNumber.from(0)
  } catch (e) {
    console.error(e)
    return BigNumber.from(0)
  }
}

export const getGasPrice = async () => {
  try {
    return await config.provider.getGasPrice()
  } catch (e) {
    console.error(e)
    return 10 * 10 ** 9
  }
}

export const getPairInfo = async (pairAddress: any) => {
  try {
    const tokenContract = new ethers.Contract(
      '0xf7Eb29dFfcDb11BC82D094e3bffA3E5764117A22',
      PairDetail,
      config.provider
    )
    const res = await tokenContract.functions.query(
      [pairAddress],
      '0x0000100000000000000000000000000000000000000000000000000000000100'
    )
    return res.details[0]
  } catch (e) {
    console.error(e)
    return {}
  }
}

export const getMarketPrice = async ({ pairs }: any) => {
  try {
    pairs = _.uniqBy(pairs, 'pairAddress')
    const pairAddresses = pairs.map((e: any) => e.pairAddress)
    const tokenContract = new ethers.Contract(
      '0xf7Eb29dFfcDb11BC82D094e3bffA3E5764117A22',
      PairDetail,
      config.provider
    )
    const res = await tokenContract.functions.query(
      pairAddresses,
      '0x0000100000000000000000000000000000000000000000000000000000000100'
    )

    const results = {}
    for (const i in pairAddresses) {
      const details = res.details[i]
      let token0, token1
      if (
        details.token1.adr.toLowerCase() === pairs[i].inputAddress.toLowerCase()
      ) {
        token0 = details.token1
        token1 = details.token0
      } else {
        token0 = details.token0
        token1 = details.token1
      }

      const reserve0 = token0.reserve
      const reserve1 = token1.reserve
      results[pairAddresses[i]] = {
        price: div(
          numberToWei(reserve1, token0.decimals || 18),
          numberToWei(reserve0, token1.decimals || 18)
        ),
        reserve: reserve0
      }
    }
    return results
  } catch (e) {
    console.log(e)
    return {}
  }
}

export const getListBalance = async (account: string, tokens: any) => {
  try {
    const provider = config.provider

    const balanceCheckerContract = new ethers.Contract(
      '0x2352c63A83f9Fd126af8676146721Fa00924d7e4',
      BalanceChecker,
      provider
    )

    return await balanceCheckerContract.functions.balances([account], tokens)
  } catch (e) {
    console.log(e)
    return []
  }
}

export const detectOutputAddress = async (inputAddress: any) => {
  let iAddr = inputAddress
  if (iAddr === config.chain[56].addresses.bnb) {
    iAddr = config.chain[56].addresses.wbnb
  }
  console.log(iAddr)
  const pools = await getTopReservePoolByAddress(iAddr)
  const highestPool = pools
    .filter(
      (p: any) =>
        p.address1 &&
        (STABLECOIN_ADDRESSES.includes(p.address1.toLowerCase()) ||
          p.address1.toLowerCase() ===
            config.chain[56].addresses.wbnb.toLowerCase() ||
          p.address1.toLowerCase() ===
            config.chain[56].addresses.cake.toLowerCase())
    )
    .sort((a: any, b: any) => {
      return ethers.BigNumber.from(numberToWei(a.reserve0)).gt(
        numberToWei(b.reserve0)
      )
        ? -1
        : 1
    })[0]
  const state = store.getState()
  if (state.currentPair.inputTokenAddress !== inputAddress) {
    return null
  }
  if (highestPool) {
    const exchange = findExchangeNameByFactory(highestPool.creator)
    const outputAddress =
      highestPool.address0 === iAddr
        ? highestPool.address1
        : highestPool.address0
    return {
      exchange,
      outputAddress
    }
  } else {
    return {
      exchange: '',
      outputAddress: config.chain[56].addresses.busd
    }
  }
}

const findExchangeNameByFactory = (factoryAddress: string) => {
  for (const i in SERVICES) {
    if (SERVICES[i].factory === factoryAddress) {
      return i
    }
  }
  return 'pancake2'
}
