// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'
import { BigNumber } from 'ethers'

export const marketSwap = createSlice({
  name: 'marketSwap',
  initialState,
  reducers: {
    setAmountInReducer: (state, action: PayloadAction<{ amount: string }>) => {
      state.amountIn = action.payload.amount
    },
    setAmountOutReducer: (state, action: PayloadAction<{ amount: string }>) => {
      state.amountOut = action.payload.amount
    },
    setTimeoutReducer: (
      state,
      action: PayloadAction<{
        timeout: number
      }>
    ) => {
      localStorage.setItem('swapTimeout', action.payload.timeout.toString())
      state.timeout = action.payload.timeout
    },
    setSlippageReducer: (
      state,
      action: PayloadAction<{
        slippage: number
      }>
    ) => {
      localStorage.setItem('swapSlippage', action.payload.slippage.toString())
      state.slippage = action.payload.slippage
    },
    setBestSolution: (state, action: PayloadAction<{ bestSolution: any }>) => {
      state.bestSolution = action.payload.bestSolution
    },
    setRoutes: (
      state,
      action: PayloadAction<{
        routes: any
        inputAddress: string
        outputAddress: string
      }>
    ) => {
      state.routes = {
        value: action.payload.routes,
        inputAddress: action.payload.inputAddress,
        outputAddress: action.payload.outputAddress
      }
    },
    setRoutesTest: (
      state,
      action: PayloadAction<{
        routes: any
      }>
    ) => {
      state.routesTest = action.payload.routes
    },
    setRoutesCap: (
      state,
      action: PayloadAction<{
        routes: any
      }>
    ) => {
      state.routesCap = action.payload.routes
    },
    setGasLimit: (
      state,
      action: PayloadAction<{
        gasLimit: BigNumber
        gasAmount: BigNumber
      }>
    ) => {
      state.gasLimit = action.payload.gasLimit
      state.gasAmount = action.payload.gasAmount
      state.loadingGasAmount = false
    },
    setGetAmountOutCallback: (
      state,
      action: PayloadAction<{
        nextCallBack: any
      }>
    ) => {
      state.getAmountOutCallback = action.payload.nextCallBack
    },
    setLoadingAmountOutReduce: (
      state,
      action: PayloadAction<{
        loading: boolean
      }>
    ) => {
      state.loadingGasAmount = action.payload.loading
    },
    setCurrentPriceImpact: (
      state,
      action: PayloadAction<{
        priceImpact: number
      }>
    ) => {
      state.currentPriceImpact = action.payload.priceImpact
    },
    setIsFinishedReduce: (
      state,
      action: PayloadAction<{
        isFinished: boolean
      }>
    ) => {
      state.getAmountOutFinished = action.payload.isFinished
    }
  }
})

// Actions
export const {
  setGasLimit,
  setRoutesCap,
  setBestSolution,
  setSlippageReducer,
  setRoutes,
  setRoutesTest,
  setAmountInReducer,
  setAmountOutReducer,
  setTimeoutReducer,
  setGetAmountOutCallback,
  setCurrentPriceImpact,
  setLoadingAmountOutReduce,
  setIsFinishedReduce
} = marketSwap.actions

export default marketSwap.reducer
