import { mul, numberToWei, weiToNumber } from '../../utils/helpers'
import { store } from '../../state'
import { getConfig } from '../../config'
import { SERVICES } from '../../utils/constant'
import { utils } from 'ethers'
import { toast } from 'react-toastify'

const history = {}

const convertResolution = (oldResolution: string) => {
  if (oldResolution.includes('D')) {
    return oldResolution
  } else {
    if (Number(oldResolution) < 60) {
      return oldResolution
    } else {
      return Number(oldResolution) / 60 + 'H'
    }
  }
}

export const resolutionToSeconds = {
  '1': 60,
  '5': 300,
  '15': 900,
  '1H': 3600,
  '60': 3600,
  '4h': 14400,
  '240': 14400,
  '1D': 86400
}

export default {
  history: history,

  getRoutes: async function (inputAddress: string, outputAddress: string) {
    try {
      const state = store.getState()
      const tokens = state.tokens.tokens
      const res = await fetch(
        `${getConfig(
          'SWAP_API_ENDPOINT'
        )}route/${inputAddress}/${outputAddress}`
      ).then((r) => r.json())
      const routeDetail: any[] = res[0] ? res[0].route : []
      const routes = []
      const chartName = []

      for (let i = 0; i < routeDetail.length; i++) {
        routes.push(utils.getAddress(routeDetail[i].ai))
        routes.push(utils.getAddress(routeDetail[i].a))

        chartName.push(tokens[utils.getAddress(routeDetail[i].ai)]?.symbol)
        chartName.push(
          this.getAmmNameByAddress(utils.getAddress(routeDetail[i].c))
        )
      }

      routes.push(outputAddress)
      chartName.push(tokens[outputAddress]?.symbol)

      return {
        routeDetail,
        routes,
        chartName
      }
    } catch (e) {
      console.error(e)
      toast.error('Routes cannot be load, please try again later.')
      return {
        routeDetail: [],
        routes: [],
        chartName: []
      }
    }
  },

  getAmmNameByAddress(routerAddress: string) {
    for (let i in SERVICES) {
      if (SERVICES[i].factory === routerAddress) {
        return i
      }
    }
    return ''
  },

  getBars: function ({
    currencyCode,
    route,
    resolution,
    limit,
    to
  }: {
    currencyCode?: string
    route: string
    resolution: string
    limit: number
    to: number
  }) {
    const state = store.getState()
    const tokens = state.tokens.tokens

    const routeArr = route.split('/')
    const inputAddress = routeArr[0]
    const outputAddress = routeArr[routeArr.length - 1]
    const inputToken: any = tokens[inputAddress]
    const outputToken: any = tokens[outputAddress]

    const convertUsdFlag = currencyCode === 'USD' ? '&u' : ''
    const outputPrice =
      currencyCode === 'USD'
        ? state.tokens?.tokenPrices[outputAddress] || '1'
        : '1'
    const q = route.split('/').join(',')

    return fetch(
      `${getConfig(
        'CHART_API_ENDPOINT'
      )}candleline4?q=${q}&r=${convertResolution(
        resolution
      )}&l=${limit}&t=${to}${convertUsdFlag}`
    )
      .then((r: any) => r.json())
      .then((response: any) => {
        const bars = []
        if (
          response &&
          response.s === 'ok' &&
          response.b &&
          response.b.length > 0
        ) {
          const decimal = 18 + outputToken.decimal - inputToken.decimal

          for (let i = 0; i < response.b.length; i++) {
            bars.push({
              low: mul(
                outputPrice,
                weiToNumber(numberToWei(response.l[i]), decimal)
              ),
              open: mul(
                outputPrice,
                weiToNumber(numberToWei(response.o[i]), decimal)
              ),
              block: response.b[i],
              time:
                new Date().getTime() -
                (state.setting.headBlock - response.b[i]) *
                  state.setting.timePerBlock,
              volume: weiToNumber(response.v[i].split('.')[0], 18),
              close: mul(
                outputPrice,
                weiToNumber(numberToWei(response.c[i]), decimal)
              ),
              high: mul(
                outputPrice,
                weiToNumber(numberToWei(response.h[i]), decimal)
              )
            })
          }
          return bars
        } else {
          return []
        }
      })
  }
}
