import { useDispatch, useSelector } from 'react-redux'
import { setBlockConfig as setBlockConfigReduce } from './reducer'

export const useSetting = () => {
  const { headBlock, timePerBlock, loadedTimePerBlock } = useSelector(
    (state: any) => {
      return {
        headBlock: state.setting.headBlock,
        timePerBlock: state.setting.timePerBlock,
        loadedTimePerBlock: state.setting.loadedTimePerBlock
      }
    }
  )
  const dispatch = useDispatch()

  const setBlockConfig = ({
    headBlock,
    timePerBlock,
    loadedTimePerBlock
  }: {
    headBlock?: number
    timePerBlock?: number
    loadedTimePerBlock?: boolean
  }) => {
    dispatch(
      setBlockConfigReduce({ headBlock, timePerBlock, loadedTimePerBlock })
    )
  }

  return {
    setBlockConfig,
    headBlock,
    timePerBlock,
    loadedTimePerBlock
  }
}
