export const ExchangeIcon = ({
  exchange,
  className
}: {
  className?: string
  exchange:
    | 'pancake'
    | 'pancake2'
    | 'ape'
    | 'jul'
    | 'bakery'
    | 'jet'
    | 'cafe'
    | 'mdex'
    | 'bi'
    | 'baby'
}) => {
  switch (exchange) {
    case 'pancake':
      return <PancakeIcon className={className} />
    case 'pancake2':
      return <Pancake2Icon className={className} />
    case 'ape':
      return <ApeIcon className={className} />
    case 'jul':
      return <JulIcon className={className} />
    case 'bakery':
      return <BakeryIcon className={className} />
    case 'jet':
      return <JetswapIcon className={className} />
    case 'cafe':
      return <CafeSwapIcon className={className} />
    case 'mdex':
      return <MdexSwapIcon className={className} />
    case 'bi':
      return <BiswapIcon className={className} />
    case 'baby':
      return <BabySwapIcon className={className} />
    default:
      return <BakeryIcon className={className} />
  }
}

