import { useDispatch, useSelector } from 'react-redux'
import { useCurrentPair } from '../../currentPair/hook'
import { BigNumber, ethers } from 'ethers'
import {
  setWalletTxs,
  setWalletTxsFromAmm,
  setWalletTxsFromSwapx
} from '../reducer'
import { useEffect, useState } from 'react'
import { getPairInfo } from '../../../service/commonService'
import config from '../../../config'
import AsyncTaskThrottle from 'async-task-throttle-on-response'
import { convertBNBAddressToWBNB } from '../../../utils/helpers'
import { useSetting } from '../../setting/hook'

// import { STABLECOIN_ADDRESSES } from '../../../utils/constant'
const { getPoolAddressHeximal } = require('bsc_util')

const bn = BigNumber.from

export const useWalletTx = () => {
  const { exchange, inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { timePerBlock, headBlock } = useSetting()
  const [walletTxsInPair, setWalletTxsInPair] = useState()
  const { walletTxs, walletTxsFromAmm, walletTxsFromSwapX } = useSelector(
    (state: any) => {
      return {
        walletTxs: state.wallet.walletTxs,
        walletTxsFromAmm: state.wallet.walletTxsFromAmm,
        walletTxsFromSwapX: state.wallet.walletTxsFromSwapX
      }
    }
  )

  const dispatch = useDispatch()
  const task = async (callback: any) => {
    await callback()
  }
  const throttleTask = AsyncTaskThrottle.create(task, 1, 5000)

  const getTxsInCurrentPair = async () => {
    const result: any = []
    let pairAddress = ''
    try {
      pairAddress = getPoolAddressHeximal(
        exchange,
        convertBNBAddressToWBNB(inputTokenAddress),
        convertBNBAddressToWBNB(outputTokenAddress)
      )
    } catch (e) {
      return
    }
    if (!pairAddress) return
    const pairInfo = await getPairInfo(pairAddress)

    walletTxsFromAmm
      .filter((tx: any) => {
        return tx.address.toLowerCase() === pairAddress.toLowerCase()
      })
      .forEach((tx: any) => {
        const r: any = {}
        r.inputToken = !tx.amount0In.isZero()
          ? pairInfo?.token0?.adr
          : pairInfo?.token1?.adr
        r.outputToken = !tx.amount0In.isZero()
          ? pairInfo?.token1?.adr
          : pairInfo?.token0?.adr

        if (
          inputTokenAddress === config.chain[56].addresses.bnb ||
          outputTokenAddress === config.chain[56].addresses.bnb
        ) {
          r.inputToken = convertBNBAddressToWBNB(r.inputToken)
          r.outputToken = convertBNBAddressToWBNB(r.outputToken)
        }

        r.spentAmount = !tx.amount0In.isZero() ? tx.amount0In : tx.amount1In
        r.returnAmount = !tx.amount0Out.isZero() ? tx.amount0Out : tx.amount1Out

        result.push({
          time: tx.time,
          transactionHash: tx.transactionHash,
          id: tx.id,
          ...r
        })
      })

    walletTxsFromSwapX
      .filter((tx: any) => {
        return (
          (tx.inputToken.toLowerCase() === inputTokenAddress.toLowerCase() &&
            tx.outputToken.toLowerCase() ===
              outputTokenAddress.toLowerCase()) ||
          (tx.outputToken.toLowerCase() === inputTokenAddress.toLowerCase() &&
            tx.inputToken.toLowerCase() === outputTokenAddress.toLowerCase())
        )
      })
      .forEach((tx: any) => {
        result.push(tx)
      })

    dispatch(setWalletTxs({ txs: result }))
  }

  const fetchWalletTxs = (account: string) => {
    throttleTask(() => fetchSwapxTxs(account))
    throttleTask(() => fetchTxsFromAmm(account))
  }

  const fetchSwapxTxs = async (account: string) => {
    if (!account) {
      dispatch(setWalletTxs({ txs: [] }))
      return
    }
    const txs: any = []
    let logs: any = []
    const topic1 = '0x' + '0'.repeat(24) + account.slice(2)
    const res = await fetch(
      `${config.apiBscScan}/api?module=logs&action=getLogs&fromBlock=4993830&toBlock=5993899932&topic1=${topic1}&topic0=0xbd99c6719f088aa0abd9e7b7a4a635d1f931601e9f304b538dc42be25d8c65c6`
    ).then((r: any) => r.json())

    if (res.status === '1') {
      logs = res.result
    } else {
      throttleTask(() => fetchSwapxTxs(account))
      return
    }

    logs.forEach((log: any, key: number) => {
      const inputToken = ethers.utils.getAddress(log.topics[2].substr(2 + 24))
      const outputToken = ethers.utils.getAddress(log.topics[3].substr(2 + 24))
      const data = log.data.substr(2).match(/.{1,64}/g)
      const blockNumber = Number(log.blockNumber)

      const dataMap = data.map((a: any) => bn('0x' + a))
      let [
        //@ts-ignore
        dstReceiver,
        amount,
        spentAmount,
        returnAmount,
        //@ts-ignore
        minReturnAmount,
        //@ts-ignore
        referrer
      ] = dataMap

      txs.push({
        id: key,
        time:
          new Date().getTime() -
          Math.abs(headBlock - blockNumber) * timePerBlock,
        inputToken,
        outputToken,
        amount,
        spentAmount: spentAmount.isZero() ? amount : spentAmount,
        returnAmount,
        transactionHash: log.transactionHash
      })
    })
    dispatch(setWalletTxsFromSwapx({ txs }))
  }

  const fetchTxsFromAmm = async (account: string) => {
    if (!account) {
      return
    }
    const txs: any = []
    let logs: any = []
    const topic2 = '0x' + '0'.repeat(24) + account.slice(2)
    const res = await fetch(
      `${config.apiBscScan}/api?module=logs&action=getLogs&fromBlock=4993830&toBlock=5993899932&topic2=${topic2}&topic0=0xd78ad95fa46c994b6551d0da85fc275fe613ce37657fb8d5e3d130840159d822`
    ).then((r: any) => r.json())
    if (res.status === '1') {
      logs = res.result
    } else {
      throttleTask(() => fetchTxsFromAmm(account))
      return
    }

    logs.forEach((log: any, key: number) => {
      const sender = ethers.utils.getAddress(log.topics[1].substr(2 + 24))
      const recipient = ethers.utils.getAddress(log.topics[2].substr(2 + 24))
      const data = log.data.substr(2).match(/.{1,64}/g)
      const blockNumber = Number(log.blockNumber)

      const [amount0In, amount1In, amount0Out, amount1Out] = data.map(
        (a: any) => bn('0x' + a)
      )

      txs.push({
        id: key,
        time:
          new Date().getTime() -
          Math.abs(headBlock - blockNumber) * timePerBlock,
        sender,
        address: log.address,
        recipient,
        amount0In,
        amount1In,
        amount0Out,
        amount1Out,
        transactionHash: log.transactionHash
      })
    })

    dispatch(setWalletTxsFromAmm({ txs }))
  }

  useEffect(() => {
    const result = walletTxs.filter((tx: any) => {
      return (
        tx.inputToken.toLowerCase() === inputTokenAddress.toLowerCase() &&
        tx.outputToken.toLowerCase() === outputTokenAddress.toLowerCase()
      )
    })

    setWalletTxsInPair(result)
  }, [walletTxs, inputTokenAddress, outputTokenAddress])

  return {
    walletTxs,
    walletTxsFromAmm,
    walletTxsFromSwapX,
    fetchWalletTxs,
    walletTxsInPair,
    fetchTxsFromAmm,
    getTxsInCurrentPair
  }
}
