import { createStore, applyMiddleware, combineReducers } from 'redux'
import thunk from 'redux-thunk'
import { reduceSetStateData } from '../store/actions/initSettingData'
import tokensReducer from './token/reducer'
import currentPairReduce from './currentPair/reducer'
import walletReduce from './wallet/reducer'
import customWeb3ReactReduce from './customWeb3React/reducer'
import chartReduce from './chart/reducer'
import marketSwapReduce from './marketSwap/reducer'
import settingReduce from './setting/reducer'

export const store = createStore(
  combineReducers({
    swap: reduceSetStateData,
    currentPair: currentPairReduce,
    tokens: tokensReducer,
    wallet: walletReduce,
    web3React: customWeb3ReactReduce,
    chart: chartReduce,
    setting: settingReduce,
    marketSwap: marketSwapReduce
  }),
  applyMiddleware(thunk)
)
