// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'
export const tokens = createSlice({
  name: 'chart',
  initialState,
  reducers: {
    setPriceScale: (state, action: PayloadAction<{ priceScale: any }>) => {
      state.priceScale = action.payload.priceScale
    },
    setChartInfo: (
      state,
      action: PayloadAction<{ name: any; routerArr: any }>
    ) => {
      state.symbolName = action.payload.name
      state.routeArr = action.payload.routerArr
    }
  }
})

// Actions
export const { setPriceScale, setChartInfo } = tokens.actions

export default tokens.reducer
