import { utils, BigNumber, ethers } from 'ethers'
import { toast } from 'react-toastify'
import config from '../config'
import React from 'react'
import { SERVICES } from './constant'
import _ from 'lodash'

export const tokenNameToDisplay = (name: string, length = 40) => {
  return name.slice(0, length) + (name.length > length ? '...' : '')
}

export const messageAndViewOnBsc = ({
  title = '',
  message = '',
  hash = ''
}: {
  title: string
  message?: string
  hash?: string
}) => {
  return (
    <div>
      <h4>{title}</h4>
      {message && <p className='mt-1'>{message}</p>}
      {hash && (
        <p className='mt-1'>
          <a
            href={`${config.explorer}/tx/${hash}`}
            className='bsc-link'
            target='_blank'
            rel='noreferrer'
          >
            View on BscScan
          </a>
        </p>
      )}
    </div>
  )
}

export const shortenAddressString = (address: string) => {
  return (
    address.slice?.(0, 8) +
    '...' +
    address.slice?.(address.length - 6, address.length)
  )
}

export const weiToNumber = (wei: any, decimal: number = 18) => {
  if (!wei || !Number(wei)) return '0'
  wei = wei.toString()
  return utils.formatUnits(wei, decimal)
}

export const numberToWei = (number: any, decimal: number = 18) => {
  number = number.toString()

  const arr = number.split('.')
  if (arr[1] && arr[1].length > decimal) {
    arr[1] = arr[1].slice(0, decimal)
    number = arr.join('.')
  }

  return utils.parseUnits(number, decimal).toString()
}

export const formatFloat = (number: any, decimal: number = 2) => {
  number = number.toString()
  const arr = number.split('.')
  if (arr.length > 1) {
    arr[1] = arr[1].slice(0, decimal)
  }
  return arr.join('.')
}

export const formatPercent = (floatNumber: any, decimal: number = 2) => {
  floatNumber = floatNumber.toString()
  return formatFloat(weiToNumber(numberToWei(floatNumber), 16), decimal)
}

export const formatDate = (timestamp: number) => {
  const date = new Date(timestamp)
  const d = date.getDate()
  const m = date.getMonth() + 1 // Month from 0 to 11
  const y = date.getFullYear()
  return y + '-' + (m <= 9 ? '0' + m : m) + '-' + (d <= 9 ? '0' + d : d)
}

export const formatTime = (timestamp: number) => {
  const date = new Date(timestamp)
  const h = date.getHours()
  const m = date.getMinutes() + 1 // Month from 0 to 11
  const s = date.getSeconds()
  return (
    (h <= 9 ? '0' + h : h) +
    ':' +
    (m <= 9 ? '0' + m : m) +
    ':' +
    (s <= 9 ? '0' + s : s)
  )
}

export const mul = (a: any, b: any) => {
  const result = weiToNumber(
    BigNumber.from(numberToWei(a)).mul(numberToWei(b)),
    36
  )
  const arr = result.split('.')
  arr[1] = arr[1]?.slice(0, 18)
  return arr.join('.')
}

export const sub = (a: any, b: any) => {
  return weiToNumber(BigNumber.from(numberToWei(a)).sub(numberToWei(b)))
}

export const div = (a: any, b: any) => {
  if (!b || b.toString() === '0') {
    return '0'
  }
  return weiToNumber(BigNumber.from(numberToWei(a, 36)).div(numberToWei(b)))
}

export const add = (a: any, b: any) => {
  return weiToNumber(BigNumber.from(numberToWei(a)).add(numberToWei(b)))
}

export const maxBN = (a: BigNumber, b: BigNumber) => {
  if (a.gt(b)) {
    return a
  }
  return b
}

export const minBN = (a: BigNumber, b: BigNumber) => {
  if (a.lt(b)) {
    return a
  }
  return b
}

export function hasDuplicates(array: any) {
  var valuesSoFar = Object.create(null)
  for (var i = 0; i < array.length; ++i) {
    if (!array[i]) return false

    var value = array[i].toLowerCase()
    if (value in valuesSoFar) {
      return true
    }
    valuesSoFar[value] = true
  }
  return false
}

export const showTxErrorMessage = (e: any) => {
  console.error(e)
  if (e.data && e.data.message) {
    toast.error(e.data.message)
  } else {
    toast.error(e.message || 'Error')
  }

  return ethers.BigNumber.from(0)
}

export const max = (number1: any, number2: any) => {
  number1 = number1.toString()
  number2 = number2.toString()

  return BigNumber.from(numberToWei(number1)).lt(
    BigNumber.from(numberToWei(number2))
  )
    ? number2
    : number1
}

export const decodePath = (path: string) => {
  let inputTokenAddr = ''
  let outputTokenAddr = ''
  const arr = path.split('/')
  const swapIndex = arr.indexOf('swap')
  let tab: any = arr[swapIndex + 1] || ''
  tab = ['chart', 'swap', 'summary', 'reward'].includes(tab) ? tab : null

  if (arr.includes('token')) {
    const tokenIndex = arr.findIndex((e) => e === 'token')
    inputTokenAddr =
      arr[tokenIndex + 1] && ethers.utils.isAddress(arr[tokenIndex + 1])
        ? ethers.utils.getAddress(arr[tokenIndex + 1].toLowerCase())
        : ''

    outputTokenAddr =
      arr[tokenIndex + 2] && ethers.utils.isAddress(arr[tokenIndex + 2])
        ? ethers.utils.getAddress(arr[tokenIndex + 2].toLowerCase())
        : ''
  }

  return {
    inputTokenAddr,
    outputTokenAddr,
    tab
  }
}

export const shiftedBy = (number: any, decimal: number) => {
  return weiToNumber(numberToWei(number), 18 + decimal)
}

export const detectDecimalFromPrice = (price: number | string) => {
  if (Number(price || 0) === 0 || Number(price || 0) >= 1) {
    return 4
  } else {
    const rate = weiToNumber(
      BigNumber.from(numberToWei(1, 36))
        .div(numberToWei(price || 0))
        .toString()
    )
    return rate.split('.')[0].length + 3
  }
}

export const convertBNBAddressToWBNB = (address: string) => {
  return address.toLowerCase() === config.chain[56].addresses.bnb.toLowerCase()
    ? config.chain[56].addresses.wbnb
    : address
}

export const bn = ethers.BigNumber.from

export const getRouterByFactory = (factory: string) => {
  return _.find(SERVICES, { factory: ethers.utils.getAddress(factory) })?.router
}

export const getFactory = (router: string) => {
  return _.find(SERVICES, { router: ethers.utils.getAddress(router) })?.factory
}

export const getAmmNameFromRoute = (router: string) => {
  for (let i in SERVICES) {
    if (SERVICES[i].factory === ethers.utils.getAddress(router)) {
      return i
    }
  }
  return ''
}

export const formatAddress = (address: string) => {
  if (ethers.utils.isAddress(address.toLowerCase())) {
    return ethers.utils.getAddress(address.toLowerCase())
  } else {
    return ''
  }
}

export const convertRouteReserveToString = (routes: any) => {
  return routes.map((swapData: any) => {
    return {
      ...swapData,
      liquidity: swapData.toString(),
      route: swapData.route.map((r: any) => {
        r.ri = r.ri.toString()
        r.ro = r.ro.toString()
        return r
      })
    }
  })
}

export const parseCallStaticError = (error: any) => {
  const message = error.data?.message
    ? error.data?.message?.replace('check error: ', '') || 'Error'
    : error.message
  if (message.includes('reason="')) {
    const arr = message.split('reason="')
    const m = arr[1]
    return m?.split('"')[0]
  }
  return message
}
