import { CHAIN_ID_TO_NAME, STABLECOIN_SYMBOLS } from '../../utils/constant'
import {
  RESET_MARKET_ORDER,
  SET_OLD_MARKET_ORDERS,
  UPDATE_NEW_MARKET_ODER
} from './marketOrders'
import {
  SET_CHART_HAVE_NO_DATA,
  SET_FIRST_BLOCK,
  SET_LAST_CANDLE
} from './chart'

export const SET_SINGLE_DATA_WITH_KEY_AND_VALUE = Symbol(
  'SET_SINGLE_DATA_WITH_KEY_AND_VALUE'
)
export const ADD_TOKEN_AND_SET_INPUT_TOKEN = Symbol(
  'ADD_TOKEN_AND_SET_INPUT_TOKEN'
)
export const ADD_TOKEN_AND_SET_OUTPUT_TOKEN = Symbol(
  'ADD_TOKEN_AND_SET_OUTPUT_TOKEN'
)
export const ADD_BOOKMARK_ADDRESS = Symbol('ADD_BOOKMARK_ADDRESS')
export const ADD_TOKEN_AND_SET_BOOKMARK = Symbol('ADD_TOKEN_AND_SET_BOOKMARK')
export const SET_RECENT_TOKENS = Symbol('SET_RECENT_TOKENS')
export const ADD_CUSTOM_TOKEN = Symbol('ADD_CUSTOM_TOKEN')

const SET_GAS_PRICE = Symbol('SET_TRANSACTION_SPEED')
const SET_SLIPPAGE = Symbol('SET_SLIPPAGE')
const UPDATE_SWAP_ALLOWANCE_IN_LIST = Symbol('UPDATE_SWAP_ALLOWANCE_IN_LIST')
const SET_INPUT_ADDRESS_TOKEN = Symbol('SET_INPUT_ADDRESS_TOKEN')
const SET_OUTPUT_ADDRESS_TOKEN = Symbol('SET_OUTPUT_ADDRESS_TOKEN')
const SET_OUTPUT_ADDRESS_AND_EXCHANGE = Symbol(
  'SET_OUTPUT_ADDRESS_AND_EXCHANGE'
)
export const SELECT_NEW_TOKEN_FROM_API = Symbol('SELECT_NEW_TOKEN_FROM_API')
const SET_INPUT_AND_OUTPUT_ADDRESS_TOKEN = Symbol(
  'SET_INPUT_AND_OUTPUT_ADDRESS_TOKEN'
)
export const ADD_TOKEN = Symbol('ADD_TOKEN')
const SET_TOKENS = Symbol('SET_TOKENS')

const initialSettingState: any = {
  chainId: null,
  slippage: localStorage.getItem('swapSlippage')
    ? Number(localStorage.getItem('swapSlippage'))
    : 0.5,
  gasPrice: localStorage.getItem('swapGasPrice') || 5 * 10 ** 9,
  gasPriceStandard: 5 * 10 ** 9,
  lastCandle: {},
  firstBlock: {},
  contracts: {},
  inputTokenAddress: null,
  outputTokenAddress: null,
  tokensSwapAllowance: {},
  tab: 'chart',
  routerHistory: null,
  currentPath: '/swap',
  prices: {
    inputTokenWithUsd: null,
    outputTokenWithUsd: null,
    pair: null
  },
  marketOrders: [],
  lastDayCandle: {},
  pairIsReversed: false,
  loadingPrice: false,
  loadingChart: true,
  topPools: [],
  topPoolsToDisplay: [],
  timePerBlock: 3028,
  headBlock: 15725491,
  loadedTimePerBlock: false,
  balances: {},
  referralAddress: null,
  noChartData: false
}

export const setGasPrice = (gasPrice: any) => (dispatch: any) => {
  localStorage.setItem('swapGasPrice', gasPrice)
  dispatch({
    type: SET_GAS_PRICE,
    data: gasPrice
  })
}

export const setSlippage = (slippage: any) => (dispatch: any) => {
  localStorage.setItem('swapSlippage', slippage)
  dispatch({
    type: SET_SLIPPAGE,
    data: slippage
  })
}

export const setSingleStateData =
  (key: string, value: any) => (dispatch: any) =>
    dispatch?.({
      type: SET_SINGLE_DATA_WITH_KEY_AND_VALUE,
      data: {
        [key]: value
      }
    })

export const setTab =
  (tab: 'swap' | 'chart' | 'summary' | 'reward') => (dispatch: any) => {
    setSingleStateData('tab', tab)(dispatch)
  }

export const setInputAddressToken = (address: string) => (dispatch: any) => {
  dispatch({
    type: SET_INPUT_ADDRESS_TOKEN,
    data: address
  })
}

export const setOutputAddressToken = (address: string) => (dispatch: any) => {
  dispatch({
    type: SET_OUTPUT_ADDRESS_TOKEN,
    data: address
  })
}

export const setOutputAddressAndExchange =
  (address: string, exchange: string) => (dispatch: any) => {
    dispatch({
      type: SET_OUTPUT_ADDRESS_AND_EXCHANGE,
      data: {
        address,
        exchange
      }
    })
  }

export const setInputAndOutputToken =
  (inputAddress: string, outputAddress: string) => (dispatch: any) => {
    dispatch({
      type: SET_INPUT_AND_OUTPUT_ADDRESS_TOKEN,
      data: {
        inputAddress,
        outputAddress
      }
    })
  }

export const reduceSetStateData = (
  state = initialSettingState,
  action: any
) => {
  switch (action.type) {
    case SET_SINGLE_DATA_WITH_KEY_AND_VALUE:
      return {
        ...state,
        ...action.data
      }
    case SET_GAS_PRICE:
      return {
        ...state,
        gasPrice: action.data
      }
    case SET_OUTPUT_ADDRESS_AND_EXCHANGE:
      return {
        ...state,
        outputTokenAddress: action.data.address,
        exchange: action.data.exchange
      }
    case SET_SLIPPAGE:
      return {
        ...state,
        slippage: action.data
      }
    case UPDATE_SWAP_ALLOWANCE_IN_LIST:
      return {
        ...state,
        tokensSwapAllowance: {
          ...state.tokensSwapAllowance,
          [action.data.address]: action.data.allowance
        }
      }
    case SET_INPUT_AND_OUTPUT_ADDRESS_TOKEN:
      if (state.routerHistory) {
        let newPath = '/swap'
        if (state.chainId && CHAIN_ID_TO_NAME[state.chainId]) {
          newPath = '/' + CHAIN_ID_TO_NAME[state.chainId] + newPath
        }
        newPath += '/token/' + action.data.inputAddress
        state.routerHistory.push(newPath)
      }
      return {
        ...state,
        inputTokenAddress: action.data.inputAddress,
        outputTokenAddress: action.data.outputAddress,
        first: true
      }
    case SELECT_NEW_TOKEN_FROM_API:
      if (state.routerHistory) {
        let newPath = '/swap'
        if (state.chainId && CHAIN_ID_TO_NAME[state.chainId]) {
          newPath = '/' + CHAIN_ID_TO_NAME[state.chainId] + newPath
        }
        newPath += '/token/' + action.data.inputAddress
        state.routerHistory.push(newPath)
      }
      return {
        ...state,
        inputTokenAddress: action.data.inputAddress,
        outputTokenAddress: action.data.outputAddress,
        token: {
          ...state.tokens,
          [action.data.customToken.address]: action.data.customToken
        },
        first: true
      }
    case SET_INPUT_ADDRESS_TOKEN:
      if (
        state.routerHistory &&
        !STABLECOIN_SYMBOLS.includes(state.tokens[action.data]?.symbol)
      ) {
        let newPath = '/swap'
        if (state.chainId && CHAIN_ID_TO_NAME[state.chainId]) {
          newPath = '/' + CHAIN_ID_TO_NAME[state.chainId] + newPath
        }
        newPath += '/token/' + action.data
        state.routerHistory.push(newPath)
      }
      return {
        ...state,
        inputTokenAddress: action.data,
        first: true
      }
    case SET_OUTPUT_ADDRESS_TOKEN:
      return {
        ...state,
        outputTokenAddress: action.data,
        first: true
      }
    case SET_OLD_MARKET_ORDERS:
      return {
        ...state,
        marketOrders: [...state.marketOrders, ...action.data].slice(0, 100)
      }
    case UPDATE_NEW_MARKET_ODER:
      return {
        ...state,
        marketOrders: [action.data, ...state.marketOrders].slice(0, 100)
      }
    case RESET_MARKET_ORDER:
      return {
        ...state,
        marketOrders: []
      }
    case SET_TOKENS:
      return {
        ...state,
        tokens: {
          ...state.tokens,
          ...action.data
        }
      }
    case ADD_CUSTOM_TOKEN:
      console.log(action.data)
      if (
        state.tokens[action.data.address] &&
        state.tokens[action.data.address].icon
      ) {
        return state
      }
      return {
        ...state,
        tokens: {
          ...state.tokens,
          [action.data.address]: action.data
        }
      }
    case ADD_TOKEN_AND_SET_INPUT_TOKEN:
      return {
        ...state,
        tokens: {
          ...state.tokens,
          [action.data.address]: action.data
        },
        inputTokenAddress: action.data.address
      }
    case ADD_TOKEN_AND_SET_OUTPUT_TOKEN:
      return {
        ...state,
        tokens: {
          ...state.tokens,
          [action.data.address]: action.data
        },
        outputTokenAddress: action.data.address
      }
    case ADD_TOKEN_AND_SET_BOOKMARK:
      // @ts-ignore
      let newBookmarks1 = []
      // @ts-ignore
      const existIndex1 = state.bookmarkAddresses.findIndex(
        (e: string) => e === action.data.address
      )
      if (existIndex1 > -1) {
        newBookmarks1 = state.bookmarkAddresses.filter(
          (e: string) => e !== action.data.address
        )
      } else {
        newBookmarks1 = [action.data.address, ...state.bookmarkAddresses]
      }

      localStorage.setItem('bookmarkAddresses', JSON.stringify(newBookmarks1))
      return {
        ...state,
        bookmarkAddresses: newBookmarks1,
        tokens: {
          ...state.tokens,
          [action.data.address]: action.data
        }
      }
    case ADD_BOOKMARK_ADDRESS:
      // @ts-ignore
      let newBookmarks = []
      // @ts-ignore
      const existIndex = state.bookmarkAddresses.findIndex(
        (e: string) => e === action.data
      )
      if (existIndex > -1) {
        newBookmarks = state.bookmarkAddresses.filter(
          (e: string) => e !== action.data
        )
      } else {
        newBookmarks = [action.data, ...state.bookmarkAddresses]
      }

      localStorage.setItem('bookmarkAddresses', JSON.stringify(newBookmarks))
      return {
        ...state,
        bookmarkAddresses: newBookmarks
      }
    case ADD_TOKEN:
      return {
        ...state,
        tokens: {
          ...state.tokens,
          [action.data.address]: action.data
        }
      }
    case SET_RECENT_TOKENS:
      return {
        ...state,
        recentTokens: action.data
      }
    case SET_LAST_CANDLE:
      return {
        ...state,
        lastCandle: {
          [action.data.pair]: action.data.candle
        }
      }
    case SET_FIRST_BLOCK:
      return {
        ...state,
        firstBlock: {
          ...state.firstBlock,
          [action.data.pair]: action.data.block
        }
      }
    case SET_CHART_HAVE_NO_DATA:
      return {
        ...state,
        noChartData: action.data
      }
    default:
      return state
  }
}
