import React, { useEffect, useState } from 'react'
import './style.scss'
import {
  calcClaimParams,
  claim,
  estimateGas,
  getLeaderBoard
} from '../../service/rewardService'
import { Table } from '../ui/Table'
import config from '../../config'
import { useSelector } from 'react-redux'
import { Button } from '../ui/Button'
import { toast } from 'react-toastify'
import {
  formatFloat,
  messageAndViewOnBsc,
  weiToNumber
} from '../../utils/helpers'
import { ethers } from 'ethers'
import { LoadingIcon } from '../ui/Icons'
import { useWalletBalance } from '../../state/wallet/hook'
const dollarUSLocale = Intl.NumberFormat('en-US')

const LIMIT = 50

export const RewardTable = () => {
  const { account, provider, gasPrice } = useSelector((state: any) => ({
    account: state.swap.account,
    provider: state.swap.provider,
    gasPrice: state.swap.gasPrice
  }))
  const { fetchBalanceOfAllToken } = useWalletBalance()

  const [leaderboard, setLeaderboard] = useState<any>([])
  // const [page, setPage] = useState<number>(1)
  // const [totalPage, setTotalPage] = useState<number>(1)
  const [totalReward, setTotalReward] = useState<any>(0)
  const [proofTranches, setProofTranches] = useState<any>([])
  const [amounts, setAmounts] = useState<any>([])
  const [trancheIds, setTrancheIds] = useState<any>([])
  const [loadingClaim, setLoadingClaim] = useState<boolean>(false)
  const [loadingClaimParams, setLoadingClaimParams] = useState<boolean>(false)

  useEffect(() => {
    fetchLeaderboard()
  }, [])

  useEffect(() => {
    if (provider && provider) {
      fetchRewardAndClaimParams()
    }
  }, [account, provider])

  //@ts-ignore
  const fetchLeaderboard = async () => {
    try {
      const res = await getLeaderBoard({
        from: new Date().getTime() - 86400 * 7000,
        to: new Date().getTime(),
        limit: LIMIT,
        page: 1
      })
      setLeaderboard(res.docs)
      // setTotalPage(Math.ceil(res.pageInfo.total / LIMIT))
    } catch (e) {
      setLeaderboard([])
      // setTotalPage(0)
    }
  }

  const fetchRewardAndClaimParams = async () => {
    setLoadingClaimParams(true)
    try {
      const res = await calcClaimParams({ provider, account })
      console.log(res)
      setTrancheIds(res?.trancheIds || [])
      setProofTranches(res?.proofTranches || [])
      setAmounts(res?.amounts || [])
      let reward = ethers.BigNumber.from(0)
      if (res?.amounts?.length > 0) {
        res.amounts.forEach(
          (a: any) => (reward = ethers.BigNumber.from(reward).add(a))
        )
      }
      setTotalReward(reward)
    } catch (e) {
      console.error(e)
    }
    setLoadingClaimParams(false)
  }

  const updateBalance = async (hash: string) => {
    try {
      const tx = await config.provider.waitForTransaction(hash)
      if (tx.confirmations && tx.status === 1) {
        await fetchBalanceOfAllToken(account)
        toast.success(
          messageAndViewOnBsc({
            title: 'Claim Success',
            hash
          })
        )
      } else {
        toast.error(
          messageAndViewOnBsc({
            title: 'Claim Error',
            hash
          })
        )
      }
    } catch (e) {
      console.error(e)
    }
  }

  const leaderBoardColumn = [
    {
      title: 'Address',
      name: '_id',
      render: (address: string) => {
        return (
          <a
            href={config.explorer + '/address/' + address}
            className='text-link'
            target='_blank'
            rel='noreferrer'
          >
            {address.slice(0, 8) + '...'}
          </a>
        )
      }
    },
    {
      title: 'Total Fee',
      name: 'totalFee',
      render: (volume: number) => {
        return (
          <span>
            <span className='text-grey'>$</span>
            {dollarUSLocale.format(volume)}
          </span>
        )
      }
    },
    {
      title: 'Reward',
      name: 'reward',
      className: 'text-right',
      render: (reward: number) => {
        return (
          <span>
            <span className='mr-05'>{dollarUSLocale.format(reward)}</span>
            <span className='font-size-12'>LZP</span>
          </span>
        )
      }
    }
  ]

  return (
    <div className='reward'>
      <div className='reward__header text-gradient font-size-18 hidden-on-phone pt-05 pb-05'>
        Swap to Earn Reward
      </div>
      <div className='reward__table mt-05'>
        <Table columns={leaderBoardColumn} data={leaderboard} />
      </div>
      {/* {false && ( */}
      {/*  <div className='reward__pagination'> */}
      {/*    <ReactPaginate */}
      {/*      breakLabel='...' */}
      {/*      nextLabel='>' */}
      {/*      initialPage={page - 1} */}
      {/*      onPageChange={(e) => { */}
      {/*        setPage(e.selected + 1) */}
      {/*      }} */}
      {/*      marginPagesDisplayed={2} */}
      {/*      pageRangeDisplayed={2} */}
      {/*      pageCount={totalPage} */}
      {/*      previousLabel='<' */}
      {/*    /> */}
      {/*  </div> */}
      {/* )} */}
      {account &&
        (!loadingClaimParams ? (
          <div className='reward__footer'>
            <span className='reward__footer--label'>Reward</span>
            <span>
              <span className='reward__footer--value'>
                {' '}
                {formatFloat(weiToNumber(totalReward), 3)}
              </span>
              <span className='reward__footer--symbol'>LZP</span>
            </span>
            <Button
              className='reward__footer--claim'
              disabled={!totalReward || loadingClaim || trancheIds.length === 0}
              onClick={async () => {
                setLoadingClaim(true)
                try {
                  const gasAmount = await estimateGas({
                    provider,
                    account,
                    proofTranches,
                    amounts,
                    trancheIds
                  })
                  const notError = await claim({
                    provider,
                    account,
                    proofTranches,
                    amounts,
                    trancheIds,
                    gasAmount,
                    gasPrice,
                    checkError: true
                  })
                  if (notError) {
                    const hash = await claim({
                      provider,
                      account,
                      proofTranches,
                      amounts,
                      trancheIds,
                      gasAmount,
                      gasPrice
                    })
                    if (hash) {
                      toast.success(
                        messageAndViewOnBsc({
                          title: 'Transaction Submitted',
                          hash: hash
                        })
                      )
                      await updateBalance(hash)
                      fetchRewardAndClaimParams()
                    }
                  }
                } catch (e) {
                  toast.error(e.message)
                }
                setLoadingClaim(false)
              }}
            >
              {loadingClaim ? <LoadingIcon /> : 'Claim'}
            </Button>
          </div>
        ) : (
          <div className='text-center mt-1'>
            <LoadingIcon className='d-inline-block' />
          </div>
        ))}
    </div>
  )
}
