import React, { useEffect, useRef, useState } from 'react'
import './style.scss'
import {
  ErrorIcon,
  LoadingIcon,
  QuestionIcon,
  Swap2Icon,
  SwapIcon
} from '../../ui/Icons'
import { TokenInputField } from '../TokenInputField'
import { useSelector } from 'react-redux'
import {
  detectDecimalFromPrice,
  formatFloat,
  numberToWei,
  weiToNumber
} from '../../../utils/helpers'
import { ethers } from 'ethers'
import { Button } from '../../ui/Button'
import config from '../../../config'
import Modal from '../../ui/Modal'
import ReactTooltip from 'react-tooltip'
// import { PriceImpact } from '../ImpactPrice'
import { EstimateFee } from '../EstimateFee'
import { AmmRoutes } from './Routes'
import { useListTokens } from '../../../state/token/hook'
import { useCurrentPair } from '../../../state/currentPair/hook'
import { useWalletBalance } from '../../../state/wallet/hook'
import { useWeb3React } from '../../../state/customWeb3React/hook'
import { useEstimateSwapOutput } from '../../../state/marketSwap/hooks/useEstimateSwapOutput'
import { ConfirmSwapModal } from '../ConfirmSwapModal'
import { WaitingSwapModal } from '../WaitingSwapModal'
import { useMarketSwapAction } from '../../../state/marketSwap/hooks/useMarketSwapAction'
import { useWalletAllowance } from '../../../state/wallet/hooks/useWalletAllowance'
import { getConfigAddress } from '../../../utils/addressHelper'
import { useSwapInfo } from '../../../state/marketSwap/hooks/useSwapInfo'
import { useTokenPrice } from '../../../state/token/hooks/useTokenPrice'
import { formatWeiToDisplayNumber } from '../../../utils/formatBalance'
import { PriceImpact } from '../ImpactPrice'
import { BlackToken } from '../../../state/token/type'
import { killGetAmountOutWorker } from 'swap-tools-2'
import _ from 'lodash'

const bn = ethers.BigNumber.from
const DECIMAL_SLIPPAGE = 10000
export const Market = (props: any) => {
  const { theme } = useSelector((state: any) => ({
    theme: state.swap.theme
  }))
  const { account, showConnectModal } = useWeb3React()
  const { balances, accFetchBalance } = useWalletBalance()
  const { tokens, blackList } = useListTokens()
  const { tokenPrices } = useTokenPrice()
  const {
    inputTokenAddress,
    outputTokenAddress,
    setCurrentPair,
    setInputToken,
    setOutputToken,
    inputToken,
    outputToken,
    findingOutputToken
  } = useCurrentPair()

  const amountInRef = useRef<any>()
  const [price, setPrice] = useState<string>('0')
  const [inputAmount, setInputAmount] = useState<string>('')
  const [estimateSwapInfo, setEstimateSwapInfo] = useState<any>({})
  const [revertRate, setRevertRate] = useState<boolean>(true)
  const [visibleConfirmModal, setVisibleConfirmModal] = useState<boolean>(false)
  const [visibleWaitingModal, setVisibleWaitingModal] = useState<boolean>(false)
  const [visibleWarningModal, setVisibleWarningModal] = useState<boolean>(false)
  const [errorMessage] = useState<boolean>(true)
  const [loading, setLoading] = useState<boolean>(false)
  const [loadingApprove, setLoadingApprove] = useState<boolean>(false)
  const [swapLoading, setSwapLoading] = useState<boolean>(false)
  const { swapHandle, approveAggregator } = useMarketSwapAction()
  const { updateBalanceAfterSwap } = useWalletBalance()
  const {
    gasAmount,
    calculateMarketOutput: calcOutput,
    getAmountOutByAggregator
  } = useEstimateSwapOutput()
  const { fetchAggregatorAllowance, aggregatorAllowance } = useWalletAllowance()
  const {
    routes,
    slippage,
    setGasAmountToSwap,
    setLoadingAmountOut,
    getAmountOutFinished,
    setIsFinished
  } = useSwapInfo()
  const [reasonInBlackList, setReasonInBlackList] = useState<string | null>(
    null
  )

  /**
   *  calc output amount, price, estimate gas, min received
   */
  useEffect(() => {
    if (accFetchBalance !== account) return
    setEstimateSwapInfo({})
    setGasAmountToSwap(bn(0))

    if (
      routes.inputAddress !== inputTokenAddress ||
      routes.outputAddress !== outputTokenAddress
    ) {
      return
    }
    const delayDebounceFn = setTimeout(() => {
      calculateMarketOutput()
    }, 500)
    const interval = setInterval(() => {
      console.log('estimate output')
      calculateMarketOutput(false)
    }, 15000)
    return () => {
      clearTimeout(delayDebounceFn)
      clearInterval(interval)
    }
  }, [routes, inputAmount, aggregatorAllowance, account, accFetchBalance])

  useEffect(() => {
    if (Number(inputAmount)) {
      setLoading(true)
    }
  }, [inputAmount, inputToken, outputToken])

  /**
   * setPrice = inputToken price / outputToken price
   */
  useEffect(() => {
    if (tokenPrices[inputTokenAddress] && tokenPrices[outputTokenAddress]) {
      if (revertRate) {
        setPrice(
          bn(numberToWei(tokenPrices[inputTokenAddress] || 0, 36))
            .div(numberToWei(tokenPrices[outputTokenAddress]))
            .toString()
        )
      } else {
        console.log(
          tokenPrices[outputTokenAddress],
          tokenPrices[inputTokenAddress]
        )
        setPrice(
          bn(numberToWei(tokenPrices[outputTokenAddress] || 0, 36))
            .div(numberToWei(tokenPrices[inputTokenAddress]))
            .toString()
        )
      }
    }
  }, [
    revertRate,
    tokenPrices[inputTokenAddress],
    tokenPrices[outputTokenAddress]
  ])

  useEffect(() => {
    checkIsBlackList()
  }, [blackList, inputTokenAddress, outputTokenAddress])

  const checkIsBlackList = () => {
    const res = blackList.find((b: BlackToken) => {
      return (
        b?.address?.toLowerCase() === inputTokenAddress.toLowerCase() ||
        b?.address?.toLowerCase() === outputTokenAddress.toLowerCase()
      )
    })
    if (res) {
      setReasonInBlackList(res.reason)
    } else {
      setReasonInBlackList(null)
    }
  }

  /**
   * fetchAllowance when change inputToken
   */
  useEffect(() => {
    if (account && inputTokenAddress) {
      fetchAggregatorAllowance(inputTokenAddress)
    }
  }, [inputTokenAddress, account])

  const revertPairInput = () => {
    const mo = weiToNumber(
      estimateSwapInfo.amountOut,
      tokens[outputTokenAddress].decimal
    )
    setInputAmount(formatFloat(mo, detectDecimalFromPrice(mo)))
    setCurrentPair({
      inputAddress: outputTokenAddress,
      outputAddress: inputTokenAddress
    })
  }

  /**
   * get label of swap button
   */
  const buttonSwapLabel = () => {
    if (
      inputTokenAddress === getConfigAddress('bnb') &&
      outputTokenAddress === getConfigAddress('wbnb')
    ) {
      return 'Wrap'
    } else if (
      outputTokenAddress === getConfigAddress('bnb') &&
      inputTokenAddress === getConfigAddress('wbnb')
    ) {
      return 'Unwrap'
    } else {
      return 'Swap'
    }
  }

  /**
   * estimate output amount, gas fee and data input to swap
   */
  //@ts-ignore
  const calculateMarketOutput = async (fistTimeCalculate: boolean = true) => {
    if (Number(inputAmount) <= 0 || !inputToken || !outputToken) {
      setGasAmountToSwap(bn(0))
      setEstimateSwapInfo({})
      killGetAmountOutWorker()
      setIsFinished(true)
      setLoadingAmountOut(false)
      setLoading(false)
      return
    }
    try {
      console.log('start calculate')

      if (fistTimeCalculate) {
        setIsFinished(false)
        setLoading(true)
        setLoadingAmountOut(true)
      }
      calcOutput({
        amountIn: bn(numberToWei(inputAmount, inputToken.decimal)),
        inputAddress: inputTokenAddress,
        outputAddress: outputTokenAddress,
        callback: (data: any) => {
          if (
            numberToWei(
              amountInRef?.current?.value || 0,
              inputToken.decimal
            ) === data.amountIn.toString() &&
            (data.isFinished || fistTimeCalculate)
          ) {
            setEstimateSwapInfo(data)
            setIsFinished(data.isFinished)
          }
          setLoading(false)
        }
      })
    } catch (e) {
      console.error(e)
      console.log('calculate route fail')
      setLoading(true)
    }
  }

  const minAmountOut = estimateSwapInfo.amountOut
    ? estimateSwapInfo.amountOut.sub(
        estimateSwapInfo.amountOut
          .mul(slippage * DECIMAL_SLIPPAGE)
          .div(DECIMAL_SLIPPAGE * 100)
      )
    : '0'

  const checkCanSwap = () => {
    const isDisable =
      reasonInBlackList ||
      !inputAmount ||
      Number(inputAmount) === 0 ||
      loading ||
      swapLoading ||
      bn(numberToWei(inputAmount || 0, inputToken?.decimal || 18)).gt(
        balances[inputTokenAddress] || 0
      )
    return (
      !isDisable &&
      inputAmount &&
      estimateSwapInfo.amountOut &&
      !estimateSwapInfo.amountOut.isZero()
    )
  }

  return (
    <div className={`market ${props.className}`}>
      <TokenInputField
        label='From'
        amount={inputAmount}
        setAmount={(e: any) => {
          setInputAmount(e)
        }}
        token={inputToken}
        setToken={setInputToken}
        findingToken={!tokens[inputTokenAddress]}
        myRef={amountInRef}
      />

      <div className='reverse-icon'>
        <SwapIcon
          onClick={() => {
            if (inputTokenAddress && outputTokenAddress) {
              revertPairInput()
            }
          }}
        />
      </div>

      <TokenInputField
        label='To (estimated)'
        amount={weiToNumber(
          estimateSwapInfo.amountOut,
          outputToken?.decimal || 18
        )}
        token={outputToken}
        setToken={setOutputToken}
        loading={loading || !getAmountOutFinished}
        findingToken={findingOutputToken}
      />
      <input type='hidden' value={outputTokenAddress || ''} />
      <input type='hidden' value={inputTokenAddress || ''} />

      <div className='swap-info'>
        <div className='swap-info__row'>
          <span>Price</span>
          <span>
            <span className='mr-05'>
              {formatWeiToDisplayNumber(
                price,
                detectDecimalFromPrice(weiToNumber(price))
              )}
            </span>
            <span className='mr-05'>
              {inputToken && outputToken
                ? revertRate
                  ? `${outputToken.symbol} per ${inputToken.symbol}`
                  : `${inputToken.symbol} per ${outputToken.symbol}`
                : ''}
            </span>
            <Swap2Icon onClick={() => setRevertRate(!revertRate)} />
          </span>
        </div>

        <div className='swap-info__row'>
          <span className='slippage-tolerance'>
            <span>Slippage Tolerance</span>
            <a data-tip data-for='slippage'>
              <QuestionIcon className='align-item-center ml-05' />
            </a>
            <ReactTooltip id='slippage' type={theme} place='bottom'>
              <p style={{ width: '250px' }} className='text-center'>
                The higher the slippage tolerance is, the likelier the
                transaction will succeed and the higher the price becomes.
                Please set the slippage tolerance with caution.
              </p>
            </ReactTooltip>
          </span>
          <span className='text-gradient'>{slippage}%</span>
        </div>
      </div>

      {!account ? (
        <div className='swap-actions'>
          <Button onClick={showConnectModal} className='swap-btn'>
            Connect Wallet
          </Button>
        </div>
      ) : (
        <div className='swap-actions'>
          {(!aggregatorAllowance[inputTokenAddress] ||
            aggregatorAllowance[inputTokenAddress].isZero() ||
            aggregatorAllowance[inputTokenAddress].lt(
              numberToWei(inputAmount || 0, inputToken?.decimal || 18)
            )) &&
          inputTokenAddress !== config.chain[56].addresses.bnb ? (
            <Button
              className='swap-btn'
              onClick={async () => {
                setLoadingApprove(true)
                try {
                  await approveAggregator({
                    tokenAddress: inputTokenAddress
                  })
                } catch (e) {
                  console.error(e)
                }

                setLoadingApprove(false)
              }}
              disabled={loadingApprove || reasonInBlackList}
            >
              <div className='swap-btn__text'>
                {loadingApprove ? (
                  <LoadingIcon size={24} className='loading-on-button' />
                ) : (
                  ''
                )}
                <span> Approve</span>
              </div>
            </Button>
          ) : (
            <Button
              className='swap-btn'
              disabled={!checkCanSwap()}
              onClick={async () => {
                setSwapLoading(true)

                if (!getAmountOutFinished) {
                  killGetAmountOutWorker()
                  setIsFinished(true)
                  const { amountOut } = await getAmountOutByAggregator({
                    amountIn: bn(
                      numberToWei(inputAmount, inputToken?.decimal || 18)
                    ),
                    tokenIn: inputTokenAddress,
                    tokenOut: outputTokenAddress,
                    routes: _.cloneDeep(estimateSwapInfo.routes)
                  })
                  if (amountOut.gte(0)) {
                    setIsFinished(true)
                    setEstimateSwapInfo({
                      ...estimateSwapInfo,
                      amountOut
                    })
                  }
                }
                setVisibleConfirmModal(true)
              }}
            >
              <div className='swap-btn__text'>
                {inputAmount &&
                balances[inputTokenAddress] &&
                bn(numberToWei(inputAmount, inputToken?.decimal || 18)).lte(
                  balances[inputTokenAddress]
                ) &&
                (loading || swapLoading) ? (
                  <LoadingIcon size={24} className='loading-on-button' />
                ) : (
                  ''
                )}
                <div>
                  {inputAmount &&
                  balances[inputTokenAddress] &&
                  bn(numberToWei(inputAmount, inputToken?.decimal || 18)).gt(
                    balances[inputTokenAddress]
                  )
                    ? ' Insufficient ' + inputToken.symbol + ' Amount '
                    : buttonSwapLabel()}
                </div>
              </div>
            </Button>
          )}
        </div>
      )}
      <div className='text-danger font-size-12 text-center'>
        {inputAmount &&
        estimateSwapInfo.amountOut &&
        estimateSwapInfo.amountOut.isZero()
          ? 'There may be an issue with the input token.'
          : ''}
        {reasonInBlackList}
      </div>

      <ConfirmSwapModal
        visible={visibleConfirmModal}
        setVisible={(e: boolean) => {
          setVisibleConfirmModal(e)
          setSwapLoading(e)
        }}
        inputAmount={bn(
          numberToWei(inputAmount || 0, inputToken?.decimal || 18)
        )}
        outputAmount={bn(estimateSwapInfo.amountOut || 0)}
        dists={estimateSwapInfo.dists}
        routes={estimateSwapInfo.routes}
        steps={estimateSwapInfo.steps}
        estimateGasAmount={gasAmount}
        confirmSwap={async () => {
          try {
            setVisibleConfirmModal(false)
            setVisibleWaitingModal(true)
            const res = await swapHandle({
              amountOut: estimateSwapInfo.amountOut,
              inputAddress: inputTokenAddress,
              outputAddress: outputTokenAddress,
              amountIn: bn(numberToWei(inputAmount, inputToken.decimal)),
              routes: estimateSwapInfo.routes
            })

            setInputAmount('')

            // update balance
            if (res) {
              setEstimateSwapInfo({})
              await updateBalanceAfterSwap({
                inputTokenAddress,
                inputAmount,
                outputAmount: estimateSwapInfo.amountOut,
                outputTokenAddress,
                hash: res.hash,
                account,
                tokens
              })
            }
            setVisibleWaitingModal(false)
            setSwapLoading(false)
            setLoading(false)
          } catch (e) {
            console.log(e)
          }
        }}
      />
      <WaitingSwapModal
        visible={visibleWaitingModal}
        setVisible={setVisibleWaitingModal}
        outputToken={outputToken}
        inputAmount={inputAmount}
        inputToken={inputToken}
        outputAmount={estimateSwapInfo.amountOut || 0}
      />
      <Modal
        visible={visibleWarningModal}
        setVisible={(e: boolean) => {
          setVisibleWarningModal(e)
          setSwapLoading(e)
        }}
        title='Alert'
      >
        <div className='swap-warning-modal'>
          <div className='text-center'>
            <ErrorIcon />
          </div>
          <span>The transaction cannot succeed due to error:</span>
          <span className='text-danger'>{errorMessage}</span>
          <span>Please try again</span>
        </div>
      </Modal>

      <div className='amm-info'>
        <div className='amm-info__row'>
          <span className='align-item-center'>
            <span>Price Impact</span>
            <a data-tip data-for='impactPrice'>
              <QuestionIcon className='align-item-center ml-05' />
            </a>
            <ReactTooltip id='impactPrice' type={theme} place='bottom'>
              <p style={{ width: '250px' }} className='text-center'>
                The influence that the transaction has over the market price of
                the traded pair.
              </p>
            </ReactTooltip>
          </span>
          <PriceImpact
            amountIn={bn(numberToWei(inputAmount || 0))}
            amountOut={estimateSwapInfo.amountOut}
            dists={estimateSwapInfo.dists}
            // steps={estimateSwapInfo.steps}
          />
        </div>
        <div className='amm-info__row'>
          <span className='align-item-center'>
            <span>Estimated Fee</span>
            <a data-tip data-for='estimateFee'>
              <QuestionIcon className='align-item-center ml-05' />
            </a>
            <ReactTooltip id='estimateFee' type={theme} place='bottom'>
              <span>Estimated gas fee of the current transaction.</span>
            </ReactTooltip>
          </span>
          <EstimateFee estimateGasAmount={gasAmount} />
        </div>
        <div className='amm-info__row'>
          <span className='align-item-center'>
            <span>Minimum received</span>
            <a data-tip data-for='minReceive'>
              <QuestionIcon className='align-item-center ml-05' />
            </a>
            <ReactTooltip id='minReceive' type={theme} place='bottom'>
              <p style={{ width: '250px' }} className='text-center'>
                The transaction will revert if there is a large, unfavorable
                price movement before it is confirmed.
              </p>
            </ReactTooltip>
          </span>
          <span>
            {estimateSwapInfo.amountOut
              ? formatWeiToDisplayNumber(minAmountOut, 4, outputToken.decimal)
              : 0}{' '}
            {outputToken?.symbol || ''}
          </span>
        </div>
      </div>
      <AmmRoutes
        distribution={estimateSwapInfo.dists || null}
        steps={estimateSwapInfo.steps || []}
      />
    </div>
  )
}
