import React, { useEffect, useState } from 'react'
import './style.scss'
import { LaunchZoneLogo, SwapIcon, TriangleIcon } from '../../ui/Icons'
import { TokenInputField } from '../TokenInputField'
import { Button } from '../../ui/Button'
import { useDispatch, useSelector } from 'react-redux'
import {
  div,
  formatFloat,
  mul,
  numberToWei,
  weiToNumber
} from '../../../utils/helpers'
import { setSingleStateData } from '../../../store/actions/initSettingData'
import { useCurrentPair } from '../../../state/currentPair/hook'

export const Limit = (props: any) => {
  return (
    <div className='limit'>
      <div className={`limit__coming-soon ${props.className}`}>
        <LaunchZoneLogo />
        <span>Maintenance</span>
      </div>
    </div>
  )

  const dispatch = useDispatch()
  const { tokens, provider } = useSelector((state: any) => ({
    tokens: state.swap.tokens,
    provider: state.swap.prices
  }))
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()

  const [inputAmount, setInputAmount] = useState<string>('')
  const [outputAmount, setOutputAmount] = useState<string>('')
  const [marketPrice, setMarketPrice] = useState<string>('0')
  const [limitPrice, setLimitPrice] = useState<string>('0')
  const [firstGetPrice, setFirstGetPrice] = useState<boolean>(false)

  const inputToken = tokens ? tokens[inputTokenAddress] : {}
  const outputToken = tokens ? tokens[outputTokenAddress] : {}
  const setInputToken = ({ address }: { address: string }) =>
    setSingleStateData('inputTokenAddress', address)(dispatch)
  const setOutputToken = ({ address }: { address: string }) =>
    setSingleStateData('outputTokenAddress', address)(dispatch)

  useEffect(() => {
    if (!firstGetPrice) {
      fetchMarketPrice()
    }
  }, [inputTokenAddress, outputTokenAddress, provider])

  const calcInputAmount = (outputA: any) => {
    if (marketPrice && inputToken && outputToken) {
      setInputAmount(
        weiToNumber(
          div(
            numberToWei(outputA, 36),
            numberToWei(marketPrice, 18 - Number(outputToken.decimal))
          ),
          18
        )
      )
    }
  }

  const calcOutputAmount = (inputA: any, mPrice: any = marketPrice) => {
    if (mPrice && inputToken && outputToken) {
      setOutputAmount(
        weiToNumber(
          mul(mPrice, numberToWei(inputA)),
          18 + Number(outputToken.decimal)
        )
      )
    }
  }

  const fetchMarketPrice = async () => {
    if (inputToken && outputToken && provider) {
      const price = ''
      setMarketPrice(price)
      setFirstGetPrice(true)
      setLimitPrice(
        formatFloat(weiToNumber(price, Number(outputToken.decimal)), 4)
      )
      if (inputAmount) {
        calcOutputAmount(inputAmount, price)
      } else if (outputAmount) {
        calcInputAmount(outputAmount)
      }
    }
  }

  const revertPairInput = () => {
    const toA = outputAmount
    const toT = outputToken
    setOutputAmount(inputAmount)
    setInputAmount(toA)
    setOutputToken(inputToken)
    setInputToken(toT)
  }

  return (
    <div className={`limit ${props.className}`}>
      <TokenInputField
        label='From'
        amount={inputAmount}
        setAmount={(amount: any) => {
          setInputAmount(amount)
          calcOutputAmount(amount)
        }}
        token={inputToken}
        setToken={async (token: any) => {
          setInputToken(token)
          fetchMarketPrice()
        }}
      />

      <div className='limit__market-price-info'>
        <span>Market Price</span>
        <span>
          {outputToken &&
            inputToken &&
            formatFloat(weiToNumber(marketPrice, outputToken.decimal), 4) +
              ' ' +
              outputToken.symbol +
              ' per ' +
              inputToken.symbol}
        </span>
      </div>

      <div className='limit__values'>
        <div className='limit__values--price-box'>
          <span>Limit Price</span>
          <input
            type='number'
            placeholder='0.0'
            value={limitPrice}
            onChange={(e) => {
              setLimitPrice(e.target.value)
              calcOutputAmount(inputAmount)
            }}
          />
        </div>
        <div className='limit__values--expire-box'>
          <span className='text-right'>Expire in</span>
          <select defaultValue='7'>
            <option value='7'>7 Days</option>
            <option value='30'>1 Months</option>
            <option value='180'>6 Months</option>
            <option value='365'>1 Year</option>
          </select>
        </div>
      </div>

      <div className='reverse-icon'>
        <SwapIcon onClick={revertPairInput} />
      </div>

      <TokenInputField
        label='To'
        amount={outputAmount}
        setAmount={(amount: any) => {
          setOutputAmount(amount)
          calcInputAmount(amount)
        }}
        token={outputToken}
        setToken={(token: any) => {
          setOutputToken(token)
          fetchMarketPrice()
        }}
      />

      <div className='limit__notify--box'>
        <TriangleIcon className='limit__notify--icon' />
        <div className='limit__notify'>
          <span className='title text-danger'>Order below Market</span>
          <span className='content'>
            Your order price 30% below market price
          </span>
        </div>
      </div>

      <div className='limit__actions'>
        <Button>Approve Lz</Button>
        <Button>GET WBNB</Button>
      </div>

      <div className='limit__note'>
        Note: You need at least 0.005 WBNB to pay transaction fee
      </div>
    </div>
  )
}
