import React, { useMemo } from 'react'
import {
  EXCHANGES_TO_SHOW,
  SERVICES,
  STABLECOIN_ADDRESSES
} from '../../utils/constant'
import { ExchangeIcon } from '../ui/Icons'
import {
  convertBNBAddressToWBNB,
  detectDecimalFromPrice,
  numberToWei
} from '../../utils/helpers'
import { useSelector } from 'react-redux'
import { useListTokens } from '../../state/token/hook'
import { useCurrentPair } from '../../state/currentPair/hook'
import config from '../../config'
import { ethers } from 'ethers'
import { formatWeiToDisplayNumber } from '../../utils/formatBalance'

export const useLiquidity = () => {
  const { tokens } = useListTokens()
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { topPoolsToDisplay } = useSelector((state: any) => ({
    topPoolsToDisplay: state.swap.topPoolsToDisplay
  }))

  const liquidityData = useMemo(() => {
    return topPoolsToDisplay
      .filter((p: any) => {
        const inputAddress = convertBNBAddressToWBNB(inputTokenAddress)
        const outputAddress = convertBNBAddressToWBNB(outputTokenAddress)
        return (
          (p.address0 === inputAddress || p.address0 === outputAddress) &&
          p.address1 &&
          (STABLECOIN_ADDRESSES.includes(p.address1.toLowerCase()) ||
            p.address1.toLowerCase() ===
              config.chain[56].addresses.wbnb.toLowerCase() ||
            p.address1.toLowerCase() ===
              config.chain[56].addresses.cake.toLowerCase())
        )
      })
      .sort((a: any, b: any) => {
        return ethers.BigNumber.from(numberToWei(a.reserve0)).gt(
          numberToWei(b.reserve0)
        )
          ? -1
          : 1
      })
  }, [topPoolsToDisplay])

  const liquidityColumns = useMemo(() => {
    return [
      {
        name: 'creator',
        title: 'Exchange',
        render: (creator: any) => {
          let result = ''
          for (const i in SERVICES) {
            if (SERVICES[i].factory === creator) {
              result = i
            }
          }
          return (
            <span className='pool__exchange'>
              {
                // @ts-ignore
                result ? <ExchangeIcon exchange={result} /> : ''
              }
              <span className='hidden-on-phone'>
                {EXCHANGES_TO_SHOW.find((e: any) => e.value === result)?.name}
              </span>
            </span>
          )
        }
      },
      {
        name: 'token1',
        title: 'Pair',
        render: (token: any, object: any) => {
          const reserve = numberToWei(object.reserve0)
          if (tokens[inputTokenAddress] && token) {
            return (
              <span>
                <span>{object?.token0?.symbol + '/' + token.symbol}</span>
                <p>
                  {formatWeiToDisplayNumber(
                    reserve,
                    detectDecimalFromPrice(reserve),
                    tokens[inputTokenAddress].decimal
                  )}{' '}
                </p>
              </span>
            )
          } else {
            return ''
          }
        }
      },
      {
        name: 'reserve1',
        title: 'Total reserve',
        render: (value: string, object: any) => {
          return (
            <div>
              {formatWeiToDisplayNumber(
                numberToWei(value),
                detectDecimalFromPrice(value),
                18
              )}{' '}
              {object.token1 ? object.token1.symbol : ''}
            </div>
          )
        }
      },
      {
        name: '',
        title: 'Holder',
        className: 'text-right',
        // @ts-ignore
        render: (value, object: any) => {
          return (
            <div className='top-pool-action-column text-right'>
              <a
                href={
                  'https://bscscan.com/token/' +
                  object.address1 +
                  '?a=' +
                  object.address
                }
                target='_blank'
                rel='noreferrer'
                className='text-right'
              >
                {object.address.slice(0, 8)}
              </a>
            </div>
          )
        }
      }
    ]
  }, [tokens, inputTokenAddress])

  return {
    liquidityColumns,
    liquidityData
  }
}
