import React, { useState } from 'react'
import './style.scss'
import { CopyIcon, RevertIcon, SlidersHorizontalIcon } from '../ui/Icons'
import { Market } from './Market'
import { Limit } from './Limit'
import { SwapSettingModal } from './SwapSettingModal'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { useCurrentPair } from '../../state/currentPair/hook'
import { toast } from 'react-toastify'
import Modal from '../ui/Modal'
import { dracula, atomOneLight, CodeBlock } from 'react-code-blocks'
import { useSelector } from 'react-redux'
import { useListTokens } from '../../state/token/hook'

export default () => {
  const { theme } = useSelector((state: any) => ({
    theme: state.swap.theme
  }))
  const [visibleEmbedCode, setVisibleEmbedCode] = useState<boolean>(false)
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { tokens } = useListTokens()
  const [isMarketTab, setIsMarketTab] = useState<boolean>(true)
  const [visibleSetting, setVisibleSetting] = useState<boolean>(false)
  const [isRevertEmbedCode, setIsRevertEmbedCode] = useState<boolean>(false)

  const embedCode = `<!-- if you use this embed code in reactjs or vuejs -->
<!-- please copy link stylesheet and script tag to public/index.html -->
<button
  id="btn-show-iframe"
  type="button"
  onClick="window.createPopup(document.getElementById('btn-show-iframe'))"
  data-iframe-url="${window.location.origin}/swap/token/${
    !isRevertEmbedCode ? inputTokenAddress : outputTokenAddress
  }/${
    !isRevertEmbedCode ? outputTokenAddress : inputTokenAddress
  }?widget=1&tab=swap"
  data-popup-title="LaunchZone - Swap"
>
  Swap token
</button>
<link rel='stylesheet' href='${window.location.origin}/lib/embed.css' />
<script src='${window.location.origin}/lib/embed.js' ></script>
`

  return (
    <div className='swap'>
      <div className='title'>
        <span className='text-gradient font-size-18 hidden-on-phone'>Swap</span>
        <div className='action'>
          <span
            className='action-button btn-copy-embed-code'
            onClick={() => setVisibleEmbedCode(true)}
          >
            <span className='swap-icon '>{`</>`}</span>
          </span>
          <span
            className='action-button'
            onClick={() => setVisibleSetting(true)}
          >
            <SlidersHorizontalIcon />
          </span>
        </div>
      </div>
      <div className='switch-tab'>
        <button
          className={`switch-tab__button ${isMarketTab && 'active'}`}
          onClick={() => setIsMarketTab(true)}
        >
          Market
        </button>
        <button
          className={`switch-tab__button ${!isMarketTab && 'active'}`}
          onClick={() => setIsMarketTab(false)}
        >
          Limit
        </button>
      </div>
      <Modal
        visible={visibleEmbedCode}
        setVisible={setVisibleEmbedCode}
        title='Embed code'
        width={700}
      >
        <div className='embed-code-modal'>
          <div className='text-gradient embed-code-modal__pair'>
            <span onClick={() => setIsRevertEmbedCode(!isRevertEmbedCode)}>
              {`${
                !isRevertEmbedCode
                  ? tokens[inputTokenAddress]?.symbol
                  : tokens[outputTokenAddress]?.symbol
              } - ${
                !isRevertEmbedCode
                  ? tokens[outputTokenAddress]?.symbol
                  : tokens[inputTokenAddress]?.symbol
              }`}
            </span>
            <span onClick={() => setIsRevertEmbedCode(!isRevertEmbedCode)}>
              <RevertIcon />
            </span>
          </div>
          <CopyToClipboard
            onCopy={() => toast.info('Copied embed code')}
            text={embedCode}
          >
            <span className='copy-button'>
              <CopyIcon />
            </span>
          </CopyToClipboard>
          <CodeBlock
            text={embedCode}
            language='html'
            showLineNumbers
            theme={theme === 'dark' ? dracula : atomOneLight}
            wrapLines
            codeBlock
          />
        </div>
      </Modal>
      <SwapSettingModal
        setVisible={setVisibleSetting}
        visible={visibleSetting}
      />
      <Market className={(!isMarketTab && 'hidden') + ' m-0'} />
      <Limit className={(isMarketTab && 'hidden') + ' m-0'} />
    </div>
  )
}
