import config from '../config'
import { ethers } from 'ethers'

export const getConfigAddress = (name: string, chainId = 56) => {
  return config.chain[chainId].addresses[name]
}

export const isEqualAddress = (address1: string, address2: string) => {
  return (
    ethers.utils.isAddress(address1) &&
    ethers.utils.isAddress(address2) &&
    address1.toLowerCase() === address2.toLowerCase()
  )
}
