import { useDispatch, useSelector } from 'react-redux'
import { getListBalance } from '../../service/commonService'
import config from '../../config'
import { useListTokens } from '../token/hook'
import { initBalances } from './reducer'
import { useEffect } from 'react'
import _ from 'lodash'
import { toast } from 'react-toastify'
import {
  detectDecimalFromPrice,
  formatFloat,
  messageAndViewOnBsc,
  weiToNumber
} from '../../utils/helpers'
import { useWeb3React } from '../customWeb3React/hook'
// import { UPDATE_BALANCE } from '../store/actions/wallet'
// const { BalanceTrackerController } = require('balance-tracker')
// const storageKeyPrefix = 'balance-tracker-' // application specific storage key prefix for each blanceTrackerController

const TIME_INTERVAL_TO_FETCH_BALANCE = 4000

export const useWalletBalance = () => {
  const { tokens } = useListTokens()
  const { balances, accFetchBalance } = useSelector((state: any) => {
    return {
      balances: state.wallet.balances,
      accFetchBalance: state.wallet.accFetchBalance
    }
  })

  const { account } = useWeb3React()

  const dispatch = useDispatch()

  const fetchBalanceOfAllToken = async (acc: string) => {
    const tokensArr = Object.keys(tokens)
    if (acc && tokensArr.length > 0) {
      try {
        const [bnbBalance, balances] = await Promise.all([
          config.provider.getBalance(acc),
          getListBalance(acc, Object.keys(tokens)).then((r: any) => r[0])
        ])

        const results = {}
        for (let i = 0; i < tokensArr.length; i++) {
          const address = tokensArr[i]
          if (address === config.chain[56].addresses.bnb) {
            results[address] = bnbBalance.toString()
          } else if (balances[i].gt(0)) {
            results[address] = balances[i].toString()
          }
        }
        if (Object.values(results).length > 0) {
          localStorage.setItem('cache_balances', JSON.stringify(results))
        }

        dispatch(initBalances({ balances: results, account }))
      } catch (e) {
        console.error(e)
        throw new Error(e)
      }
    }

    if (!acc) {
      dispatch(initBalances({ balances: {}, account }))
    }
  }

  const updateBalanceAfterSwap = async ({
    inputTokenAddress,
    inputAmount,
    outputAmount,
    outputTokenAddress,
    hash,
    account,
    tokens
  }: {
    inputTokenAddress: string
    inputAmount: string
    outputAmount: string
    outputTokenAddress: string
    hash: string
    account: string
    tokens: any
  }) => {
    try {
      const tx = await config.provider.waitForTransaction(hash)
      if (tx.confirmations && tx.status === 1) {
        await fetchBalanceOfAllToken(account)

        toast.success(
          messageAndViewOnBsc({
            title: 'Transaction receipt',
            message:
              'Swap ' +
              formatFloat(inputAmount, detectDecimalFromPrice(inputAmount)) +
              ' ' +
              tokens[inputTokenAddress]?.symbol +
              ' for ' +
              formatFloat(
                weiToNumber(
                  outputAmount,
                  tokens[outputTokenAddress]?.decimal || 18
                ),
                detectDecimalFromPrice(outputAmount)
              ) +
              ' ' +
              tokens[outputTokenAddress]?.symbol,
            hash
          })
        )
      } else {
        toast.error(
          messageAndViewOnBsc({
            title: 'Swap failed',
            hash
          })
        )
      }
    } catch (e) {
      console.log(e)
    }
  }

  return {
    accFetchBalance,
    balances,
    fetchBalanceOfAllToken,
    updateBalanceAfterSwap
  }
}

export const useBalanceTracker = () => {
  // const dispatch = useDispatch()
  // const [tracker, setTracker] = useState<any>(null)
  const { tokens } = useListTokens()
  const { account } = useWeb3React()
  const { fetchBalanceOfAllToken } = useWalletBalance()

  useEffect(() => {
    const id = setInterval(async () => {
      if (account && Object.keys(tokens).length > 0) {
        try {
          await fetchBalanceOfAllToken(account)
        } catch (e) {
          console.log(e)
        }
      }
    }, TIME_INTERVAL_TO_FETCH_BALANCE)
    return () => {
      clearInterval(id)
    }
  }, [account, tokens])
}

