// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'
import { BigNumber } from 'ethers'

export const tokens = createSlice({
  name: 'wallet',
  initialState,
  reducers: {
    initBalances: (
      state,
      action: PayloadAction<{ balances: any; account: string }>
    ) => {
      state.balances = action.payload.balances
      state.accFetchBalance = action.payload.account
    },
    updateBalance: (
      state,
      action: PayloadAction<{
        tokenAddress: string
        balance: string
        account?: string
      }>
    ) => {
      if (action.payload.account && state.account !== action.payload.account) {
        return
      }
      state.balances[action.payload.tokenAddress] = action.payload.balance
    },
    setWalletTxsFromSwapx: (state, action: PayloadAction<{ txs: any }>) => {
      state.walletTxsFromSwapX = action.payload.txs
    },
    setWalletTxsFromAmm: (state, action: PayloadAction<{ txs: any }>) => {
      const existHash = state.walletTxsFromSwapX.map(
        (e: any) => e.transactionHash
      )

      state.walletTxsFromAmm = action.payload.txs.filter(
        (e: any) => !existHash.includes(e.transactionHash)
      )
    },
    setWalletTxs: (state, action: PayloadAction<{ txs: any }>) => {
      state.walletTxs = action.payload.txs
    },
    setWalletAccount: (state, action: PayloadAction<{ account: string }>) => {
      state.account = action.payload.account
    },
    setAgtAllowance: (
      state,
      action: PayloadAction<{ allowance: BigNumber; tokenAddress: string }>
    ) => {
      state.aggregatorAllowance[action.payload.tokenAddress] =
        action.payload.allowance
    }
  }
})

// Actions
export const {
  setAgtAllowance,
  initBalances,
  updateBalance,
  setWalletTxs,
  setWalletTxsFromSwapx,
  setWalletTxsFromAmm,
  setWalletAccount
} = tokens.actions

export default tokens.reducer
