import React, { useEffect, useState } from 'react'
import { BigNumber, ethers } from 'ethers'
import { useSwapInfo } from '../../state/marketSwap/hooks/useSwapInfo'
import {
  convertRouteReserveToString,
  formatFloat,
  numberToWei,
  weiToNumber
} from '../../utils/helpers'
import _ from 'lodash'
import { useSelector } from 'react-redux'
import { useCurrentPair } from '../../state/currentPair/hook'
import {
  getAmountOutBySolution,
  getCustomSolution,
  getPoolsFromSwapRouteData
} from 'swap-tools-2'

const bn = ethers.BigNumber.from

export const PriceImpact = ({
  amountIn,
  amountOut,
  dists
}: {
  amountIn: BigNumber
  amountOut: BigNumber
  dists: number[]
}) => {
  const { routes, routesCap, setCurrentImpactPrice } = useSwapInfo()
  const [priceImpact, setPriceImpact] = useState<string>('')
  const [color, setColor] = useState<string>('#27ae60')
  const { theme } = useSelector((state: any) => ({
    theme: state.swap.theme
  }))
  const { inputToken } = useCurrentPair()
  // const { simulateOutput } = useEstimateSwapOutput()

  useEffect(() => {
    const fetch = async () => {
      if (
        routes.value.length === 0 ||
        !amountIn ||
        !amountOut ||
        amountIn.isZero() ||
        amountOut.isZero() ||
        !dists
      ) {
        setPriceImpact('0')
        setColor('#27ae60')
        return
      }
      let noSlippageRate = null
      const ai = amountIn.gt(numberToWei(1, inputToken.decimal))
        ? bn(numberToWei(1, inputToken.decimal))
        : amountIn.div(100)
      let swapRouteData = routesCap.map((r: any) => {
        return {
          liquidity: r.liquidity,
          route: r.steps,
          score: 0
        }
      })
      swapRouteData = convertRouteReserveToString(_.cloneDeep(swapRouteData))
      const pools = await getPoolsFromSwapRouteData({ swapRouteData })
      const solution = getCustomSolution({
        amountIn: ai.toString(),
        swapRouteData,
        dists
      })
      solution.forEach((solution) => {
        if (solution.score == null) {
          console.warn('malformed solution: null score', solution)
          solution.score = 0
        }
      })
      const res = getAmountOutBySolution({
        amountIn: ai,
        pools,
        solution
      })
      noSlippageRate = res.amountOut

      if (noSlippageRate) {
        let colorPriceImpact = '27ae60'

        const currentRate = amountOut.mul(numberToWei(1)).div(amountIn)

        const result = bn(noSlippageRate.sub(currentRate))
          .mul(numberToWei(1))
          .mul(100)
          .div(noSlippageRate)

        let ip = formatFloat(weiToNumber(result), 4)
        setCurrentImpactPrice(Number(ip))

        if (currentRate.gt(noSlippageRate)) {
          ip = '0'
        }
        colorPriceImpact = '#27ae60'
        let impact: number | string = Number(ip) || 0
        if (impact > 5) colorPriceImpact = 'red'
        if (impact < 5) colorPriceImpact = 'orange'
        if (impact < 3)
          colorPriceImpact = theme === 'dark' ? 'white' : '#535353'
        if (impact < 1) colorPriceImpact = '#27ae60'
        if (impact < 0.01) ip = '0'
        setPriceImpact(ip)
        setColor(colorPriceImpact)
      }
    }

    fetch().catch(console.error) // any error from external libs must be catched
  }, [amountOut])

  return (
    <span style={{ color: color || '#27ae60' }}>
      {priceImpact ? formatFloat(priceImpact, 3) : 0}%
    </span>
  )
}
