import { numberToWei } from '../../../utils/helpers'
import config from '../../../config'
import { useDispatch, useSelector } from 'react-redux'
import {
  setGasLimit,
  setGetAmountOutCallback,
  setRoutes,
  setSlippageReducer,
  setTimeoutReducer,
  setCurrentPriceImpact as setCurrentImpactPriceReduce,
  setRoutesTest,
  setLoadingAmountOutReduce,
  setIsFinishedReduce
} from '../reducer'
import { State } from '../../types'
import { BigNumber } from 'ethers'
import { useTokenPrice } from '../../token/hooks/useTokenPrice'
import { getRoutes, killGetAmountOutWorker } from 'swap-tools-2'

export const useSwapInfo = () => {
  const {
    bestSolution,
    routes,
    slippage,
    timeout,
    routesCap,
    routesTest,
    gasLimit,
    gasAmount,
    loadingGasAmount,
    currentPriceImpact,
    getAmountOutFinished
  }: any = useSelector((state: State) => {
    return {
      routesTest: state.marketSwap.routesTest,
      routesCap: state.marketSwap.routesCap,
      routes: state.marketSwap.routes,
      slippage: state.marketSwap.slippage,
      timeout: state.marketSwap.timeout,
      bestSolution: state.marketSwap.bestSolution,
      gasLimit: state.marketSwap.gasLimit,
      gasAmount: state.marketSwap.gasAmount,
      loadingGasAmount: state.marketSwap.loadingGasAmount,
      currentPriceImpact: state.marketSwap.currentPriceImpact,
      getAmountOutFinished: state.marketSwap.getAmountOutFinished
    }
  })
  const { tokenPrices } = useTokenPrice()

  const dispatch = useDispatch()

  const fetchRoutes = async (inputAddress: string, outputAddress: string) => {
    killGetAmountOutWorker()
    const routes = await getRoutes(inputAddress, outputAddress, {
      routeLimit: 64,
      routeNumber: 8
    })

    dispatch(setGetAmountOutCallback({ nextCallBack: null }))
    dispatch(setRoutes({ routes, inputAddress, outputAddress }))
    dispatch(setRoutesTest({ routes }))
  }

  const setGasAmountToSwap = (gasLimit: BigNumber) => {
    dispatch(setGasLimit({ gasLimit, gasAmount: calcGasAmount(gasLimit) }))
  }

  const setLoadingAmountOut = (loading: boolean) => {
    dispatch(setLoadingAmountOutReduce({ loading }))
  }

  const calcGasAmount = (gasLimit: BigNumber) => {
    return gasLimit
      .mul(2)
      .div(3)
      .mul(5 * 10 ** 9)
      .mul(numberToWei(tokenPrices[config.chain[56].addresses.wbnb] || 0))
      .div(numberToWei(1))
  }

  const setSlippage = (slippage: number) => {
    dispatch(setSlippageReducer({ slippage }))
  }

  const setTimeOut = (timeout: number) => {
    dispatch(setTimeoutReducer({ timeout }))
  }

  const setCurrentImpactPrice = (priceImpact: number) => {
    dispatch(setCurrentImpactPriceReduce({ priceImpact }))
  }

  const setIsFinished = (isFinished: boolean) => {
    dispatch(setIsFinishedReduce({ isFinished }))
  }

  return {
    routesCap,
    bestSolution,
    slippage,
    timeout,
    routesTest,
    getAmountOutFinished,
    routes,
    gasLimit,
    gasAmount,
    currentPriceImpact,
    loadingGasAmount,
    fetchRoutes,
    setTimeOut,
    setGasAmountToSwap,
    setSlippage,
    setCurrentImpactPrice,
    setLoadingAmountOut,
    setIsFinished
  }
}
