import React, { useEffect, useState } from 'react'
import './style.scss'
import {
  ChartingLibraryWidgetOptions,
  IChartingLibraryWidget,
  ResolutionString,
  widget
} from '../../charting_library'
import { Datafeed } from '../../service/datafeed'
import { useDispatch, useSelector } from 'react-redux'
import { setSingleStateData } from '../../store/actions/initSettingData'
import { EmptyStarIcon, LaunchZoneLogo, StarIcon } from '../ui/Icons'
import { useListTokens } from '../../state/token/hook'
import { useCurrentPair } from '../../state/currentPair/hook'
import { useWindowSize } from '../../hooks/useWindowSize'
import { useWalletTx } from '../../state/wallet/hooks/useWalletTx'
import { useWeb3React as useCustomWeb3React } from '../../state/customWeb3React/hook'
import { useChartInfo } from '../../state/chart/hook'
import { Loading } from '../ui/Loading'
import { SelectTokenInput } from './SelectToken'
import { useBookmark } from '../../state/token/hooks/useBookmarkAndRecent'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'
import { useTokenPrice } from '../../state/token/hooks/useTokenPrice'
import { detectDecimalFromPrice, formatFloat } from '../../utils/helpers'

export interface ChartContainerProps {
  interval: ChartingLibraryWidgetOptions['interval']
  datafeedUrl: string
  libraryPath: ChartingLibraryWidgetOptions['library_path']
  chartsStorageUrl: ChartingLibraryWidgetOptions['charts_storage_url']
  chartsStorageApiVersion: ChartingLibraryWidgetOptions['charts_storage_api_version']
  clientId: ChartingLibraryWidgetOptions['client_id']
  userId: ChartingLibraryWidgetOptions['user_id']
  fullscreen: ChartingLibraryWidgetOptions['fullscreen']
  autosize: ChartingLibraryWidgetOptions['autosize']
  studiesOverrides: ChartingLibraryWidgetOptions['studies_overrides']
  containerId: ChartingLibraryWidgetOptions['container_id']
}

export const Chart = ({
  interval,
  containerId,
  libraryPath,
  clientId,
  userId,
  fullscreen,
  autosize,
  studiesOverrides
}: ChartContainerProps) => {
  const dispatch = useDispatch()
  const { theme, loadingChart, noChartData } = useSelector((state: any) => ({
    theme: state.swap.theme,
    loadingChart: state.swap.loadingChart,
    noChartData: state.swap.noChartData
  }))

  const { account } = useCustomWeb3React()
  const {
    inputTokenAddress,
    outputTokenAddress,
    exchange,
    setPairByInputToken,
    findingOutputToken
  } = useCurrentPair()
  const { walletTxs } = useWalletTx()
  const { tokens } = useListTokens()
  const { width } = useWindowSize()
  const { routerArr, fetchChartInfo } = useChartInfo()
  const [tradingviewWidget, setTradingviewWidget] = useState<any>(null)
  const { bookmarkAddresses, addBookmarkAddress } = useBookmark()
  const { tokenPrices } = useTokenPrice()

  useEffect(() => {
    setSingleStateData('loadingChart', true)(dispatch)
  }, [inputTokenAddress, outputTokenAddress, exchange])

  useEffect(() => {
    if (
      findingOutputToken ||
      !tokens[inputTokenAddress] ||
      !tokens[outputTokenAddress]
    ) {
      return
    }
    fetchChartInfo(inputTokenAddress, outputTokenAddress)
  }, [
    findingOutputToken,
    tokens[inputTokenAddress],
    tokens[outputTokenAddress]
  ])

  useEffect(() => {
    if (routerArr && routerArr.length > 0) {
      setTradingviewWidget(null)
      initChart()
    }
  }, [routerArr])

  useEffect(() => {
    if (tradingviewWidget) {
      changeTheme(tradingviewWidget, theme)
    }
  }, [theme])

  useEffect(() => {
    if (
      tradingviewWidget &&
      tokens[inputTokenAddress] &&
      tokens[outputTokenAddress] &&
      tradingviewWidget.symbol
    ) {
      console.log('refresh mark')
      try {
        tradingviewWidget.activeChart().clearMarks()
        if (account) {
          tradingviewWidget.activeChart().refreshMarks()
        }
      } catch (e) {
        console.error(e)
      }
    }
  }, [walletTxs])

  const createShapes = (widget: any) => {
    const pairName =
      tokens[inputTokenAddress].symbol + '/' + tokens[outputTokenAddress].symbol
    const allShapes = JSON.parse(localStorage.getItem('shapes') || '{}')
    const shapes = allShapes[pairName] || []
    // @ts-ignore
    widget.activeChart().removeAllShapes()

    for (const shape of shapes) {
      if (shape.point && shape.point.length > 1) {
        // @ts-ignore
        widget.activeChart().createMultipointShape(shape.point, shape.options)
      } else if (shape.point.length === 1) {
        // @ts-ignore
        widget.activeChart().createShape(shape.point[0], shape.options)
      }
    }
  }

  const initChart = async () => {
    const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone

    const widgetOptions: any = {
      symbol: routerArr.join('/'),
      datafeed: Datafeed,
      interval: interval as ChartingLibraryWidgetOptions['interval'],
      container_id: containerId as ChartingLibraryWidgetOptions['container_id'],
      library_path: libraryPath as string,
      locale: 'en',
      data_status: 'streaming',
      enabled_features: [
        'hide_left_toolbar_by_default',
        'disable_resolution_rebuild',
        'pricescale_currency'
      ],
      disabled_features: [
        'popup_hints',
        'header_chart_type',
        'header_compare',
        'timeframes_toolbar',
        'header_undo_redo',
        'header_symbol_search'
      ],
      client_id: clientId,
      user_id: userId,
      fullscreen: fullscreen,
      autosize: autosize,
      studies_overrides: studiesOverrides,
      timezone: timezone === 'Asia/Saigon' ? 'Asia/Ho_Chi_Minh' : timezone,
      loading_screen: {
        backgroundColor: 'transparent'
      },
      theme: theme === 'dark' ? 'Dark' : 'Light'
    }

    const tvWidget: IChartingLibraryWidget = new widget(widgetOptions)
    tvWidget.onChartReady(() => {
      setTradingviewWidget(tvWidget)
      changeTheme(tvWidget, theme)
      setSingleStateData('initChart', true)(dispatch)
      setSingleStateData('loadingChart', false)(dispatch)
      const saveIndicators = () => {
        const indicators = tvWidget.activeChart().getAllStudies()
        localStorage.setItem(
          'indicators',
          JSON.stringify(indicators.filter((i: any) => i.name !== 'Volume'))
        )
      }

      const createIndicators = () => {
        const indicatorsStored = JSON.parse(
          localStorage.getItem('indicators') || '[]'
        )
        for (const indicatorStored of indicatorsStored) {
          tvWidget.activeChart().createStudy(indicatorStored.name)
        }
      }

      const saveShapes = () => {
        let currentSymbol = tvWidget.symbolInterval().symbol
        currentSymbol = currentSymbol.split(':')[0]
        const shapes = tvWidget.activeChart().getAllShapes()
        const result = []
        for (const i in shapes) {
          if (shapes[i].name === 'brush') continue
          const detail = tvWidget.activeChart().getShapeById(shapes[i].id)
          const options = detail.getProperties()
          result.push({
            point: detail.getPoints(),
            options: {
              shape: shapes[i].name,
              ...options
            }
          })
        }
        const oldShapes = JSON.parse(localStorage.getItem('shapes') || '{}')

        localStorage.setItem(
          'shapes',
          JSON.stringify({
            ...oldShapes,
            [currentSymbol]: result
          })
        )
      }

      tvWidget.subscribe('panes_height_changed', saveIndicators)
      tvWidget.subscribe('study', saveIndicators)
      tvWidget.subscribe('drawing_event', () => saveShapes)
      tvWidget.subscribe('drawing', () => saveShapes)
      tvWidget.subscribe('mouse_up', () => {
        saveShapes()
        saveIndicators()
      })
      createIndicators()
      createShapes(tvWidget)
    })
  }

  const changeTheme = (widget: any, themeChanged: string) => {
    if (widget) {
      widget.changeTheme(theme === 'dark' ? 'Dark' : 'Light')
      widget.addCustomCSSFile(
        themeChanged === 'dark' ? '/darkTheme.css' : '/lightTheme.css'
      )
      if (themeChanged === 'dark') {
        setTimeout(() => {
          widget?.applyOverrides({
            'paneProperties.backgroundType': 'solid',
            'paneProperties.background': '#181818',
            'paneProperties.backgroundGradientEndColor': '#181818',
            'paneProperties.backgroundGradientStartColor': '#181818',
            'paneProperties.vertGridProperties.color': 'rgba(128,128,128,0.2)',
            'paneProperties.horzGridProperties.color': 'rgba(128,128,128,0.2)',
            'scalesProperties.textColor': '#ffffff'
          })
        })
      } else {
        setTimeout(() => {
          widget?.applyOverrides({
            'paneProperties.background': '#ffffff',
            'paneProperties.vertGridProperties.color': 'rgba(128,128,128,0.2)',
            'paneProperties.horzGridProperties.color': 'rgba(128,128,128,0.2)',
            'scalesProperties.textColor': '#181818'
          })
        })
      }
    }
  }

  const tokenAddress =
    inputTokenAddress &&
    outputTokenAddress &&
    STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase()) &&
    !STABLECOIN_ADDRESSES.includes(outputTokenAddress.toLowerCase())
      ? outputTokenAddress
      : inputTokenAddress

  const BookmarkIcon = bookmarkAddresses.includes(inputTokenAddress)
    ? StarIcon
    : EmptyStarIcon

  return (
    <div key={1} className='chart'>
      <div className='chart__header'>
        <div className='search-token-and-price-box'>
          <SelectTokenInput key={2} onSelect={setPairByInputToken} />
          <BookmarkIcon
            onClick={() => {
              addBookmarkAddress(inputTokenAddress)
            }}
          />
          {STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase()) ? (
            ''
          ) : !tokenPrices[inputTokenAddress] ? (
            <span style={{ width: '6rem' }} className='skeleton-loader__bone' />
          ) : (
            tokenPrices[tokenAddress] && (
              <span>
                <span className='text-grey'>$</span>
                <span className='text-buy'>
                  {formatFloat(
                    tokenPrices[tokenAddress],
                    detectDecimalFromPrice(tokenPrices[tokenAddress])
                  )}
                </span>
              </span>
            )
          )}
        </div>
      </div>
      <div className='chart-box'>
        <div
          id={containerId}
          className={`TVChartContainer ${loadingChart ? 'hidden' : ''}`}
          style={{
            width: '100%',
            height: width && width < 992 ? '450px' : '100%'
          }}
        />
        {loadingChart ? <Loading /> : ''}

        <div
          className={`chart__nodata-notify ${
            !loadingChart && noChartData ? '' : 'hidden'
          }`}
        >
          <div>
            <LaunchZoneLogo />
          </div>
          <p className='mt-2'>No data available</p>
        </div>
      </div>
    </div>
  )
}

Chart.defaultProps = {
  interval: (localStorage.getItem('chart_resolution') ||
    '240') as ResolutionString,
  containerId: 'tv_chart_container',
  datafeedUrl: 'https://demo_feed.tradingview.com',
  libraryPath: '/charting_library/',
  chartsStorageUrl: 'https://saveload.tradingview.com',
  chartsStorageApiVersion: '1.2',
  clientId: 'tradingview.com',
  userId: 'public_user_id',
  fullscreen: false,
  autosize: true,
  studiesOverrides: {}
}
