import { BigNumber } from 'ethers'
import { bn } from '../../utils/helpers'

interface RouteType {
  value: any
  inputAddress: string
  outputAddress: string
}

export interface marketSwapState {
  amountOut: string
  amountIn: string
  routes: RouteType
  slippage: number
  timeout: number
  bestSolution: any
  routesCap: any
  gasLimit: BigNumber
  gasAmount: BigNumber
  currentPriceImpact: number
  routesTest: any
  loadingGasAmount: boolean
  getAmountOutFinished: boolean
  getAmountOutCallback:
    | null
    | (({ ai, s }: { ai: BigNumber; s: string | null }) => Promise<any>)
}

export const initialState: marketSwapState = {
  amountOut: '',
  amountIn: '',
  routesCap: [],
  routesTest: [],
  routes: {
    value: [],
    inputAddress: '',
    outputAddress: ''
  },
  slippage: localStorage.getItem('swapSlippage')
    ? Number(localStorage.getItem('swapSlippage'))
    : 0.5,
  timeout: localStorage.getItem('swapTimeout')
    ? Number(localStorage.getItem('swapTimeout'))
    : 20,
  bestSolution: {},
  gasLimit: bn(0),
  gasAmount: bn(0),
  currentPriceImpact: 0,
  getAmountOutCallback: null,
  loadingGasAmount: false,
  getAmountOutFinished: true
}
