import React, { useEffect, useMemo, useState } from 'react'

import { div, numberToWei } from '../../utils/helpers'

import { getPairInfo } from '../../service/commonService'
import {
  RESET_MARKET_ORDER,
  SET_OLD_MARKET_ORDERS,
  UPDATE_NEW_MARKET_ODER
} from '../../store/actions/marketOrders'
import config from '../../config'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'
import { useDispatch, useSelector } from 'react-redux'
import { useCurrentPair } from '../../state/currentPair/hook'
import { useListTokens } from '../../state/token/hook'
import { ethers } from 'ethers'
import { formatWeiToDisplayNumber } from '../../utils/formatBalance'
import moment from 'moment'

const range = 16
const bn = ethers.BigNumber.from

const TIME_TO_FETCH_NEW_ORDER = 10000

export const useMarketOrder = () => {
  const { marketOrders } = useSelector((state: any) => ({
    marketOrders: state.swap.marketOrders
  }))

  const [currentPairAddress, setCurrentPairAddress] = useState<any>('')
  const [loadingMarketOrder, setLoadingMarketOrder] = useState<any>(false)
  const [lastBlock, setLastBlock] = useState<any>(null)
  const [listenInterval, setListenInterval] = useState<any>(null)

  const {
    inputTokenAddress,
    outputTokenAddress,
    currentPairAddress: pairAddress
  } = useCurrentPair()
  const { tokens } = useListTokens()
  const dispatch = useDispatch()

  const inputToken = tokens ? tokens[inputTokenAddress] : {}
  const outputToken = tokens ? tokens[outputTokenAddress] : {}

  useEffect(() => {
    return () => {
      config.wssProvider.removeAllListeners()
    }
  }, [])

  useEffect(() => {
    try {
      if (
        inputTokenAddress &&
        outputTokenAddress &&
        inputTokenAddress !== outputTokenAddress &&
        currentPairAddress !== pairAddress
      ) {
        clearInterval(listenInterval)
        setCurrentPairAddress(pairAddress)
        dispatch({ type: RESET_MARKET_ORDER })
        fetchRecentSwaps()
      }
    } catch (e) {
      console.error(e)
    }
  }, [pairAddress])

  const fetchRecentSwaps = async () => {
    setLoadingMarketOrder(true)
    const pairInfo = await getPairInfo(pairAddress)
    config.provider
      .getBlockNumber()
      .then(async (head: number) => {
        // fetch new market orders each TIME_TO_FETCH_NEW_ORDER second
        setLastBlock(head)
        const intervalId = setInterval(() => {
          listenRecentSwaps({ pairInfo })
        }, TIME_TO_FETCH_NEW_ORDER)
        setListenInterval(intervalId)

        // fetch recent orders (in last 3000 block or 50 orders)
        await getRecentSwaps({
          pairInfo,
          latestBlock: head,
          pairAddress,
          toBlock: head,
          range,
          callback: (data: any) => {
            dispatch({
              type: SET_OLD_MARKET_ORDERS,
              data
            })
          }
        })
        setLoadingMarketOrder(false)
      })
      .catch((e: any) => {
        console.error(e)
        setLoadingMarketOrder(false)
      })
  }

  //@ts-ignore
  const listenRecentSwaps = async ({ pairInfo }: { pairInfo: any }) => {
    try {
      const logs = await config.provider.getLogs({
        address: pairAddress,
        topics: [
          '0xd78ad95fa46c994b6551d0da85fc275fe613ce37657fb8d5e3d130840159d822'
        ],
        fromBlock: lastBlock,
        toBlock: 'latest'
      })

      let newLastBLock = lastBlock
      for (let i = 0; i < logs.length; i++) {
        if (logs[i].blockNumber > newLastBLock) {
          newLastBLock = logs[i].blockNumber
        }
        const data = decodeLog(logs[i], pairInfo, null)
        dispatch({
          type: UPDATE_NEW_MARKET_ODER,
          data
        })
      }
      setLastBlock(newLastBLock)
    } catch (e) {
      console.error(e)
    }
  }

  // @ts-ignore
  const getRecentSwaps = async ({
    pairInfo,
    latestBlock,
    pairAddress,
    toBlock,
    range = 1,
    callback,
    maxRecord = 50
  }: {
    pairInfo: any
    latestBlock: number
    pairAddress: string
    toBlock: number
    range: number
    callback: any
    maxRecord?: number
  }) => {
    try {
      const results: any = []
      const logs = await config.provider.getLogs({
        address: pairAddress,
        topics: [
          '0xd78ad95fa46c994b6551d0da85fc275fe613ce37657fb8d5e3d130840159d822'
        ],
        fromBlock: toBlock - range,
        toBlock
      })

      logs.forEach((log: any) => {
        const data = decodeLog(log, pairInfo, latestBlock)
        results.push(data)
      })

      callback(results.slice(0, maxRecord), pairAddress)

      if (results.length < maxRecord && range < 3000) {
        return getRecentSwaps({
          pairInfo,
          latestBlock,
          pairAddress,
          toBlock: toBlock - range,
          range: range * 2,
          callback,
          maxRecord: maxRecord - results.length
        })
      } else {
        return results
      }
    } catch (e) {
      console.log(e)
    }
  }

  const decodeLog = (log: any, pairInfo: any, latestBlock: number | null) => {
    const sender = ethers.utils.getAddress(log.topics[1].substr(2 + 24))
    const recipient = ethers.utils.getAddress(log.topics[2].substr(2 + 24))
    const data = log.data.substr(2).match(/.{1,64}/g)

    const [amount0In, amount1In, amount0Out, amount1Out] = data.map((a: any) =>
      bn('0x' + a)
    )

    let inputToken = !amount0In.isZero()
      ? pairInfo.token0.adr
      : pairInfo.token1.adr
    let outputToken = !amount0In.isZero()
      ? pairInfo.token1.adr
      : pairInfo.token0.adr
    const spentAmount = !amount0In.isZero() ? amount0In : amount1In
    const returnAmount = !amount0Out.isZero() ? amount0Out : amount1Out

    if (
      inputTokenAddress === config.chain[56].addresses.bnb ||
      outputTokenAddress === config.chain[56].addresses.bnb
    ) {
      inputToken =
        inputToken === config.chain[56].addresses.wbnb
          ? config.chain[56].addresses.bnb
          : inputToken

      outputToken =
        outputToken === config.chain[56].addresses.wbnb
          ? config.chain[56].addresses.bnb
          : outputToken
    }

    const timestamp = latestBlock
      ? new Date().getTime() - (latestBlock - log.blockNumber) * 3000
      : new Date().getTime()

    return {
      sender,
      recipient,
      inputToken,
      outputToken,
      spentAmount,
      returnAmount,
      time: timestamp,
      transactionHash: log.transactionHash,
      pairAddress: log.address
    }
  }

  const detectInfoToShow = ({ inputTokenInTx }: { inputTokenInTx: string }) => {
    let inputToken = tokens ? tokens[inputTokenAddress] : {}
    let outputToken = tokens ? tokens[outputTokenAddress] : {}

    if (STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase())) {
      const tmp = { ...inputToken }
      inputToken = { ...outputToken }
      outputToken = tmp
    }

    const isBuy =
      inputTokenInTx.toLowerCase() === outputToken.address.toLowerCase()

    return { inputToken, outputToken, isBuy }
  }

  const marketOrderColumns = useMemo(() => {
    return [
      {
        name: 'inputToken',
        title: 'Type',
        className: 'market-order__item hidden-on-phone',
        render: (inputTokenInTx: string) => {
          const { isBuy } = detectInfoToShow({ inputTokenInTx })
          return (
            <span className={`market-order__type ${isBuy ? 'buy' : 'sell'}`}>
              {isBuy ? 'Buy' : 'Sell'}
            </span>
          )
        }
      },
      {
        name: 'inputToken',
        title: 'Tokens',
        className: 'market-order__item',
        // @ts-ignore
        render: (inputTokenInTx: string, object: any) => {
          const { isBuy, inputToken } = detectInfoToShow({ inputTokenInTx })
          const amount = isBuy ? object.returnAmount : object.spentAmount

          return (
            <div className={`market-order__token ${isBuy ? 'buy' : 'sell'}`}>
              <p>
                {formatWeiToDisplayNumber(amount, 4, inputToken.decimal || 18)}
              </p>
              <p>{inputToken.symbol}</p>
            </div>
          )
        }
      },
      {
        name: 'spentAmount',
        title: 'Total Pay',
        className: 'market-order__item hidden-on-phone',
        // @ts-ignore
        render: (spentAmount: any, object: any) => {
          const { isBuy, outputToken } = detectInfoToShow({
            inputTokenInTx: object.inputToken
          })
          const amount = isBuy ? spentAmount : object.returnAmount
          return (
            <div
              className={`market-order__total-pay ${isBuy ? 'buy' : 'sell'}`}
            >
              <p>
                {formatWeiToDisplayNumber(amount, 4, outputToken.decimal || 18)}
              </p>
              <p>{outputToken.symbol}</p>
            </div>
          )
        }
      },
      {
        name: 'price',
        title: 'Price',
        className: 'market-order__item',
        //@ts-ignore
        render: (x: string, tx: any) => {
          const { isBuy, outputToken, inputToken } = detectInfoToShow({
            inputTokenInTx: tx.inputToken
          })
          let price = isBuy
            ? div(tx.spentAmount, tx.returnAmount)
            : div(tx.returnAmount, tx.spentAmount)
          const priceDecimal = 18 + outputToken.decimal - inputToken.decimal
          let priceToDisplay = formatWeiToDisplayNumber(
            numberToWei(price, priceDecimal),
            4,
            priceDecimal
          )
          return (
            <div className={`market-order__price ${isBuy ? 'buy' : 'sell'}`}>
              <p>{priceToDisplay}</p>
              <p>
                {STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase())
                  ? inputToken?.symbol
                  : outputToken?.symbol}
              </p>
            </div>
          )
        }
      },
      {
        name: 'time',
        title: 'Time',
        className: 'market-order__item',
        render: (timestamp: number, object: any) => (
          <span className={`market-order__time ${object.type}`}>
            {moment(timestamp).format('hh:mm a')}
          </span>
        )
      },
      {
        name: 'transactionHash',
        title: 'Tx',
        className: 'market-order__item text-right market-order__hash',
        render: (hash: string) => (
          <a
            href={'https://bscscan.com/tx/' + hash}
            target='_blank'
            rel='noreferrer'
          >
            {hash.slice(0, 8)}
          </a>
        )
      }
    ]
  }, [inputToken, outputToken])

  return { marketOrderColumns, loadingMarketOrder, marketOrders }
}
