// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { BlackToken, initialState } from './type'
const MAX_RECENT_SEARCH = 20
export const tokens = createSlice({
  name: 'tokens',
  initialState,
  reducers: {
    setBlackListTokens: (
      state,
      action: PayloadAction<{ blackList: BlackToken[] }>
    ) => {
      state.blackList = action.payload.blackList
    },
    setNoFeeTokens: (state, action: PayloadAction<{ noFeeList: string[] }>) => {
      state.noFeeList = action.payload.noFeeList
    },
    setWhiteListTokens: (state, action: PayloadAction<{ tokens: any }>) => {
      state.tokens = action.payload.tokens
    },
    addToken: (state, action: PayloadAction<{ token: any }>) => {
      state.tokens[action.payload.token.address] = action.payload.token
    },
    addTokenPrice: (
      state,
      action: PayloadAction<{ tokenAddress: string; price: string }>
    ) => {
      state.tokenPrices[action.payload.tokenAddress] = action.payload.price
    },
    addTokenRate: (
      state,
      action: PayloadAction<{
        tokenAddress: string
        outputAddress: string
        price: string
      }>
    ) => {
      state.tokenPrices[
        action.payload.tokenAddress + '/' + action.payload.outputAddress
      ] = action.payload.price
    },
    addBookMarkAddressReducer: (
      state,
      action: PayloadAction<{ address: string }>
    ) => {
      let newBookmarks = []

      if (
        state.bookmarkAddresses.findIndex(
          (e) => e === action.payload.address
        ) >= 0
      ) {
        newBookmarks = state.bookmarkAddresses.filter(
          (e: string) => e !== action.payload.address
        )
      } else {
        newBookmarks = [action.payload.address, ...state.bookmarkAddresses]
      }
      localStorage.setItem('bookmarkAddresses', JSON.stringify(newBookmarks))
      state.bookmarkAddresses = newBookmarks
    },
    addRecentToken: (state, action: PayloadAction<{ token: any }>) => {
      const oldRecent = state.recentTokens.filter(
        (e: any) => e.address !== action.payload.token.address
      )
      const newRecentTokens = [action.payload.token, ...oldRecent].slice(
        0,
        MAX_RECENT_SEARCH
      )
      localStorage.setItem('recentTokens', JSON.stringify(newRecentTokens))
      state.recentTokens = newRecentTokens
    }
  }
})

// Actions
export const {
  setWhiteListTokens,
  addToken,
  addTokenPrice,
  addTokenRate,
  addBookMarkAddressReducer,
  addRecentToken,
  setBlackListTokens,
  setNoFeeTokens
} = tokens.actions

export default tokens.reducer
