import historyProvider, { resolutionToSeconds } from './historyProvider'
import { store } from '../../state'
import {
  detectDecimalFromPrice,
  div,
  numberToWei,
  weiToNumber
} from '../../utils/helpers'
import {
  SET_CHART_HAVE_NO_DATA,
  SET_FIRST_BLOCK,
  SET_LAST_CANDLE
} from '../../store/actions/chart'
import moment from 'moment'
import { formatWeiToDisplayNumber } from '../../utils/formatBalance'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'

const supportedResolutions = ['1', '5', '15', '60', '240', '1D']
const configDefault = {
  supported_resolutions: supportedResolutions,
  supports_search: false,
  supports_group_request: false,
  supports_marks: true,
  supports_timescale_marks: true,
  supports_time: true,
  timezone: 'Asia/Bangkok'
}

const TIME_IN_RESOLUTION = {
  '1': 60,
  '5': 60 * 5,
  '15': 60 * 15,
  '60': 60 * 60,
  '120': 60 * 120,
  '240': 60 * 240,
  '1D': 60 * 60 * 24
}

const TIME_TO_UPDATE_CHART = 15000

export const Datafeed = {
  subscribeBarsInterval: {},
  inputAddress: '',
  outputAddress: '',
  exchange: 'pancake2',
  onReady: (callback: any) => {
    console.log('=====onReady running====')
    const state = store.getState()
    const outputAddress = state.currentPair.outputTokenAddress
    const inputAddress = state.currentPair.inputTokenAddress
    const outputSymbol = state.tokens.tokens[outputAddress]?.symbol

    const config = {
      ...configDefault,
      currency_codes:
        STABLECOIN_ADDRESSES.includes(outputAddress.toLowerCase()) ||
        STABLECOIN_ADDRESSES.includes(inputAddress.toLowerCase())
          ? [outputSymbol]
          : ['USD', outputSymbol]
    }

    setTimeout(() => callback(config), 0)
  },

  resolveSymbol: async function (
    symbolInfo: any,
    onSymbolResolvedCallback: any,
    _onResolveErrorCallback: any,
    extension: any
  ) {
    console.log('======resolveSymbol running====')
    const state = store.getState()
    const tokenPrices = state.tokens.tokenPrices
    const [inputAddress] = detectPairInfo(symbolInfo)
    let priceScale = state.chart.priceScale

    if (
      extension.currencyCode === 'USD' ||
      extension.currencyCode === undefined
    ) {
      priceScale = tokenPrices[inputAddress]
        ? 10 ** detectDecimalFromPrice(tokenPrices[inputAddress])
        : 1000
    }

    const symbolName = state.chart.symbolName

    var symbolStub = {
      name: symbolName,
      description: '',
      type: 'crypto',
      ticker: symbolInfo,
      session: '24x7',
      exchange: '',
      minmov: 1,
      pricescale: priceScale,
      has_intraday: true,
      supported_resolution: supportedResolutions,
      volume_precision: 8,
      data_status: 'streaming',
      currency_code: extension.currencyCode || 'USD',
      original_currency_code: 'USD'
    }
    onSymbolResolvedCallback(symbolStub)
  },

  getBars: async function (
    symbolInfo: any,
    interval: any,
    periodParams: any,
    onHistoryCallback: any,
    onErrorCallback: any
  ) {
    console.log('=====getBars running======')
    localStorage.setItem('chart_resolution', interval)

    const [inputAddress, outputAddress] = detectPairInfo(symbolInfo.ticker)

    if (periodParams.firstDataRequest) {
      store.dispatch({
        type: SET_LAST_CANDLE,
        data: {}
      })
      store.dispatch({
        type: SET_CHART_HAVE_NO_DATA,
        data: false
      })
      store.dispatch({
        type: SET_FIRST_BLOCK,
        data: {
          pair: inputAddress + '-' + outputAddress + '-' + interval,
          block: null
        }
      })
    }

    historyProvider
      .getBars({
        currencyCode: symbolInfo.currency_code,
        route: symbolInfo.ticker,
        resolution: interval,
        limit: calcLimitCandle(periodParams.from, periodParams.to, interval),
        to: periodParams.to
      })
      .then((bars: any) => {
        if (bars.length > 0) {
          const state = store.getState()
          const firstBlock =
            state.swap.firstBlock[
              inputAddress + '-' + outputAddress + '-' + interval
            ]
          if (firstBlock) {
            bars = bars.filter((candle: any) => {
              return candle.block < firstBlock
            })
          }

          bars = bars.map((e: any) => {
            return {
              ...e,
              open: Number(e.open),
              close: Number(e.close),
              low: Number(e.low),
              high: Number(e.high),
              volume: Number(e.volume)
            }
          })
          if (periodParams.firstDataRequest) {
            store.dispatch({
              type: SET_LAST_CANDLE,
              data: {
                pair: inputAddress + '-' + outputAddress + '-' + interval,
                candle: bars[bars.length - 1]
              }
            })
          }
          store.dispatch({
            type: SET_FIRST_BLOCK,
            data: {
              pair: inputAddress + '-' + outputAddress + '-' + interval,
              block: bars[0].block
            }
          })

          onHistoryCallback(bars, { noData: false })
        } else {
          if (periodParams.firstDataRequest) {
            store.dispatch({
              type: SET_CHART_HAVE_NO_DATA,
              data: true
            })
          }
          onHistoryCallback(bars, { noData: true })
        }
      })
      .catch((err: any) => {
        onErrorCallback(err)
      })
  },

  subscribeBars: function (
    symbolInfo: any,
    resolution: any,
    onRealtimeCallback: any,
    subscriberUID: any,
    _onResetCacheNeededCallback: any
  ) {
    const [inputAddress] = detectPairInfo(symbolInfo.ticker)
    if (inputAddress === 'init') {
      return
    }
    this.subscribeBarsInterval[subscriberUID] = setInterval(() => {
      const [inputAddress, outputAddress] = detectPairInfo(symbolInfo.ticker)

      historyProvider
        .getBars({
          currencyCode: symbolInfo.currency_code,
          route: symbolInfo.ticker,
          resolution,
          limit: 1,
          to: Math.floor(new Date().getTime() / 1000)
        })
        .then((data: any) => {
          if (data.length > 0) {
            const state = store.getState()
            const candle = data[data.length - 1]
            const lastCandle =
              state.swap.lastCandle[
                inputAddress + '-' + outputAddress + '-' + resolution
              ]
            let time

            if (candle.block > lastCandle.block) {
              time = new Date().getTime()
              store.dispatch({
                type: SET_LAST_CANDLE,
                data: {
                  pair: inputAddress + '-' + outputAddress + '-' + resolution,
                  candle: {
                    ...candle,
                    time
                  }
                }
              })
            } else {
              time = lastCandle.time
            }

            const dataToUpdate = {
              time,
              open: Number(candle.open),
              close: Number(candle.close),
              low: Number(candle.low),
              high: Number(candle.high),
              volume: Number(candle.volume)
            }

            onRealtimeCallback(dataToUpdate)
          }
        })
        .catch((e) => {
          console.error(e)
        })
    }, TIME_TO_UPDATE_CHART)
  },
  unsubscribeBars: function (subscriberUID: string) {
    if (this.subscribeBarsInterval[subscriberUID]) {
      clearInterval(this.subscribeBarsInterval[subscriberUID])
    }
  },
  getTimescaleMarks: async function (
    symbolInfo: any,
    //@ts-ignore
    from: any,
    //@ts-ignore
    to: any,
    onDataCallback: any,
    //@ts-ignore
    resolution: any
  ) {
    const state = store.getState()
    const walletTxs = state.wallet.walletTxs
    const tokens = state.tokens.tokens

    const [inputAddress, outputAddress] = detectPairInfo(symbolInfo.ticker)
    console.log(inputAddress, outputAddress, symbolInfo)

    const result = walletTxs.map((t: any) => {
      return detectMarkInfo(
        t,
        tokens[inputAddress],
        tokens[outputAddress],
        resolution,
        true
      )
    })

    onDataCallback(result)
  },
  getMarks: async function (
    symbolInfo: any,
    //@ts-ignore
    from: any,
    //@ts-ignore
    to: any,
    onDataCallback: any,
    //@ts-ignore
    resolution: any
  ) {
    const state = store.getState()
    const walletTxs = state.wallet.walletTxs
    const tokens = state.tokens.tokens

    const [inputAddress, outputAddress] = detectPairInfo(symbolInfo.ticker)

    const result = walletTxs.map((t: any) => {
      return detectMarkInfo(
        t,
        tokens[inputAddress],
        tokens[outputAddress],
        resolution
      )
    })

    onDataCallback(result)
  }
}

const detectMarkInfo = (
  tx: any,
  currentInputToken: any,
  currentOutputToken: any,
  resolution: string,
  timescaleMark = false
) => {
  const isBuy =
    tx.inputToken.toLowerCase() === currentOutputToken?.address.toLowerCase()
  const amount = isBuy ? tx.returnAmount : tx.spentAmount
  const time = moment(tx.time).format('DD/MM/YYYY, hh:mm a')

  const result: any = {
    id: tx.transactionHash,
    time: tx.time / 1000 - (timescaleMark ? 0 : resolutionToSeconds[resolution])
  }
  let label = isBuy ? 'Buy' : 'Sell'
  let price = isBuy
    ? div(tx.spentAmount, tx.returnAmount)
    : div(tx.returnAmount, tx.spentAmount)
  const priceDecimal =
    18 + currentOutputToken.decimal - currentInputToken.decimal
  let priceToDisplay = formatWeiToDisplayNumber(
    numberToWei(price, priceDecimal),
    4,
    priceDecimal
  )

  if (timescaleMark) {
    result.color = isBuy ? '#96CF24' : '#f34159'
    result.tooltip = [
      (isBuy ? 'Buy' : 'Sell') + ' at ' + time,
      'Amount:' +
        weiToNumber(amount, currentInputToken.decimal) +
        ' ' +
        currentInputToken.symbol,
      'Price: ' + priceToDisplay + ' ' + currentOutputToken.symbol
    ]
  } else {
    result.color = isBuy ? 'green' : 'red'
    label = label.slice(0, 1)
    result.labelFontColor = '#ffffff'
    result.text = `<div>
      <p>${isBuy ? 'Buy' : 'Sell'} at ${time}</p>
      <p>Amount: ${formatWeiToDisplayNumber(
        amount,
        4,
        currentInputToken.decimal
      )} ${currentInputToken.symbol}</p>
      <p>
        Price: ${priceToDisplay} ${currentOutputToken.symbol}
      </p>
      <a href="https://bscscan.com/tx/${
        tx.transactionHash
      }" target="_blank">View on BSC</a>
    </div>`
  }
  result.label = label
  return result
}

const calcLimitCandle = (
  fromTime: number,
  toTime: number,
  resolution: string
) => {
  const timeInCandle = TIME_IN_RESOLUTION[resolution]
  if (!timeInCandle) return 320
  return Math.floor((toTime - fromTime) / timeInCandle) + 20
}

const detectPairInfo = (ticker: string) => {
  const inputAddress = ticker.split('/')[0]
  const outputAddress = ticker.split('/')[ticker.split('/').length - 1]

  return [inputAddress, outputAddress]
}
