import { ethers } from 'ethers'
import config, { getConfig } from '../../config'
import {
  getListCustomToken,
  getTokenDecimal,
  getTokenFromAddress
} from '../../service/tokenService'
import {
  addToken,
  setBlackListTokens,
  setNoFeeTokens,
  setWhiteListTokens
} from './reducer'
import { useDispatch, useSelector } from 'react-redux'
import { BlackToken } from './type'

export const useListTokens = () => {
  const { tokens, blackList, noFeeList } = useSelector((state: any) => {
    return {
      tokens: state.tokens.tokens,
      blackList: state.tokens.blackList,
      noFeeList: state.tokens.noFeeList
    }
  })

  const dispatch = useDispatch()

  const initBlackListToken = async () => {
    try {
      const blackList: BlackToken[] = await fetch(
        getConfig('BLACK_LIST_TOKENS_ENDPOINT')
      ).then((r) => r.json())

      dispatch(setBlackListTokens({ blackList }))
    } catch (e) {
      console.error(e)
    }
  }

  const initNoFeeToken = async () => {
    try {
      const noFeeList: string[] = await fetch(
        getConfig('NO_FEE_TOKENS_ENDPOINT')
      ).then((r) => r.json())

      dispatch(setNoFeeTokens({ noFeeList }))
    } catch (e) {
      console.error(e)
    }
  }

  const initWhitelistToken = async () => {
    try {
      const tokens = {}
      const res = await fetch(getConfig('TOKEN_INFO_ENDPOINT')).then((r) =>
        r.json()
      )
      res
        .filter(
          (token: any) =>
            token.network === 'binance-smart-chain' &&
            ethers.utils.isAddress(token.address)
        )
        .forEach((token: any) => {
          tokens[ethers.utils.getAddress(token.address)] = {
            ...token,
            icon:
              token.logo ||
              `https://s2.coinmarketcap.com/static/img/coins/64x64/${token.cmcId}.png`,
            address: ethers.utils.getAddress(token.address)
          }
        })
      tokens[config.chain[56].addresses.bnb] = {
        address: config.chain[56].addresses.bnb,
        decimal: 18,
        symbol: 'BNB',
        name: 'BNB',
        icon: 'https://s2.coinmarketcap.com/static/img/coins/64x64/1839.png'
      }
      const customTokens = getListCustomToken()

      dispatch(
        setWhiteListTokens({
          tokens: {
            ...tokens,
            ...customTokens
          }
        })
      )
    } catch (e) {
      console.error(e)
    }
  }

  return {
    tokens,
    blackList,
    initWhitelistToken,
    initBlackListToken,
    initNoFeeToken,
    noFeeList
  }
}

export const useCustomToken = () => {
  const { tokens } = useSelector((state: any) => {
    return {
      tokens: state.tokens.tokens
    }
  })
  const dispatch = useDispatch()

  const addCustomToken = async (address: string) => {
    if (tokens[address]) return

    const tokenInfo = await getTokenFromAddress(address)
    if (tokenInfo) {
      dispatch(addToken({ token: tokenInfo }))

      let oldCustomTokens: any = localStorage.getItem('customTokens')
      oldCustomTokens = oldCustomTokens ? JSON.parse(oldCustomTokens) : []
      localStorage.setItem(
        'customTokens',
        JSON.stringify([...oldCustomTokens, tokenInfo])
      )
      return tokenInfo
    } else {
      return null
    }
  }

  const addCustomTokenByToken = async (token: any) => {
    let customToken = {
      ...token,
      decimal: 18
    }
    dispatch(addToken({ token: customToken }))

    const decimal = await getTokenDecimal(token.address)
    if (decimal !== 18) {
      customToken = { ...customToken, decimal }
      dispatch(addToken({ token: { ...customToken, decimal } }))
    }

    let oldCustomTokens: any = localStorage.getItem('customTokens')
    oldCustomTokens = oldCustomTokens ? JSON.parse(oldCustomTokens) : []
    localStorage.setItem(
      'customTokens',
      JSON.stringify([...oldCustomTokens, customToken])
    )
  }

  return { addCustomToken, addCustomTokenByToken }
}
