import React from 'react'
import './style.scss'

export const Input = React.forwardRef((props: any, ref) => {
  return (
    <div className={`swap-input-group ${props.className}`}>
      {props.prefix && (
        <span
          className='prefix'
          onClick={() => {
            // @ts-ignore
            ref.current.focus()
          }}
        >
          {props.prefix}
        </span>
      )}
      <input
        ref={ref}
        type='text'
        {...props}
        placeholder={props.placeholder ? props.placeholder : ''}
        className='input'
        // ref={input}
        autoComplete='no'
      />
      {props.suffix && (
        <span
          className='suffix'
          onClick={() => {
            // @ts-ignore
            ref.current.focus()
          }}
        >
          {props.suffix}
        </span>
      )}
    </div>
  )
})
