import React, { useEffect, useState } from 'react'
import './style.scss'
import { useSelector } from 'react-redux'
import { getTotalSupplyOfToken } from '../../service/tokenService'
import {
  add,
  convertBNBAddressToWBNB,
  div,
  formatPercent,
  mul,
  sub,
  weiToNumber
} from '../../utils/helpers'
import { BscIcon } from '../ui/Icons'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'
import { useListTokens } from '../../state/token/hook'
import { useCurrentPair } from '../../state/currentPair/hook'
import { useTokenPrice } from '../../state/token/hooks/useTokenPrice'

const dollarUSLocale = Intl.NumberFormat('en-US')

export default () => {
  const [totalSupply, setTotalSupply] = useState<any>(null)
  const [inputTokenPrice, setInputTokenPrice] = useState<any>(null)
  const [loading, setLoading] = useState<boolean>(true)
  const [totalLiquidity, setTotalLiquidity] = useState<any>(null)
  const [fullyMarketCap, setFullyMarketCap] = useState<any>(null)
  const { tokenPrices } = useTokenPrice()
  const { topPoolsToDisplay, lastDayCandle } = useSelector((state: any) => ({
    topPoolsToDisplay: state.swap.topPoolsToDisplay,
    lastDayCandle: state.swap.lastDayCandle
  }))
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { tokens } = useListTokens()

  useEffect(() => {
    if (
      topPoolsToDisplay &&
      topPoolsToDisplay.length > 0 &&
      tokenPrices[outputTokenAddress] &&
      tokenPrices[inputTokenAddress]
    ) {
      const totalReserve = topPoolsToDisplay
        .map((b: any) => b.reserve0)
        .reduce((partialSum: any, a: any) => add(partialSum, a), 0)
      const price = STABLECOIN_ADDRESSES.includes(
        inputTokenAddress.toLowerCase()
      )
        ? tokenPrices[outputTokenAddress]
        : tokenPrices[inputTokenAddress]
      const total = mul(totalReserve, price)

      if (total) {
        setTotalLiquidity(total?.toString()?.split('.')[0] || null)
      }
    }
  }, [topPoolsToDisplay, inputTokenPrice])

  useEffect(() => {
    if (
      totalSupply &&
      tokenPrices[outputTokenAddress] &&
      tokenPrices[inputTokenAddress]
    ) {
      const price = STABLECOIN_ADDRESSES.includes(
        inputTokenAddress.toLowerCase()
      )
        ? tokenPrices[outputTokenAddress]
        : tokenPrices[inputTokenAddress]
      const result = mul(
        weiToNumber(totalSupply, tokens[inputTokenAddress]?.decimal || 18),
        price
      )
      setFullyMarketCap(result?.toString()?.split('.')[0] || null)
    }
  }, [totalSupply, tokenPrices])

  useEffect(() => {
    const price = tokenPrices[inputTokenAddress] || null
    setInputTokenPrice(price)
  }, [tokenPrices[inputTokenAddress]])

  useEffect(() => {
    if (inputTokenAddress && outputTokenAddress) {
      fetchTotalSupply()
    }
  }, [inputTokenAddress, outputTokenAddress])

  const fetchTotalSupply = async () => {
    setLoading(true)
    let address =
      STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase()) &&
      !STABLECOIN_ADDRESSES.includes(outputTokenAddress.toLowerCase())
        ? outputTokenAddress
        : inputTokenAddress
    address = convertBNBAddressToWBNB(address)
    const res = await getTotalSupplyOfToken(address)
    setTotalSupply(res)
    setLoading(false)
  }

  const getAddressToShow = () => {
    return STABLECOIN_ADDRESSES.includes(inputTokenAddress.toLowerCase())
      ? outputTokenAddress
      : inputTokenAddress
  }

  return (
    <div className='token-info-card'>
      <div className='title'>
        <span className='text-gradient font-size-18 hidden-on-phone'>
          Summary
        </span>
      </div>
      {loading ? (
        <div className='summary_rows'>
          <div className='skeleton-loader__bone' />
          <div className='skeleton-loader__bone' />
          <div className='skeleton-loader__bone' />
        </div>
      ) : (
        <div className='summary_rows'>
          <div className='info-row'>
            <span className='label'>Total Supply:</span>
            <span className='value'>
              {totalSupply
                ? dollarUSLocale.format(
                    parseInt(
                      weiToNumber(
                        totalSupply,
                        tokens[inputTokenAddress]
                          ? tokens[inputTokenAddress].decimal
                          : 18
                      )
                    )
                  )
                : ''}
            </span>
          </div>
          <div className='info-row'>
            <span className='label'>Fully Diluted Market Cap:</span>
            <span className='value'>
              {inputTokenPrice && totalSupply ? (
                <span>
                  <span className='text-grey'>$</span>
                  {dollarUSLocale.format(parseInt(fullyMarketCap))}
                </span>
              ) : (
                ''
              )}
            </span>
          </div>
          <div className='info-row'>
            <span className='label'>Volume 24h:</span>
            {lastDayCandle.volume ? (
              <span className='value'>
                <span className='text-grey'>$</span>
                <span>{dollarUSLocale.format(lastDayCandle.volume)}</span>
                <span
                  className={
                    'pl-05 ' +
                    (lastDayCandle.close > lastDayCandle.open
                      ? 'text-buy'
                      : 'text-sell')
                  }
                >
                  (
                  {formatPercent(
                    div(
                      sub(lastDayCandle.close, lastDayCandle.open),
                      lastDayCandle.open
                    )
                  )}
                  %)
                </span>
              </span>
            ) : (
              ''
            )}
          </div>
          {topPoolsToDisplay.length > 0 && totalLiquidity ? (
            <div className='info-row'>
              <span className='label'>Total liquidity:</span>
              <span className='value'>
                <span>
                  <span className='text-grey'>$</span>
                  {dollarUSLocale.format(Number(totalLiquidity))}
                </span>
              </span>
            </div>
          ) : (
            ''
          )}
        </div>
      )}
      {loading ? (
        <div className='useful-links__placeholder'>
          <div className='skeleton-loader__bone' />
          <div className='skeleton-loader__bone' />
          <div className='skeleton-loader__bone' />
        </div>
      ) : (
        <div className='useful-links'>
          <span className='useful-links__item'>
            <BscIcon />
            <a
              href={`https://bscscan.com/token/${getAddressToShow()}`}
              target='_blank'
              rel='noreferrer'
            >
              Transaction
            </a>
          </span>
          <span className='useful-links__item'>
            <BscIcon />
            <a
              href={`https://bscscan.com/token/${getAddressToShow()}#code`}
              target='_blank'
              rel='noreferrer'
            >
              Contract
            </a>
          </span>
          <span className='useful-links__item'>
            <BscIcon />
            <a
              href={`https://bscscan.com/token/${getAddressToShow()}#balances`}
              target='_blank'
              rel='noreferrer'
            >
              Holders
            </a>
          </span>
        </div>
      )}
    </div>
  )
}
