import React, { useEffect, useState } from 'react'
import { CustomTokenIcon } from '../Icons'

export const TokenIcon = (props: any) => {
  const [isError, setIsError] = useState<boolean>(!props.src)
  const onError = () => {
    setIsError(true)
  }

  useEffect(() => {
    setIsError(false)
  }, [props.src])

  if (isError) {
    return <CustomTokenIcon size={props.size || 50} {...props} />
  } else {
    return (
      <img
        onError={onError}
        style={{ width: props.size || 50, height: props.size || 50 }}
        {...props}
        src={props.src}
      />
    )
  }
}
