import React from 'react'
import { formatWeiToDisplayNumber } from '../../utils/formatBalance'
import { useSwapInfo } from '../../state/marketSwap/hooks/useSwapInfo'
import { LoadingIcon } from '../ui/Icons'

export const EstimateFee = ({
  estimateGasAmount
}: {
  estimateGasAmount: any
}) => {
  const { loadingGasAmount } = useSwapInfo()

  if (loadingGasAmount) {
    return <LoadingIcon />
  }

  return (
    <span>
       $
      {estimateGasAmount ? formatWeiToDisplayNumber(estimateGasAmount, 4) : 0}
    </span>
  )
}
