import {
  getAggregatorContract,
  getWbnbContract
} from '../../../utils/contractHelpers'
import { LARGE_VALUE } from '../../../utils/constant'
import { useWeb3React } from '../../customWeb3React/hook'
import { toast } from 'react-toastify'
import { BigNumber, ethers } from 'ethers'
import IERC20 from '../../../assets/abi/IERC20.json'
import {
  bn,
  messageAndViewOnBsc,
  parseCallStaticError
} from '../../../utils/helpers'
import config from '../../../config'
import { useWalletAllowance } from '../../wallet/hooks/useWalletAllowance'
import { getConfigAddress } from '../../../utils/addressHelper'
import { useSwapInfo } from './useSwapInfo'
import { generateAggregatorDataInput } from 'swap-tools-2'

const DECIMAL_SLIPPAGE = 10000
export const useMarketSwapAction = () => {
  const { account, provider } = useWeb3React()

  const { setAggregatorAllowance } = useWalletAllowance()
  const { slippage, timeout } = useSwapInfo()

  const checkSwapError = async ({
    inputAddress,
    outputAddress,
    amountIn,
    routes
  }: {
    inputAddress: string
    outputAddress: string
    amountIn: BigNumber
    routes: any
  }) => {
    try {
      const dataInput = generateAggregatorDataInput({
        tokenIn: inputAddress,
        tokenOut: outputAddress,
        amountIn: amountIn,
        sender: account,
        routes
      })
      const p = provider.getSigner()
      if (
        // swap bnb to wbnb (use wbnb.deposit)
        inputAddress === getConfigAddress('bnb') &&
        outputAddress === getConfigAddress('wbnb')
      ) {
        const contract = getWbnbContract(p)
        await contract.estimateGas.deposit({
          from: account,
          value: amountIn
        })
      } else if (
        // swap wbnb to bnb (use wbnb.withdraw)
        inputAddress === getConfigAddress('wbnb') &&
        outputAddress === getConfigAddress('bnb')
      ) {
        const contract = getWbnbContract(p)
        await contract.callStatic.withdraw(amountIn)
      } else {
        // normal pair => use aggregator.swap
        // deadline
        dataInput[0].deadline =
          Math.floor(new Date().getTime() / 1000) + timeout * 60
        const aggregator = getAggregatorContract(p)
        await aggregator.callStatic.aggregate(...dataInput)
      }

      return false
    } catch (e) {
      return parseCallStaticError(e) || 'Error'
    }
  }

  const swapHandle = async ({
    amountOut,
    inputAddress,
    outputAddress,
    amountIn,
    routes
  }: {
    amountOut: BigNumber
    inputAddress: string
    outputAddress: string
    amountIn: BigNumber
    routes: any
  }) => {
    try {
      const dataInput = generateAggregatorDataInput({
        tokenIn: inputAddress,
        tokenOut: outputAddress,
        amountIn: amountIn,
        sender: account,
        routes
      })

      const p = provider.getSigner()
      let res = null
      if (
        // swap bnb to wbnb (use wbnb.deposit)
        inputAddress === getConfigAddress('bnb') &&
        outputAddress === getConfigAddress('wbnb')
      ) {
        const contract = getWbnbContract(p)
        res = await contract.deposit({
          from: account,
          value: amountIn
        })
      } else if (
        // swap wbnb to bnb (use wbnb.withdraw)
        inputAddress === getConfigAddress('wbnb') &&
        outputAddress === getConfigAddress('bnb')
      ) {
        const contract = getWbnbContract(p)
        res = await contract.withdraw(amountIn)
      } else {
        // normal pair => use aggregator.swap
        // minAmountOut
        dataInput[0].amountOutMin = amountOut.sub(
          amountOut.mul(slippage * DECIMAL_SLIPPAGE).div(DECIMAL_SLIPPAGE * 100)
        )
        // deadline
        dataInput[0].deadline =
          Math.floor(new Date().getTime() / 1000) + timeout * 60
        const aggregator = getAggregatorContract(p)
        res = await aggregator.aggregate(...dataInput)
      }

      res?.wait(1)
      return res
    } catch (e) {
      const message = parseCallStaticError(e) || 'Error'
      toast.error(message)
      return false
    }
  }

  const approveAggregator = async ({
    tokenAddress
  }: {
    tokenAddress: string
  }) => {
    try {
      if (!provider) return BigNumber.from(0)
      const signer = provider.getSigner()
      const tokenContract = new ethers.Contract(tokenAddress, IERC20, signer)
      const txRes = await tokenContract.functions.approve(
        config.chain[56].addresses.aggregator,
        LARGE_VALUE
      )
      await txRes.wait(1)

      toast.success(
        messageAndViewOnBsc({
          title: 'Approve success',
          hash: txRes.hash
        })
      )
      setAggregatorAllowance(tokenAddress, bn(LARGE_VALUE))

      return ethers.BigNumber.from(LARGE_VALUE)
    } catch (e) {
      if (e.data && e.data.message) {
        toast.error(e.data.message)
      } else {
        toast.error(e.message || 'Error')
      }

      return ethers.BigNumber.from(0)
    }
  }

  return {
    swapHandle,
    checkSwapError,
    approveAggregator
  }
}
