// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'
export const tokens = createSlice({
  name: 'setting',
  initialState,
  reducers: {
    setBlockConfig: (
      state,
      action: PayloadAction<{
        headBlock?: number
        timePerBlock?: number
        loadedTimePerBlock?: boolean
      }>
    ) => {
      if (action.payload.headBlock) {
        state.headBlock = action.payload.headBlock
      }
      if (action.payload.timePerBlock) {
        state.timePerBlock = action.payload.timePerBlock
      }
      if (action.payload.loadedTimePerBlock) {
        state.loadedTimePerBlock = action.payload.loadedTimePerBlock
      }
    }
  }
})

// Actions
export const { setBlockConfig } = tokens.actions

export default tokens.reducer
