import React from 'react'
import './style.scss'
import { Button } from '../Button'

interface TabProps extends React.HTMLAttributes<HTMLDivElement> {
  tabs: any
  setTab: any
  tab: any
}

export const Tabs = (props: TabProps) => {
  const setTab = props.setTab
  const currentTab = props.tab
  const tabs = props.tabs
  return (
    <div className={`swap-tabs ${props.className}`}>
      {tabs.map((tab: any, key: number) => {
        return (
          <Button
            key={key}
            className={`swap-tabs__item ${
              currentTab === tab.value ? 'active' : ''
            }`}
            styletype={currentTab === tab.value ? '' : 'transparent'}
            onClick={() => setTab(tab.value)}
          >
            {tab.name}
          </Button>
        )
      })}
    </div>
  )
}
