import production from './production.json'
import development from './development.json'
import commonConfig from './config.json'
import { ethers } from 'ethers'

export type configType =
  | 'CHART_API_ENDPOINT'
  | 'SWAP_API_ENDPOINT'
  | 'REWARD_API_ENDPOINT'
  | 'TOKEN_INFO_ENDPOINT'
  | 'BLACK_LIST_TOKENS_ENDPOINT'
  | 'NO_FEE_TOKENS_ENDPOINT'

let env = 'production'
let config: any = {
  ...commonConfig,
  development,
  production
}

config.provider = new ethers.providers.JsonRpcProvider(
  config.chain[56].rpcUrlEzdefiPrivate
)

config.wssProvider = new ethers.providers.JsonRpcProvider(
  config.chain[56].rpcUrlEzdefiPrivate
)

export default config

export const setupConfig = (newEnv: string) => {
  env = newEnv
}

export const getConfig = (configKey: configType) => {
  return config[env][configKey] ?? config.production[configKey]
}
