import { store } from '../state'
import config, { getConfig } from '../config'
import { BigNumber, ethers } from 'ethers'
import IERC20 from '../assets/abi/IERC20.json'
import { getMarketPrice } from './commonService'
import { SERVICES, STABLECOIN_ADDRESSES } from '../utils/constant'
import { mul, weiToNumber } from '../utils/helpers'
import { toast } from 'react-toastify'
const { getPoolAddressHeximal } = require('bsc_util')
const bn = ethers.BigNumber.from

export const getTokenFromAddress = async (address: string) => {
  try {
    const provider = config.provider
    const tokenContract = new ethers.Contract(address, IERC20, provider)

    const promises = [
      tokenContract.functions.decimals().then((r: any) => r[0]),
      tokenContract.functions.symbol().then((r: any) => r[0]),
      tokenContract.functions.name().then((r: any) => r[0])
    ]
    const [decimal, symbol, name] = await Promise.all(promises)

    const icon = null

    return {
      decimal,
      symbol,
      name,
      address: ethers.utils.getAddress(address.toLowerCase()),
      icon
    }
  } catch (e) {
    console.error(e)
    return null
  }
}

export const getTokenDecimal = async (address: string) => {
  try {
    const provider = config.provider
    const tokenContract = new ethers.Contract(address, IERC20, provider)
    return await tokenContract.functions.decimals().then((r: any) => r[0])
  } catch (e) {
    console.error(e)
    return 18
  }
}

export const getTotalSupplyOfToken = async (tokenAddress: string) => {
  try {
    const provider = config.provider
    const tokenContract = new ethers.Contract(tokenAddress, IERC20, provider)
    const a = (await tokenContract.functions.totalSupply())[0]
    return a
  } catch (e) {
    console.error(e)
    toast.error('Total supply cannot be load, please try again later.')
    return BigNumber.from(0)
  }
}

export const getTopReservePoolByAddress = async (tokenAddress: string) => {
  try {
    const state = store.getState()
    const tokens = state.tokens.tokens
    let res: any = await fetch(
      getConfig('SWAP_API_ENDPOINT') + 'pool/top-token-reserve/' + tokenAddress
    ).then((r) => r.json())

    const token1Addresses = res.map((p: any) => {
      return p.address1 === tokenAddress ? p.address0 : p.address1
    })

    const promisses = []

    for (const i in token1Addresses) {
      if (!tokens[token1Addresses[i]]) {
        promisses.push(getTokenFromAddress(token1Addresses[i]))
      } else {
        promisses.push(tokens[token1Addresses[i]])
      }
    }

    // @ts-ignore
    const data = await Promise.all(promisses)

    res = res
      .filter((pool: any) => {
        const creator = pool.creator
        const supportedCreators = Object.values(SERVICES).map(
          (s: any) => s.factory
        )
        return supportedCreators.includes(creator)
      })
      .map((pool: any) => {
        const outputAddress =
          pool.address1 === tokenAddress ? pool.address0 : pool.address1
        const inputAddress =
          pool.address1 === tokenAddress ? pool.address1 : pool.address0
        // const quoteAddress = pool.address1 === tokenAddress ? pool.address0 : pool.address1
        const token0 = tokens[tokenAddress]
        const token1 = data.find((t: any) => t.address === outputAddress)

        return {
          ...pool,
          address0: inputAddress,
          address1: outputAddress,
          token1,
          token0,
          reserve0: weiToNumber(
            pool.address1 === tokenAddress ? pool.reserve1 : pool.reserve0
          ),
          reserve1: weiToNumber(
            pool.address1 === tokenAddress ? pool.reserve0 : pool.reserve1
          )
        }
      })

    return res
  } catch (e) {
    console.error(e)
    return []
  }
}

export const getListCustomToken = () => {
  let customTokens: any = localStorage.getItem('customTokens')
  customTokens = customTokens ? JSON.parse(customTokens) : []

  const results = {}
  for (const i in customTokens) {
    if (customTokens[i]) {
      // @ts-ignore
      results[customTokens[i].address] = customTokens[i]
    }
  }

  // default token
  results[config.chain[56].addresses.lz] = {
    name: 'LaunchZone',
    decimal: 18,
    symbol: 'LZ',
    icon: 'https://raw.githubusercontent.com/ezDeFi/ezdefi-media/master/launchzone-logo/lz-logo.png',
    address: config.chain[56].addresses.lz
  }
  results[config.chain[56].addresses.busd] = {
    name: 'BUSD BEP20',
    decimal: 18,
    symbol: 'BUSD',
    icon: 'https://s2.coinmarketcap.com/static/img/coins/64x64/4687.png',
    address: config.chain[56].addresses.busd
  }
  return results
}

export const searchTokens = async (keyword: string) => {
  try {
    const res = await fetch(
      getConfig('SWAP_API_ENDPOINT') + 'token?q=' + keyword
    ).then((r: any) => r.json())
    return {
      tokens: res,
      keyword
    }
  } catch (e) {
    console.log(e)
    return {
      tokens: [],
      keyword
    }
  }
}

export const getPriceByAddress = async (address: string, exchange: string) => {
  if (address === config.chain[56].addresses.busd) {
    return '1'
  }
  let price
  if (
    address === config.chain[56].addresses.bnb ||
    address === config.chain[56].addresses.wbnb
  ) {
    const res = await getReserveRate(
      config.chain[56].addresses.wbnb,
      config.chain[56].addresses.busd,
      exchange
    )
    price = res?.price || '600'
  } else {
    const [reserveRateWithUsd, reserveRateWithBnb] = await Promise.all([
      getReserveRate(address, config.chain[56].addresses.busd, exchange),
      getPriceThrowBnb(address, exchange)
    ])

    if (bn(reserveRateWithBnb.reserve).gt(reserveRateWithUsd.reserve)) {
      price = reserveRateWithBnb.price
    } else {
      price = reserveRateWithUsd.price
    }
  }
  const state = store.getState()
  if (
    state.currentPair.inputTokenAddress === address ||
    (state.currentPair.outputTokenAddress &&
      state.currentPair.outputTokenAddress === address &&
      STABLECOIN_ADDRESSES.includes(
        state.currentPair.inputTokenAddress.toLowerCase()
      ) &&
      !STABLECOIN_ADDRESSES.includes(
        state.currentPair.outputTokenAddress.toLowerCase()
      ))
  ) {
    return price
  } else {
    return null
  }
}

const getPriceThrowBnb = async (address: string, exchange: any) => {
  const keys: any = {
    bnb: {
      pairAddress: getPoolAddressHeximal(
        exchange,
        config.chain[56].addresses.wbnb,
        config.chain[56].addresses.busd
      ),
      inputAddress: config.chain[56].addresses.wbnb
    },
    inputWithBnb: {
      pairAddress: getPoolAddressHeximal(
        exchange,
        address,
        config.chain[56].addresses.wbnb
      ),
      inputAddress: address
    }
  }

  const res = await getMarketPrice({
    pairs: Object.values(keys)
  })
  if (res[keys.bnb?.pairAddress] && res[keys.inputWithBnb?.pairAddress]) {
    const reserveWithBnb = res[keys.inputWithBnb?.pairAddress].reserve
    return {
      price: mul(
        res[keys.inputWithBnb?.pairAddress].price,
        res[keys.bnb?.pairAddress].price
      ),
      reserve: reserveWithBnb
    }
  } else {
    return {
      price: '1',
      reserve: bn(0)
    }
  }
}

export const getReserveRate = async (
  address0: string,
  address1: string,
  exchange: any
) => {
  try {
    const pairAddress = getPoolAddressHeximal(exchange, address0, address1)
    const res = await getMarketPrice({
      pairs: [
        {
          pairAddress: pairAddress,
          inputAddress: address0
        }
      ]
    })
    if (!res[pairAddress]) {
      // eslint-disable-next-line no-throw-literal
      throw 'Do not have pair pairAddress'
    }
    return res[pairAddress]
  } catch (e) {
    console.error(e)
    return {
      price: '1',
      reserve: bn(0)
    }
  }
}
