import React, { useState } from 'react'
import './style.scss'
import { Table } from '../ui/Table'
import { useLiquidity } from './useLiquidity'
import { useMarketOrder } from './useMarketOrder'
import { useCurrentPair } from '../../state/currentPair/hook'
import { useWalletTxTable } from './useWalletTxTable'
import { useWeb3React } from '../../state/customWeb3React/hook'
import { SkeletonLoader } from './SkeletonLoader'

const TABS = {
  WALLET_TXS: 1,
  ORDER_HISTORY: 2,
  MARKET_ORDER: 3,
  INFO: 4
}

export default () => {
  const [tab, setTab] = useState<any>(TABS.MARKET_ORDER)

  const { currentPairAddress } = useCurrentPair()
  const { marketOrderColumns, loadingMarketOrder, marketOrders } =
    useMarketOrder()
  const { liquidityColumns, liquidityData } = useLiquidity()
  const { column, data } = useWalletTxTable()
  const { account } = useWeb3React()

  return (
    <div className='tx-history'>
      <div className='tabs'>
        <span
          className={`tabs__item ${tab === TABS.MARKET_ORDER ? 'active' : ''}`}
          onClick={() => setTab(TABS.MARKET_ORDER)}
        >
          Market Orders
        </span>
        {account && (
          <span
            className={`tabs__item ${tab === TABS.WALLET_TXS ? 'active' : ''}`}
            onClick={() => setTab(TABS.WALLET_TXS)}
          >
            Wallet Orders
          </span>
        )}

        <span
          className={`tabs__item ${tab === TABS.INFO ? 'active' : ''}`}
          onClick={() => setTab(TABS.INFO)}
        >
          Liquidity
        </span>
      </div>
      <div className='tabs__content'>
        <div className={tab !== TABS.MARKET_ORDER ? 'hidden' : ''}>
          {currentPairAddress &&
          marketOrders.length === 0 &&
          loadingMarketOrder ? (
            <SkeletonLoader />
          ) : (
            <Table
              columns={marketOrderColumns}
              data={marketOrders
                .filter((o: any) => {
                  return o.pairAddress === currentPairAddress
                })
                .sort((a: any, b: any) => b.time - a.time)}
            />
          )}
        </div>
        <Table
          columns={liquidityColumns}
          data={liquidityData}
          className={tab !== TABS.INFO ? 'hidden' : ''}
        />
        {account && (
          <Table
            columns={column}
            data={data.sort((a: any, b: any) => b.time - a.time)}
            className={tab !== TABS.WALLET_TXS ? 'hidden' : ''}
          />
        )}
      </div>
    </div>
  )
}
