import { EmptyStarIcon, StarIcon } from '../Icons'
import {
  formatFloat,
  shortenAddressString,
  tokenNameToDisplay,
  weiToNumber
} from '../../../utils/helpers'
import React from 'react'
import { useWalletBalance } from '../../../state/wallet/hook'
import { TokenIcon } from '../Image'

export const TokenOption = ({
  isWide = false,
  token,
  onSelect,
  inBookmark,
  onClickBookmark
}: {
  isWide?: boolean
  inBookmark: boolean
  token: any
  onSelect: any
  onClickBookmark?: any
}) => {
  const { balances } = useWalletBalance()
  const onClickHandle = async (e: any) => {
    if (e.target.className === 'bookmark-btn') {
      return
    }
    onSelect(token)
  }

  const BookmarkIcon = inBookmark ? StarIcon : EmptyStarIcon

  return (
    <div
      className={`select-token__option ${isWide ? 'wide' : ''}`}
      onClick={onClickHandle}
    >
      <span className='left'>
        {onClickBookmark ? (
          <BookmarkIcon
            className='bookmark-btn'
            onClick={() => {
              onClickBookmark(token.address)
            }}
          />
        ) : (
          ''
        )}

        <div className='name-and-icon'>
          <TokenIcon
            src={
              token && token.icon
                ? token.icon
                : `https://farm.army/bsc/token/${token?.address.toLowerCase()}.webp`
            }
            className='token-icon'
            size={20}
            alt={token.symbol}
          />

          <div className={`custom-token-name ${isWide ? 'wide' : ''}`}>
            <span className='token-name'>
              {token.icon ? (
                token.symbol
              ) : (
                <span>
                  {token.symbol}{' '}
                  {!token.icon ? `(${tokenNameToDisplay(token.name)})` : ''}
                </span>
              )}
            </span>
            <span className='search-token__item--address'>
              {shortenAddressString(token.address)}
            </span>
          </div>
        </div>
      </span>
      <span className='acc-balance'>
        {balances && balances[token.address]
          ? formatFloat(
              weiToNumber(balances[token.address], token.decimal || 18),
              4
            )
          : 0}
      </span>
    </div>
  )
}
