import { useDispatch, useSelector } from 'react-redux'
import {
  setExchange,
  setInputAddressToken,
  setPair,
  setOutputAddressToken
} from './reducer'
import { detectOutputAddress } from '../../service/commonService'
import { useCustomToken } from '../token/hook'
import { useEffect, useState } from 'react'
import { convertBNBAddressToWBNB } from '../../utils/helpers'
import { generatePath, useHistory } from 'react-router-dom'
import swapConfig from '../../configs'
import { CHAIN_ID_TO_NAME } from '../../utils/constant'
import { useWeb3React } from '../customWeb3React/hook'

const { getPoolAddressHeximal } = require('bsc_util')

export const useCurrentPair = () => {
  const {
    tokens,
    outputTokenAddress,
    inputTokenAddress,
    exchange,
    findingOutputToken
  } = useSelector((state: any) => {
    return {
      tokens: state.tokens.tokens,
      outputTokenAddress: state.currentPair.outputTokenAddress,
      inputTokenAddress: state.currentPair.inputTokenAddress,
      exchange: state.currentPair.exchange,
      findingOutputToken: state.currentPair.findingOutputToken
    }
  })
  const { chainId } = useWeb3React()

  const { addCustomTokenByToken } = useCustomToken()
  const history = useHistory()
  const dispatch = useDispatch()
  const [currentPairAddress, setCurrentPairAddress] = useState<string>('')

  useEffect(() => {
    try {
      if (exchange && inputTokenAddress && outputTokenAddress) {
        const pairAddress: string = getPoolAddressHeximal(
          exchange,
          convertBNBAddressToWBNB(inputTokenAddress),
          convertBNBAddressToWBNB(outputTokenAddress)
        )
        setCurrentPairAddress(pairAddress)
      }
    } catch (e) {}
  }, [exchange, inputTokenAddress, outputTokenAddress])

  /**
   * set output token address (add custom token if 'token' is not exist in store.tokens.tokens)
   * @param token
   */
  const setOutputToken = (token: any) => {
    updateUrl({ inputAddress: inputTokenAddress, outputAddress: token.address })

    if (tokens[token.address]) {
      if (token.address === inputTokenAddress) {
        dispatch(
          setPair({
            inputAddress: outputTokenAddress,
            outputAddress: token.address
          })
        )
      } else {
        dispatch(setOutputAddressToken({ outputAddress: token.address }))
      }
    } else {
      addCustomTokenByToken(token)
      dispatch(setOutputAddressToken({ outputAddress: token.address }))
    }
  }

  /**
   * set input token address (add custom token if 'token' is not exist in store.tokens.tokens)
   * @param token
   */
  const setInputToken = (token: any) => {
    updateUrl({
      inputAddress: token.address,
      outputAddress: outputTokenAddress
    })
    if (tokens[token.address]) {
      if (token.address === outputTokenAddress) {
        dispatch(
          setPair({
            inputAddress: token.address,
            outputAddress: inputTokenAddress
          })
        )
      } else {
        dispatch(setInputAddressToken({ inputAddress: token.address }))
      }
    } else {
      console.log(token)
      addCustomTokenByToken(token)
      dispatch(setInputAddressToken({ inputAddress: token.address }))
    }
  }

  /**
   * set input address and find output address by call api to get Top Pools
   * and redirect to pool have bigest liquidity
   * @param token
   */
  const setPairByInputToken = async (token: any) => {
    updateUrl({
      inputAddress: token.address,
      outputAddress: outputTokenAddress
    })
    token = { ...token, decimal: 18 }
    if (!tokens[token.address]) {
      addCustomTokenByToken(token)
    }
    dispatch(setPair({ findingOutputToken: true, inputAddress: token.address }))
    const res = await detectOutputAddress(token.address)
    updateUrl({
      inputAddress: token.address,
      outputAddress: res?.outputAddress || outputTokenAddress
    })
    if (res) {
      dispatch(
        setPair({
          outputAddress: res.outputAddress,
          exchange: res.exchange,
          findingOutputToken: false
        })
      )
    }
  }

  /**
   * set input address
   * @param address
   */
  const setInputAddress = (address: string) => {
    updateUrl({ inputAddress: address, outputAddress: outputTokenAddress })
    dispatch(setInputAddressToken({ inputAddress: address }))
  }

  /**
   * set output address
   * @param address
   */
  const setOutputAddress = (address: string) => {
    updateUrl({ inputAddress: inputTokenAddress, outputAddress: address })

    dispatch(setOutputAddressToken({ outputAddress: address }))
  }

  /**
   * set exchange
   * @param exchange
   */
  const setCurrentExchange = (exchange: string) => {
    dispatch(setExchange({ exchange }))
  }

  /**
   * generate new url by input and output address and push to history
   * @param inputAddress
   * @param outputAddress
   */
  const updateUrl = ({
    inputAddress,
    outputAddress
  }: {
    inputAddress?: string | null
    outputAddress?: string | null
  }) => {
    const url = generatePath(swapConfig.path, {
      chain: CHAIN_ID_TO_NAME[chainId] || undefined,
      token: inputAddress ? 'token' : undefined,
      inputTokenAddress: inputAddress || undefined,
      outputTokenAddress: outputAddress ? outputAddress : undefined
    })

    history.push(url + window.location.search)
  }

  /**
   * detect the pool what have biggest liquidity
   * and set outputTokenAddress is outputTokenAddress in that pool
   * @param inputTokenAddr
   */
  const redirectToTopPool = async (inputTokenAddr: string) => {
    // dispatch(setInputAddressToken({ inputAddress: inputTokenAddr }))
    dispatch(
      setPair({
        inputAddress: inputTokenAddr,
        findingOutputToken: true
      })
    )
    const res = await detectOutputAddress(inputTokenAddr)
    if (res) {
      dispatch(
        setPair({
          outputAddress: res.outputAddress,
          exchange: res.exchange,
          findingOutputToken: false
        })
      )
    }
  }

  /**
   * set current pair and update url
   *
   * @param inputAddress
   * @param outputAddress
   * @param exchange
   * @param pushToUrl
   */
  const setCurrentPair = ({
    inputAddress,
    outputAddress,
    exchange,
    pushToUrl = true
  }: {
    inputAddress: string
    outputAddress?: string
    exchange?: string
    pushToUrl?: boolean
  }) => {
    if (pushToUrl) {
      updateUrl({ inputAddress, outputAddress })
    }
    dispatch(setPair({ inputAddress, outputAddress, exchange }))
  }

  return {
    updateUrl,
    setCurrentPair,
    setInputToken,
    setOutputToken,
    setPairByInputToken,
    outputTokenAddress,
    inputTokenAddress,
    exchange,
    setInputAddress,
    setOutputAddress,
    setCurrentExchange,
    redirectToTopPool,
    currentPairAddress,
    inputToken: tokens[inputTokenAddress],
    outputToken: tokens[outputTokenAddress],
    findingOutputToken
  }
}
