import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { add, decodePath } from '../utils/helpers'
import { setSingleStateData } from '../store/actions/initSettingData'
import {
  DEFAULT_INPUT_TOKEN_ADDRESS,
  DEFAULT_OUTPUT_TOKEN_ADDRESS,
  STABLECOIN_SYMBOLS
} from '../utils/constant'
import { getTopReservePoolByAddress } from '../service/tokenService'
import { ethers } from 'ethers'
import { useCustomToken, useListTokens } from '../state/token/hook'
import { useCurrentPair } from '../state/currentPair/hook'
import { useBalanceTracker, useWalletBalance } from '../state/wallet/hook'
import { useWalletTx } from '../state/wallet/hooks/useWalletTx'
import { useWeb3React as useCustomWeb3React } from '../state/customWeb3React/hook'
import config from '../config'
import { useTokenPrice } from '../state/token/hooks/useTokenPrice'
import { useSwapInfo } from '../state/marketSwap/hooks/useSwapInfo'
import { getConfigAddress } from '../utils/addressHelper'
import { toast } from 'react-toastify'
import { useSetting } from '../state/setting/hook'
import historyProvider from '../service/datafeed/historyProvider'
import { useChartInfo } from '../state/chart/hook'

export const useFetchCommonData = ({
  useWeb3React,
  useSubPage,
  language,
  theme,
  xStorageClient
}: {
  useWeb3React: any
  useSubPage: any
  language: string
  theme: string
  xStorageClient: any
}) => {
  const web3React = useWeb3React()
  const { routerArr } = useChartInfo()
  const { account, chainId } = useCustomWeb3React()
  const { loadedTimePerBlock } = useSetting()
  const {
    exchange,
    inputTokenAddress,
    outputTokenAddress,
    setCurrentPair,
    updateUrl,
    redirectToTopPool,
    findingOutputToken
  } = useCurrentPair()
  const { tokens, initWhitelistToken, initBlackListToken, initNoFeeToken } =
    useListTokens()
  const { fetchTokenPrice } = useTokenPrice()

  const {
    fetchWalletTxs,
    getTxsInCurrentPair,
    walletTxsFromAmm,
    walletTxsFromSwapX
  } = useWalletTx()
  const { fetchBalanceOfAllToken } = useWalletBalance()
  const { addCustomToken } = useCustomToken()
  useBalanceTracker()
  const dispatch = useDispatch()
  const history = useHistory()
  const path = useSubPage()
  const [initPath, setInitPath] = useState<boolean>(false)
  const { fetchRoutes } = useSwapInfo()

  useEffect(() => {
    const balancesCached = localStorage.getItem('cache_balances') || '{}'
    setSingleStateData('balances', JSON.parse(balancesCached))(dispatch)
    setSingleStateData('routerHistory', history)(dispatch)
    setSingleStateData('currentPath', path)(dispatch)
    setSingleStateData('language', language || 'en')(dispatch)
    initWhitelistToken()
    initBlackListToken()
    initNoFeeToken()
  }, [])

  /**
   * detect input and output token when page is loaded
   */
  useEffect(() => {
    if (initPath || !history) {
      return
    }
    const { inputTokenAddr, outputTokenAddr } = decodePath(path)
    if (inputTokenAddr && !outputTokenAddr) {
      console.log(123)
      redirectToTopPool(inputTokenAddr)
    }
    setCurrentPair({
      inputAddress: inputTokenAddr || DEFAULT_INPUT_TOKEN_ADDRESS,
      outputAddress: outputTokenAddr || DEFAULT_OUTPUT_TOKEN_ADDRESS,
      pushToUrl: false
    })
    setInitPath(true)
  }, [path])

  /**
   * update url when chain be changed
   */
  useEffect(() => {
    const { inputTokenAddr, outputTokenAddr } = decodePath(path)
    updateUrl({
      inputAddress: inputTokenAddr,
      outputAddress: outputTokenAddr
    })
  }, [chainId, path])

  /**
   * fetch top liquidity of input token
   */
  useEffect(() => {
    try {
      if (
        findingOutputToken === false &&
        tokens[inputTokenAddress] &&
        tokens[inputTokenAddress]
      ) {
        fetchTopReservePools()
        fetchRoutes(inputTokenAddress, outputTokenAddress)
      }
    } catch (e) {
      console.error(e)
      toast.error('Routes cannot be load, please try again later.')
    }
  }, [
    findingOutputToken,
    tokens[inputTokenAddress],
    tokens[outputTokenAddress]
  ])

  /**
   * fetch input and output price (usd)
   */
  useEffect(() => {
    if (
      !findingOutputToken &&
      tokens[inputTokenAddress] &&
      tokens[outputTokenAddress]
    ) {
      fetchTokenPrice(inputTokenAddress)
      fetchTokenPrice(outputTokenAddress)
      fetchTokenPrice(outputTokenAddress, getConfigAddress('wbnb'))
    }
  }, [
    findingOutputToken,
    tokens[inputTokenAddress],
    tokens[outputTokenAddress]
  ])

  useEffect(() => {
    fetchTokenPrice(config.chain[56].addresses.wbnb)
    const interval = setInterval(() => {
      fetchTokenPrice(config.chain[56].addresses.wbnb)
    }, 60000)

    return () => clearInterval(interval)
  }, [])

  /**
   * fetch all orders of current account
   */
  useEffect(() => {
    if (account && loadedTimePerBlock) {
      fetchWalletTxs(account)
    }
  }, [account, loadedTimePerBlock])

  /**
   * get wallet orders of current pair
   */
  useEffect(() => {
    if (exchange && inputTokenAddress && outputTokenAddress) {
      getTxsInCurrentPair()
    }
  }, [
    walletTxsFromAmm,
    walletTxsFromSwapX,
    exchange,
    inputTokenAddress,
    outputTokenAddress
  ])

  /**
   * fetch wallet balance
   */
  useEffect(() => {
    const fetch = async () => {
      try {
        await fetchBalanceOfAllToken(account)
      } catch (e) {
        toast.error('Balance cannot be load, please try again later.')
      }
    }
    fetch()
  }, [tokens, account])

  /**
   * move web3react to store
   */
  useEffect(() => {
    if (web3React) {
      for (const i in web3React) {
        const key = i === 'library' ? 'provider' : i
        setSingleStateData(key, web3React[i])(dispatch)
      }
    }
  }, [web3React])

  /**
   * auto add custom token
   */
  useEffect(() => {
    if (
      inputTokenAddress &&
      Object.values(tokens).length > 0 &&
      !tokens[inputTokenAddress]
    ) {
      addCustomToken(inputTokenAddress)
    }
    if (
      outputTokenAddress &&
      Object.values(tokens).length > 0 &&
      !tokens[outputTokenAddress]
    ) {
      addCustomToken(outputTokenAddress)
    }
  }, [tokens, inputTokenAddress, outputTokenAddress])

  useEffect(() => {
    setSingleStateData('theme', theme || 'light')(dispatch)
  }, [theme])

  useEffect(() => {
    if (routerArr && routerArr.length > 0 && loadedTimePerBlock) {
      fetchLastDayCandle()
    }
  }, [routerArr, loadedTimePerBlock])

  /**
   * get referralAddress from xStorageClient and set to store
   */
  useEffect(() => {
    const fetchReferralAddress = async () => {
      try {
        if (xStorageClient) {
          const referralAddress = await xStorageClient
            .get('LZ_REFERRAL')
            .then((address: string) =>
              address ? ethers.utils.getAddress(address) : undefined
            )
            .catch(console.error)

          setSingleStateData('referralAddress', referralAddress)(dispatch)
        }
      } catch (e) {
        console.error(e)
        toast.error('Referral address cannot be load, please try again later.')
      }
    }

    fetchReferralAddress()
  }, [xStorageClient])

  const fetchTopReservePools = async () => {
    setSingleStateData('topPools', [])(dispatch)
    let address =
      STABLECOIN_SYMBOLS.includes(tokens[inputTokenAddress]?.symbol) &&
      !STABLECOIN_SYMBOLS.includes(tokens[outputTokenAddress]?.symbol)
        ? outputTokenAddress
        : inputTokenAddress

    if (address === config.chain[56].addresses.bnb) {
      address = config.chain[56].addresses.wbnb
    }

    let topPoolsState: any, topPools: any
    if (address === inputTokenAddress) {
      topPoolsState = await getTopReservePoolByAddress(address)
      topPools = topPoolsState
    } else {
      ;[topPoolsState, topPools] = await Promise.all([
        getTopReservePoolByAddress(address),
        getTopReservePoolByAddress(inputTokenAddress)
      ])
    }

    setSingleStateData('topPools', topPools)(dispatch)
    setSingleStateData('topPoolsToDisplay', topPoolsState)(dispatch)
  }

  const fetchLastDayCandle = async () => {
    const toTime = Math.floor(new Date().getTime() / 1000)
    const fromTime = Math.floor(new Date().getTime() / 1000) - 86400000
    historyProvider
      .getBars({
        currencyCode: 'USD',
        route: routerArr.join('/'),
        resolution: '60',
        limit: 24,
        to: toTime
      })
      .then((bars: any) => {
        const volume = bars
          .filter(
            (b: any) =>
              Math.floor(b.time / 1000) > fromTime &&
              Math.floor(b.time / 1000) < toTime
          )
          .map((b: any) => b.volume)
          .reduce((partialSum: any, a: any) => add(partialSum, a), 0)
        const open = bars[0].open
        const close = bars[bars.length - 1].close

        if (bars.length) {
          setSingleStateData('lastDayCandle', {
            volume: volume.split('.')[0],
            open,
            close
          })(dispatch)
        }
      })
      .catch((err: any) => {
        console.error(err)
      })
  }
}
