// eslint-disable-next-line no-unused-vars
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { initialState } from './type'

export const tokens = createSlice({
  name: 'currentPair',
  initialState,
  reducers: {
    setFindingOutputToken: (
      state,
      action: PayloadAction<{
        findingOutputToken: boolean
      }>
    ) => {
      state.findingOutputToken = action.payload.findingOutputToken
    },
    setPair: (
      state,
      action: PayloadAction<{
        inputAddress?: string
        outputAddress?: string
        exchange?: string
        findingOutputToken?: boolean
      }>
    ) => {
      if (action.payload.findingOutputToken !== undefined) {
        state.findingOutputToken = action.payload.findingOutputToken
      }
      if (action.payload.inputAddress) {
        state.inputTokenAddress = action.payload.inputAddress
      }
      if (action.payload.outputAddress) {
        state.outputTokenAddress = action.payload.outputAddress
      }
      if (action.payload.exchange) {
        state.exchange = action.payload.exchange
      }
    },
    setInputAddressToken: (
      state,
      action: PayloadAction<{ inputAddress: string }>
    ) => {
      state.inputTokenAddress = action.payload.inputAddress
    },
    setOutputAddressToken: (
      state,
      action: PayloadAction<{ outputAddress: string }>
    ) => {
      state.outputTokenAddress = action.payload.outputAddress
    },
    setExchange: (state, action: PayloadAction<{ exchange: string }>) => {
      state.exchange = action.payload.exchange
    },
    setCurrentPairAddress: (
      state,
      action: PayloadAction<{ address: string }>
    ) => {
      state.currentPairAddress = action.payload.address
    }
  }
})

// Actions
export const {
  setFindingOutputToken,
  setPair,
  setInputAddressToken,
  setOutputAddressToken,
  setExchange,
  setCurrentPairAddress
} = tokens.actions

export default tokens.reducer
