import React from 'react'
import './style.scss'
import { LaunchZoneLogo } from '../Icons'

interface TableProps extends React.HTMLAttributes<HTMLTableElement> {
  columns: ColumnInterface[]
  data: any[]
}

interface ColumnInterface {
  name: string
  title: string
  render?: any
  className?: string
}

export const Table = ({ columns, data, className }: TableProps) => {
  if (!data || data.length === 0) {
    return (
      <div className={'swap-table__nodata ' + className}>
        <LaunchZoneLogo />
        <div className='mt-2'>No data available</div>
      </div>
    )
  }

  return (
    <table className={'swap-table ' + className}>
      <thead>
        <tr>
          {columns.map((column, key) => {
            return (
              <th key={key} className={column.className || ''}>
                {column.title}
              </th>
            )
          })}
        </tr>
      </thead>
      <tbody>
        {data.map((rowData: any, key) => {
          return (
            <tr key={key}>
              {columns.map(
                (
                  column: { name: string; render?: any; className?: string },
                  key: number
                ) => {
                  if (column.render) {
                    return (
                      <td key={key} className={column.className || ''}>
                        {column.render(rowData[column.name], rowData)}
                      </td>
                    )
                  }
                  return (
                    <td key={key} className={column.className || ''}>
                      {rowData[column.name]}
                    </td>
                  )
                }
              )}
            </tr>
          )
        })}
      </tbody>
    </table>
  )
}
