import { useDispatch, useSelector } from 'react-redux'
import { State } from '../../types'
import {
  bn,
  convertBNBAddressToWBNB,
  numberToWei
} from '../../../utils/helpers'
import {
  getAggregatorContract,
  getWbnbContract
} from '../../../utils/contractHelpers'
import { useWeb3React } from '../../customWeb3React/hook'
import { useTokenPrice } from '../../token/hooks/useTokenPrice'
import { BigNumber } from 'ethers'
import { getConfigAddress } from '../../../utils/addressHelper'
import { useWalletBalance } from '../../wallet/hook'
import { useWalletAllowance } from '../../wallet/hooks/useWalletAllowance'
import { useSwapInfo } from './useSwapInfo'
import {
  getPoolsFromSwapRouteData,
  getAmountOutByRoutes,
  getAmountOut,
  generateAggregatorDataInput
} from 'swap-tools-2'
import { setRoutesCap } from '../reducer'
import _ from 'lodash'

export const useEstimateSwapOutput = () => {
  const { balances } = useWalletBalance()
  const { aggregatorAllowance } = useWalletAllowance()
  const { routes: routesState, setGasAmountToSwap } = useSwapInfo()
  // @ts-ignore
  const { amountOut, gasAmount, getAmountOutCallback } = useSelector(
    (state: State) => {
      return {
        amountOut: state.marketSwap.amountOut,
        gasAmount: state.marketSwap.gasAmount,
        getAmountOutCallback: state.marketSwap.getAmountOutCallback
      }
    }
  )

  const dispatch = useDispatch()

  const { tokenPrices } = useTokenPrice()
  const { account } = useWeb3React()

  /**
   * find best route to swap
   */
  const calculateMarketOutput = ({
    amountIn,
    inputAddress,
    outputAddress,
    callback
  }: {
    amountIn: BigNumber
    inputAddress: string
    outputAddress: string
    callback: (data: any) => void
  }) => {
    if (!amountIn || !inputAddress || !outputAddress) return

    if (
      inputAddress === getConfigAddress('bnb') &&
      outputAddress === getConfigAddress('wbnb')
    ) {
      estimateBnbToWbnbGas(amountIn)

      callback({
        amountOut: amountIn
      })
    }

    if (
      inputAddress === getConfigAddress('wbnb') &&
      outputAddress === getConfigAddress('bnb')
    ) {
      estimateWbnbToBnbGas(amountIn)

      callback({
        amountOut: amountIn
      })
    }

    getAmountOutAndGasEachRoute(
      amountIn,
      inputAddress,
      outputAddress,
      (data: any) => {
        callback(data)
        if (data.isFinished) {
          if (data.gasUsed) {
            setGasAmountToSwap(data.gasUsed)
          } else {
            estimateGas(data.dataInput)
          }
        }
      }
    )
  }

  const estimateWbnbToBnbGas = async (amount: BigNumber) => {
    try {
      const aggregator = getWbnbContract()
      const gasLimit = await aggregator.estimateGas.widrawal(amount)
      setGasAmountToSwap(gasLimit)

      return gasLimit
    } catch (e) {
      console.error(e)
      return bn(50000)
    }
  }

  const estimateBnbToWbnbGas = async (amount: BigNumber) => {
    try {
      const aggregator = getWbnbContract()
      const gasLimit = await aggregator.estimateGas.deposit({
        from: account,
        value: amount
      })
      setGasAmountToSwap(gasLimit)

      return gasLimit
    } catch (e) {
      console.error(e)
      return bn(50000)
    }
  }

  const estimateGas = async (dataInput: any) => {
    try {
      const aggregator = getAggregatorContract()
      let gasLimit = bn(250000)
      if (dataInput) {
        gasLimit = await aggregator.estimateGas.aggregate(...dataInput)
      }
      setGasAmountToSwap(gasLimit)

      return gasLimit
    } catch (e) {
      // console.error(e)
      setGasAmountToSwap(bn(250000))
      return bn(250000)
    }
  }

  /**
   * find amount out and gas limit of each route
   */
  const getAmountOutAndGasEachRoute = async (
    amountIn: BigNumber,
    inputAddress: string,
    outputAddress: string,
    // @ts-ignore
    callback: (data: any) => void
  ) => {
    const routes = _.cloneDeep(routesState.value)
    //@ts-ignore
    const baseFee = getBaseFee(outputAddress)

    const configs = {
      baseFee,
      stepFee: baseFee,
      routeCapLength: 4,
      populationSize: 250,
      generations: 30,
      skip: 2,
      extendLastBestSolution: true
    }

    getAmountOut({
      tokenIn: inputAddress,
      tokenOut: outputAddress,
      amountIn: _.cloneDeep(amountIn),
      routes,
      configs,
      sender: isUseSimulate(amountIn, inputAddress) ? null : account,
      notification: (candidate: any) => {
        dispatch(setRoutesCap({ routes: candidate.routesCap }))
        callback({
          amountIn,
          dists: candidate.dists,
          routes: candidate.routes,
          amountOut: candidate.amountOut,
          dataInput: candidate.aggregatorDataInput,
          gasUsed: candidate.gasUsed,
          isFinished: candidate.isFinished
        })
      }
    })
  }

  const getBaseFee = (outputAddress: string) => {
    const outputTokenPriceInBnb =
      tokenPrices[outputAddress + '/' + getConfigAddress('wbnb')]
    return convertBNBAddressToWBNB(outputAddress) === getConfigAddress('wbnb')
      ? bn(numberToWei('0.0005'))
      : bn(numberToWei('0.0005'))
          .mul(
            numberToWei(outputTokenPriceInBnb ? outputTokenPriceInBnb : '400')
          )
          .div(numberToWei(1))
  }

  const simulateOutput = async ({
    amountIn,
    routes
  }: {
    amountIn: BigNumber
    routes: any
  }) => {
    try {
      let swapRouteData = routes.map((r: any) => ({
        liquidity: r.liquidity,
        route: r.steps,
        dists: r.dists
      }))
      const pools = await getPoolsFromSwapRouteData({ swapRouteData })

      const res: { steps: any; amountOut: BigNumber }[] = await Promise.all(
        swapRouteData.map(async (route: any) => {
          const res = getAmountOutByRoutes({
            amountIn,
            pools,
            routes: route
          })

          return {
            dists: route.dists,
            steps: route.route,
            amountOut: res.amountOut
          }
        })
      )
      return res
    } catch (e) {
      console.error(e)
      return []
    }
  }

  const isUseSimulate = (amountIn: BigNumber, inputAddress: string) => {
    return (
      !account ||
      amountIn.gt(balances[inputAddress] || 0) ||
      !aggregatorAllowance[inputAddress] ||
      (inputAddress !== getConfigAddress('bnb') &&
        aggregatorAllowance[inputAddress] &&
        aggregatorAllowance[inputAddress].lt(amountIn))
    )
  }

  const getAmountOutByAggregator = async ({
    tokenIn,
    tokenOut,
    routes,
    amountIn
  }: {
    tokenIn: string
    tokenOut: string
    routes: any
    amountIn: BigNumber
  }) => {
    try {
      const gasLimit = bn('20000000')

      const params = generateAggregatorDataInput({
        tokenIn,
        tokenOut,
        amountIn,
        sender: account,
        routes
      })
      params[4].gasLimit = gasLimit

      const contract = getAggregatorContract()
      const [amountOut, gasLeft] = await contract.callStatic.aggregate(
        ...params
      )
      const gasUsed = gasLimit.sub(gasLeft)
      setGasAmountToSwap(gasUsed)
      return {
        amountOut
      }
    } catch (e) {
      console.error('estimate fail', e)
      return {
        amountOut: bn(0)
      }
    }
  }

  return {
    simulateOutput,
    amountOut,
    calculateMarketOutput,
    getAmountOutByAggregator,
    gasAmount
  }
}
