import { useDispatch, useSelector } from 'react-redux'
import { State } from '../../types'
import config from '../../../config'
import { convertBNBAddressToWBNB } from '../../../utils/helpers'
import { addTokenPrice, addTokenRate } from '../reducer'
import { getConfigAddress } from '../../../utils/addressHelper'
import { toast } from 'react-toastify'
import { getTokenRate } from 'swap-tools-2'
import { useListTokens } from '../hook'

export const useTokenPrice = () => {
  const { tokens } = useListTokens()
  const { tokenPrices } = useSelector((state: State) => {
    return {
      tokenPrices: state.tokens.tokenPrices
    }
  })

  const dispatch = useDispatch()

  /**
   * 1. get route from '/swap/route' to get best liquidity route
   * 2. calculator price in USD from that route
   * @param address
   * @param outputAddress
   */
  const fetchTokenPrice = async (address: string, outputAddress?: string) => {
    const addressToGetPrice = convertBNBAddressToWBNB(address)
    let outputAddressToGetPrice = outputAddress
      ? outputAddress
      : config.chain[56].addresses.busd
    outputAddressToGetPrice = convertBNBAddressToWBNB(outputAddressToGetPrice)
    if (
      address === outputAddressToGetPrice ||
      addressToGetPrice === outputAddressToGetPrice
    ) {
      dispatch(addTokenPrice({ tokenAddress: address, price: '1' }))
      return '1'
    }

    try {
      // const res = await fetch(
      //   `${getConfig(
      //     'SWAP_API_ENDPOINT'
      //   )}route/${addressToGetPrice}/${outputAddressToGetPrice}`
      // ).then((r) => r.json())
      // const route = res[0].route
      //
      // let price = '1'
      // for (let i = 0; i < route.length; i++) {
      //   const { ri: reserveInput, ro: reserveOutput } = route[i]
      //   price = mul(price, div(reserveOutput, reserveInput))
      // }

      const price = await getTokenRate(
        {
          address: addressToGetPrice,
          decimals: tokens[addressToGetPrice]?.decimal || 18
        },
        {
          address: outputAddressToGetPrice,
          decimals: tokens[outputAddressToGetPrice]?.decimal || 18
        }
      )

      if (outputAddressToGetPrice !== getConfigAddress('busd')) {
        dispatch(
          addTokenRate({
            price,
            outputAddress: outputAddressToGetPrice,
            tokenAddress: addressToGetPrice
          })
        )
      } else if (
        // if address = bnb or wbnb => store price of bnb and wbnb = price
        address === config.chain[56].addresses.bnb ||
        address === config.chain[56].addresses.wbnb
      ) {
        dispatch(
          addTokenPrice({
            tokenAddress: config.chain[56].addresses.wbnb,
            price
          })
        )
        dispatch(
          addTokenPrice({ tokenAddress: config.chain[56].addresses.bnb, price })
        )
      } else {
        dispatch(addTokenPrice({ tokenAddress: address, price }))
      }

      return price
    } catch (e) {
      console.error(e)
      toast.error('Price cannot be load, please try again later.')
      return '1'
    }
  }

  return {
    tokenPrices,
    fetchTokenPrice
  }
}
