import { addBookMarkAddressReducer, addRecentToken, addToken } from '../reducer'
import { useDispatch, useSelector } from 'react-redux'
import { getTokenDecimal } from '../../../service/tokenService'

export const useBookmark = () => {
  const { bookmarkAddresses, tokens } = useSelector((state: any) => {
    return {
      bookmarkAddresses: state.tokens.bookmarkAddresses,
      tokens: state.tokens.tokens
    }
  })

  const dispatch = useDispatch()
  const addTokenToBookmark = async (token: any) => {
    if (tokens[token.address]) {
      dispatch(addBookMarkAddressReducer({ address: token.address }))
    } else {
      const customToken = {
        ...token,
        decimal: 18
      }
      dispatch(addToken({ token: customToken }))
      dispatch(addBookMarkAddressReducer({ address: customToken.address }))

      const decimal = await getTokenDecimal(token.address)
      if (decimal !== 18) {
        customToken.decimal = decimal
        dispatch(addToken({ token: customToken }))
      }

      let oldCustomTokens: any = localStorage.getItem('customTokens')
      oldCustomTokens = oldCustomTokens ? JSON.parse(oldCustomTokens) : []
      localStorage.setItem(
        'customTokens',
        JSON.stringify([...oldCustomTokens, customToken])
      )
    }
  }

  const addBookmarkAddress = (address: string) => {
    dispatch(addBookMarkAddressReducer({ address }))
  }

  return {
    addTokenToBookmark,
    bookmarkAddresses,
    addBookmarkAddress
  }
}

export const useRecentToken = () => {
  const { recentTokens } = useSelector((state: any) => {
    return {
      recentTokens: state.tokens.recentTokens,
      tokens: state.tokens.tokens
    }
  })

  const dispatch = useDispatch()
  const addTokenToRecentList = (token: any) => {
    dispatch(addRecentToken({ token }))
  }

  return {
    addTokenToRecentList,
    recentTokens
  }
}
