import React, { useEffect, useRef, useState } from 'react'
import './style.scss'
import { Input } from '../../ui/Input'
import { SearchIcon } from '../../ui/Icons'
import { sub } from '../../../utils/helpers'
import { ethers } from 'ethers'
import { searchTokens } from '../../../service/tokenService'
import _ from 'lodash'
import { TokenOption } from '../../ui/SelectTokenModal/TokenOption'
import { useWindowSize } from '../../../hooks/useWindowSize'
import { Tabs } from '../../ui/Tabs'
import { useCurrentPair } from '../../../state/currentPair/hook'
import {
  useBookmark,
  useRecentToken
} from '../../../state/token/hooks/useBookmarkAndRecent'
import { useCustomToken, useListTokens } from '../../../state/token/hook'
import { useWalletBalance } from '../../../state/wallet/hook'
import { TokenIcon } from '../../ui/Image'

const TABS = {
  ALL: 1,
  FAVORITE: 2,
  RECENT: 3
}

export const SelectTokenInput = ({ onSelect }: { onSelect: any }) => {
  const { balances } = useWalletBalance()
  const { tokens } = useListTokens()
  const { addTokenToBookmark, bookmarkAddresses } = useBookmark()
  const { addTokenToRecentList, recentTokens } = useRecentToken()
  const { inputTokenAddress } = useCurrentPair()
  const [searchKeyword, setSearchKeyword] = useState<string>('')
  const { addCustomToken } = useCustomToken()

  const [tab, setTab] = useState<any>(TABS.ALL)
  const [visible, setVisible] = useState<boolean>(false)
  const [resultTokens, setResultTokens] = useState<any>([])
  const [tokensFromApi, setTokensFromApi] = useState<any>([])
  const inputRef = useRef<any>(null)

  useEffect(() => {
    let res = [...Object.values(tokens), ...tokensFromApi]
    res = _.uniqBy(res, 'address')

    setResultTokens(res)
  }, [tokensFromApi, tokens])

  useEffect(() => {
    window.addEventListener('keyup', (e: any) => {
      if (e.which === 27) {
        setVisible(false)
      }
    })
  }, [])

  const getTokenToShow = () => {
    let result = []

    if (tab === TABS.ALL) {
      result = resultTokens
    } else if (tab === TABS.RECENT) {
      result = recentTokens
    } else if (tab === TABS.FAVORITE) {
      result = resultTokens.filter((token: any) => {
        return (
          bookmarkAddresses.includes(token.address) || tab !== TABS.FAVORITE
        )
      })
    }

    return result
      .filter((token: any) => {
        return (
          token.symbol.toLowerCase().includes(searchKeyword.toLowerCase()) ||
          token.name.toLowerCase().includes(searchKeyword.toLowerCase()) ||
          token.address.toLowerCase() === searchKeyword.toLowerCase()
        )
      })
      .sort((a: any, b: any) => {
        if (tab === TABS.RECENT) {
          return 1
        }
        return Number(sub(balances[b.address] || 0, balances[a.address] || 0))
      })
  }

  const { width } = useWindowSize()
  const isSmallDesktop = width && width < 1200

  const tabs = [
    { value: TABS.ALL, name: 'All' },
    { value: TABS.FAVORITE, name: 'Favourites' }
  ]
  if (isSmallDesktop) {
    tabs.push({
      value: TABS.RECENT,
      name: 'Recent searches'
    })
  }

  return (
    <div className={`search-token-dropdown ${visible ? 'visible' : ''}`}>
      <Input
        ref={inputRef}
        onFocus={() => setVisible(true)}
        // onBlur={() => setSearchKeyword('')}
        type='text'
        placeholder='Search name or paste address'
        value={visible ? searchKeyword : ''}
        onChange={async (e: any) => {
          setSearchKeyword(e.target.value)
          if (ethers.utils.isAddress(e.target.value)) {
            const checksumAddress = ethers.utils.getAddress(e.target.value)
            if (tokens[checksumAddress]) return

            addCustomToken(e.target.value)
          }
          const res: any = await searchTokens(e.target.value)
          if (inputRef?.current?.value === res.keyword) {
            setTokensFromApi(res.tokens)
          }
        }}
        suffix={<SearchIcon className='mt-1' />}
        prefix={
          visible ? (
            ''
          ) : (
            <div className='input-prefix'>
              <TokenIcon
                src={
                  tokens[inputTokenAddress] && tokens[inputTokenAddress].icon
                    ? tokens[inputTokenAddress].icon
                    : `https://farm.army/bsc/token/${tokens[
                        inputTokenAddress
                      ]?.address.toLowerCase()}.webp`
                }
                className='token-icon'
                size={24}
              />
              <span className='token-symbol text-gradient'>
                {tokens[inputTokenAddress]
                  ? tokens[inputTokenAddress].symbol
                  : 'Select token'}
              </span>
            </div>
          )
        }
        className={`search-token-dropdown__input ${visible ? 'focusing' : ''}`}
      />
      <div
        className={`overlay ${!visible ? 'hidden' : ''}`}
        onClick={() => setVisible(false)}
      />
      <div className={`search-token-dropdown__modal  ${visible ? 'show' : ''}`}>
        <div className='search-token-box'>
          <Tabs
            tab={tab}
            tabs={tabs}
            setTab={(e: any) => {
              if (inputRef?.current) {
                // @ts-ignore
                inputRef.current.focus()
              }
              setTab(e)
            }}
          />
          <div className='modal-content'>
            <div
              className='select-token'
              onSubmit={(event) => {
                event.preventDefault()
              }}
            >
              <div className='select-token__options'>
                {getTokenToShow().map((token: any, key: number) => (
                  <TokenOption
                    key={key}
                    inBookmark={bookmarkAddresses?.includes(token.address)}
                    token={token}
                    onSelect={(token: any) => {
                      setSearchKeyword('')
                      onSelect(token)
                      setVisible(false)
                      addTokenToRecentList(token)
                    }}
                    onClickBookmark={() => addTokenToBookmark(token)}
                    isWide={!isSmallDesktop}
                  />
                ))}
              </div>
            </div>
          </div>
        </div>
        {!isSmallDesktop && recentTokens.length > 0 ? (
          <div className='recent-token-box'>
            <span className='modal-title'>Recent searches</span>
            <div className='recent-token'>
              {recentTokens.map((token: any, key: number) => (
                <TokenOption
                  key={key}
                  inBookmark={bookmarkAddresses?.includes(token.address)}
                  token={token}
                  onSelect={(token: any) => {
                    setSearchKeyword('')
                    onSelect(token)
                    setVisible(false)
                    addTokenToRecentList(token)
                  }}
                />
              ))}
            </div>
          </div>
        ) : (
          ''
        )}
      </div>
    </div>
  )
}
