import React, { useEffect } from 'react'
import './style.scss'
import { ExitIcon } from '../Icons'

export interface modalInterface {
  setVisible: any
  visible: any
  children: any
  width?: any
  minWidth?: any
  title?: any
}

export default ({
  width,
  minWidth,
  setVisible,
  visible,
  children,
  title
}: modalInterface) => {
  useEffect(() => {
    window.addEventListener('keyup', (e: any) => {
      if (e.which === 27) {
        setVisible(false)
      }
    })
  }, [])

  return (
    <div className={`swap-modal  ${visible ? 'show' : ''}`}>
      <div className='overlay' onClick={() => setVisible(false)} />
      <div
        className='modal'
        style={{ width: width || '40rem', minWidth: minWidth || '40rem' }}
      >
        <div className='btn-close-wrap'>
          <span className='title'>{title || ''}</span>
          <span className='btn-close' onClick={() => setVisible(false)}>
            <ExitIcon />
          </span>
        </div>
        <div className='modal-content'>{children}</div>
      </div>
    </div>
  )
}
