import React, { useEffect, useState } from 'react'
import Modal, { modalInterface } from '../../ui/Modal'
import './style.scss'
import { LoadingIcon } from '../../ui/Icons'
import {
  detectDecimalFromPrice,
  formatFloat,
  weiToNumber
} from '../../../utils/helpers'
import { formatWeiToDisplayNumber } from '../../../utils/formatBalance'

interface WaitingSwapModalInterface extends Omit<modalInterface, 'children'> {
  inputToken: any
  inputAmount: string
  children?: any
  outputAmount: string
  outputToken: any
}

export const WaitingSwapModal = ({
  setVisible,
  visible,
  inputToken,
  inputAmount,
  outputAmount,
  outputToken
}: WaitingSwapModalInterface) => {
  const [inAmount, setInAmount] = useState<any>('')
  const [outAmount, setOutAmount] = useState<any>('')

  useEffect(() => {
    if (visible) {
      setInAmount(inputAmount)
      setOutAmount(outputAmount)
    }
  }, [visible])

  if (!inputToken) {
    return <div />
  }

  return (
    <Modal visible={visible} setVisible={setVisible}>
      <div className='waiting-swap-modal'>
        <LoadingIcon size={100} className='waiting-swap-modal__loading-icon' />
        <span className='font-size-18'>Waiting For Confirmation</span>
        <span className='font-size-16'>
          {inAmount
            ? `Swapping ${formatFloat(
                inAmount,
                detectDecimalFromPrice(inAmount)
              )} ${inputToken.symbol} for ${formatWeiToDisplayNumber(
                outAmount || '0',
                detectDecimalFromPrice(weiToNumber(outAmount))
              )} ${outputToken.symbol}`
            : ''}
        </span>
        <span className='font-size-14'>
          Confirm this transaction in your wallet
        </span>
      </div>
    </Modal>
  )
}
