export const SET_CHART_HAVE_NO_DATA = Symbol('SET_CHART_HAVE_NO_DATA')
export const SET_LAST_CANDLE = Symbol('SET_LAST_CANDLE')
export const SET_FIRST_BLOCK = Symbol('SET_FIRST_BLOCK')

const initialWallet = {
  noChartData: false,
  lastCandle: {},
  firstBlock: {}
}

export const reduceChart = (state = initialWallet, action: any) => {
  switch (action.type) {
    case SET_LAST_CANDLE:
      return {
        ...state,
        lastCandle: {
          [action.data.pair]: action.data.candle
        }
      }
    case SET_FIRST_BLOCK:
      return {
        ...state,
        firstBlock: {
          ...state.firstBlock,
          [action.data.pair]: action.data.block
        }
      }
    case SET_CHART_HAVE_NO_DATA:
      return {
        ...state,
        noChartData: action.data
      }
    default:
      return state
  }
}
