import { useDispatch, useSelector } from 'react-redux'
import historyProvider from '../../service/datafeed/historyProvider'
import { setChartInfo, setPriceScale } from './reducer'
import {
  bn,
  convertBNBAddressToWBNB,
  detectDecimalFromPrice
} from '../../utils/helpers'
import { STABLECOIN_ADDRESSES } from '../../utils/constant'
import { useWindowSize } from '../../hooks/useWindowSize'
import { useListTokens } from '../token/hook'
import { getTokenRate } from 'swap-tools-2'

export const useChartInfo = () => {
  const { tokens } = useListTokens()
  const { width } = useWindowSize()
  const { routerArr, symbolName } = useSelector((state: any) => {
    return {
      routerArr: state.chart.routeArr,
      symbolName: state.chart.symbolName
    }
  })

  const dispatch = useDispatch()

  const fetchChartInfo = async (
    inputAddress: string,
    outputAddress: string
  ) => {
    if (
      STABLECOIN_ADDRESSES.includes(inputAddress.toLowerCase()) &&
      !STABLECOIN_ADDRESSES.includes(outputAddress.toLowerCase())
    ) {
      const tmp = inputAddress
      inputAddress = outputAddress
      outputAddress = tmp
    }

    inputAddress = convertBNBAddressToWBNB(inputAddress)
    outputAddress = convertBNBAddressToWBNB(outputAddress)

    let { chartName, routes, routeDetail } = await historyProvider.getRoutes(
      inputAddress,
      outputAddress
    )

    if (width && width < 768) {
      chartName = chartName.filter((_e: any, i) => {
        return i % 2 === 0
      })
    }

    const priceScale = await detectPriceScale(
      routeDetail,
      tokens[inputAddress],
      tokens[outputAddress]
    )

    dispatch(setPriceScale({ priceScale }))
    dispatch(
      setChartInfo({
        routerArr: routes,
        name: chartName.join('/')
      })
    )
  }

  const detectPriceScale = async (
    routes: any,
    inputToken: any,
    outputToken: any
  ) => {
    routes = [...routes].map((r: any) => {
      r.ri = bn(r.ri)
      r.ro = bn(r.ro)
      return r
    })
    const price = await getTokenRate(
      {
        address: inputToken.address,
        decimals: inputToken.decimal
      },
      {
        address: outputToken.address,
        decimals: outputToken.decimal
      },
      routes
    )

    return price ? 10 ** detectDecimalFromPrice(price) : 1000
  }

  return {
    routerArr,
    symbolName,
    fetchChartInfo
  }
}
