import React, { useState } from 'react'
import './style.scss'
import { SelectTokenModal } from '../../ui/SelectTokenModal'
import {
  bn,
  detectDecimalFromPrice,
  formatFloat,
  numberToWei,
  weiToNumber
} from '../../../utils/helpers'
import { LoadingIcon } from '../../ui/Icons'
import { useWalletBalance } from '../../../state/wallet/hook'
import { getConfigAddress } from '../../../utils/addressHelper'
import { formatWeiToDisplayNumber } from '../../../utils/formatBalance'
import { TokenIcon } from '../../ui/Image'

export const TokenInputField = ({
  className,
  token,
  setToken,
  amount,
  setAmount,
  label,
  loading = false,
  myRef,
  findingToken = false
}: {
  token?: any
  setToken?: any
  className?: string
  amount?: string
  setAmount?: any
  label: string
  loading?: boolean
  myRef?: any
  findingToken?: boolean
}) => {
  const { balances } = useWalletBalance()
  const [visibleSelectToken, setVisibleSelectToken] = useState<boolean>(false)

  return (
    <div className={`${className}`}>
      <div className='input-token-field'>
        <div className='input-token-field__header'>
          <span className='label'>
            <span>{label}</span>
            <span>{loading && <LoadingIcon />}</span>
          </span>
          {findingToken ? (
            <div
              className='skeleton-loader__bone'
              style={{ width: '6rem', height: '2rem' }}
            />
          ) : (
            <span>
              Balance:{' '}
              {token && token.address && balances && balances[token.address]
                ? formatFloat(
                    weiToNumber(balances[token.address], token.decimal || 18),
                    4
                  )
                : 0}
            </span>
          )}
        </div>
        <div className='input-token-field__content'>
          <div className='amount-input-box'>
            {!setAmount ? (
              <span className='amount-input'>
                {Number(amount || 0) !== 0
                  ? formatWeiToDisplayNumber(
                      numberToWei(amount),
                      //@ts-ignore
                      detectDecimalFromPrice(amount)
                    )
                  : '0.0'}
              </span>
            ) : (
              <input
                ref={myRef}
                type='number'
                placeholder='0.0'
                value={amount}
                disabled={!setAmount}
                onChange={(e) => {
                  if (
                    typeof Number(e.target.value) === 'number' &&
                    Number(e.target.value) >= 0
                  ) {
                    setAmount(e.target.value)
                  }
                }}
                className='amount-input'
              />
            )}
          </div>
          {!!setAmount &&
            (findingToken ? (
              <span className='max-btn__placeholder skeleton-loader__bone' />
            ) : (
              <button
                className='max-btn'
                onClick={() => {
                  if (token?.address) {
                    let am = balances[token.address]
                    if (
                      token.address.toLowerCase() ===
                      getConfigAddress('bnb').toLowerCase()
                    ) {
                      am = bn(balances[token.address]).gt(numberToWei(0.01))
                        ? bn(balances[token.address]).sub(numberToWei(0.01))
                        : '0'
                    }

                    setAmount(weiToNumber(am, token.decimal))
                  } else {
                    setAmount('0')
                  }
                }}
              >
                MAX
              </button>
            ))}

          {findingToken ? (
            <span className='skeleton-loader__bone select-token-btn__placeholder' />
          ) : (
            <button
              className='select-token-btn'
              onClick={() => setVisibleSelectToken(true)}
            >
              <TokenIcon
                src={
                  token && token.icon
                    ? token.icon
                    : `https://farm.army/bsc/token/${token?.address.toLowerCase()}.webp`
                }
                className='token-icon'
                size={20}
              />
              <span>{token && token.symbol}</span>
              <svg
                width='12'
                height='7'
                viewBox='0 0 12 7'
                fill='none'
                className='sc-jrIrqw izEuJL'
              >
                <path d='M0.97168 1L6.20532 6L11.439 1' stroke='#9CA3AF' />
              </svg>
            </button>
          )}
        </div>
      </div>

      <SelectTokenModal
        visible={visibleSelectToken}
        setVisible={setVisibleSelectToken}
        onSelect={setToken}
      />
    </div>
  )
}
