import config, { getConfig } from '../config'
import LZPReward from '../assets/abi/LZPReward.json'
import { ethers } from 'ethers'

const Web3Util = require('web3-utils')
const keccak256 = require('keccak256')
const { MerkleTree } = require('merkletreejs')
const dayjs = require('dayjs')
const utc = require('dayjs/plugin/utc')
dayjs.extend(utc)

export const getLeaderBoard = async ({
  page,
  limit,
  from,
  to
}: {
  page: number
  limit: number
  from: number
  to: number
}) => {
  var url =
    getConfig('REWARD_API_ENDPOINT') +
    'dex/getLeaderBoard?' +
    new URLSearchParams({
      from: timestampToDate(from),
      to: timestampToDate(to),
      page: page.toString(),
      limit: limit.toString()
    }).toString()

  const res = await fetch(url).then((r: any) => r.json())
  return res.data
}

export const getRewardOfAccount = async ({ account }: { account: string }) => {
  var url =
    getConfig('REWARD_API_ENDPOINT') + 'dex/rewardOfSender?sender=' + account

  const res = await fetch(url).then((r: any) => r.json())
  return res.data.reward
}

export const calcClaimParams = async ({ provider, account }: any) => {
  const signer = provider.getSigner()
  const rewardContract = new ethers.Contract(
    config.chain[56].addresses.lzpReward,
    LZPReward.abi,
    signer
  )
  const subTractDay = [1, 2, 3, 4, 5, 6, 7]
  let trancheIds = subTractDay.map((day: number) =>
    dayjs().utc().subtract(day, 'day').startOf('day').unix()
  )

  const tranchesAndClaimed = await Promise.all([
    ...trancheIds.map((id: number) => rewardContract.tranches(id)),
    ...trancheIds.map((id: number) => rewardContract.claimed(id, account))
  ])

  trancheIds = trancheIds.filter(
    // @ts-ignore
    (id: any, key) =>
      tranchesAndClaimed[key].exist &&
      !tranchesAndClaimed[key + tranchesAndClaimed.length / 2]
  )

  let proofTranches: any = []
  let amounts: any = []
  let resultTrancheIds = []

  const whitelistDays = await Promise.all(
    trancheIds.map((id: number) => getWhitelist(id))
  )

  for (let i = 0; i < trancheIds.length; i++) {
    const whitelistDay = whitelistDays[i]
    const foundTranche1 = whitelistDay.find(
      (it: any) => it.address.toLowerCase() === account.toLowerCase()
    )
    if (foundTranche1) {
      const leavesTranche1 = whitelistDay.map((v: any) =>
        Web3Util.soliditySha3(Web3Util.toChecksumAddress(v.address), v.amount)
      )
      const treeTranche1 = new MerkleTree(leavesTranche1, keccak256, {
        sort: true
      })
      const leafTranche1 = Web3Util.soliditySha3(
        ethers.utils.getAddress(foundTranche1.address),
        foundTranche1.amount
      )
      const proofTranche1 = treeTranche1.getHexProof(leafTranche1)
      proofTranches.push(proofTranche1)
      amounts.push(ethers.BigNumber.from(foundTranche1.amount))
      resultTrancheIds.push(trancheIds[i])
    }
  }

  // verify claimed
  const verifyClaimed = await Promise.all(
    resultTrancheIds.map((resultTrancheId, key: number) =>
      rewardContract.functions.verifyClaim(
        account,
        resultTrancheId,
        amounts[key],
        proofTranches[key]
      )
    )
  )
  proofTranches = proofTranches.filter(
    // @ts-ignore
    (e, key: number) => verifyClaimed[key].valid
  )
  // @ts-ignore
  amounts = amounts.filter((e, key: number) => verifyClaimed[key].valid)
  resultTrancheIds = resultTrancheIds.filter(
    // @ts-ignore
    (e, key: number) => verifyClaimed[key].valid
  )

  return {
    proofTranches,
    amounts,
    trancheIds: resultTrancheIds
  }
}

export const claim = async ({
  provider,
  account,
  gasPrice,
  gasAmount,
  trancheIds,
  amounts,
  proofTranches,
  checkError = false
}: any) => {
  try {
    const signer = provider.getSigner()
    const rewardContract = new ethers.Contract(
      config.chain[56].addresses.lzpReward,
      LZPReward.abi,
      signer
    )

    if (checkError) {
      await rewardContract.callStatic.claimDays(
        account,
        trancheIds,
        amounts,
        proofTranches
      )
      return true
    } else {
      const tx = await rewardContract.functions.claimDays(
        account,
        trancheIds,
        amounts,
        proofTranches,
        {
          gasLimit: ethers.BigNumber.from(gasAmount).mul(120).div(100),
          gasPrice: gasPrice
        }
      )
      tx.wait(1)
      return tx.hash
    }
  } catch (error) {
    if (error?.data?.message) {
      throw new Error(error?.data?.message)
    } else if (error?.message) {
      throw new Error(error.message)
    }
    throw new Error('Transaction error, please try again later')
    return false
  }
}

export const estimateGas = async ({
  provider,
  account,
  trancheIds,
  amounts,
  proofTranches
}: any) => {
  try {
    const signer = provider.getSigner()
    const rewardContract = new ethers.Contract(
      config.chain[56].addresses.lzpReward,
      LZPReward.abi,
      signer
    )

    return await rewardContract.estimateGas.claimDays(
      account,
      trancheIds,
      amounts,
      proofTranches
    )
  } catch (error) {
    if (error?.data?.message) {
      throw new Error(error?.data?.message)
    } else if (error?.message) {
      throw new Error(error)
    }
    throw new Error('Transaction error, please try again later')
  }
}

const timestampToDate = (timestamp: number) => {
  const date = new Date(timestamp)
  return date.getMonth() + 1 + '-' + date.getDate() + '-' + date.getFullYear()
}

const getWhitelist = async (trancheId: number) => {
  const res = await fetch(
    `https://raw.githubusercontent.com/poorprogramer/lzpreward/main/${trancheId}.json`
  ).then((r) => r.json())

  return res
}
