import React, { useEffect, useLayoutEffect } from 'react'
import { toast, ToastContainer } from 'react-toastify'
import { useDispatch, useSelector } from 'react-redux'
import { setTab } from './store/actions/initSettingData'
import Swap from './components/Swap'
import { Chart } from './components/Chart'
import Card from './components/ui/Card'
import Summary from './components/Summary'
import TransactionHistory from './components/TransactionHistory'
import 'react-toastify/dist/ReactToastify.css'
import './styles/main.scss'
import { useWindowSize } from './hooks/useWindowSize'
import { useFetchCommonData } from './hooks/useFetchCommonData'
import { RewardTable } from './components/Reward'
import config from './config'
import { useInitWeb3React } from './state/customWeb3React/hook'
import { useSetting } from './state/setting/hook'
// import config from "./config";
const TABS = ['swap', 'chart', 'summary', 'reward']
export default ({
  theme,
  useWeb3React,
  language,
  useSubPage,
  xStorageClient,
  showConnectWalletModal
}: {
  theme: string
  useWeb3React: any
  language?: any
  useSubPage: any
  xStorageClient: any
  showConnectWalletModal: () => void
}) => {
  const { chainId } = useWeb3React()
  const path = useSubPage()
  const { setBlockConfig } = useSetting()
  useInitWeb3React(useWeb3React, showConnectWalletModal)
  useFetchCommonData({
    theme,
    useWeb3React,
    language,
    useSubPage,
    xStorageClient
  })
  useLayoutEffect(() => {
    config.provider
      .getBlockNumber()
      .then((head: number) => {
        const t = (new Date().getTime() - 1598646248000) / head
        setBlockConfig({
          timePerBlock: t,
          headBlock: head,
          loadedTimePerBlock: true
        })
      })
      .catch((e: any) => {
        console.log(e)
        toast.error(
          'Connection to BSC network is unstable, please check your internet connection or try again later.'
        )
        setBlockConfig({
          loadedTimePerBlock: true
        })
      })
  }, [])

  useEffect(() => {
    const initTab: any = getInitTab()

    if (initTab && TABS.includes(initTab)) {
      setTab(initTab)(dispatch)
    }
  }, [path, chainId])

  const getInitTab = () => {
    const search = window.location.search
    const params = new URLSearchParams(search)
    return params.get('tab')
  }

  const dispatch = useDispatch()
  const { tab } = useSelector((state: any) => ({
    tab: state.swap.tab
  }))
  const { width } = useWindowSize()
  const isPhone = width && width < 992

  const tabs = (
    <div className='hidden-on-desktop'>
      <div className='select-chart-or-swap swap-tab'>
        <span
          className={`chart-button ${tab === 'chart' ? 'active' : ''}`}
          onClick={() => setTab('chart')(dispatch)}
        >
          Chart
        </span>
        <span
          className={`swap-button ${tab === 'swap' ? 'active' : ''}`}
          onClick={() => setTab('swap')(dispatch)}
        >
          Swap
        </span>
        <span
          className={`swap-button ${tab === 'summary' ? 'active' : ''}`}
          onClick={() => setTab('summary')(dispatch)}
        >
          Summary
        </span>
        <span
          className={`swap-button ${tab === 'reward' ? 'active' : ''}`}
          onClick={() => setTab('reward')(dispatch)}
        >
          Reward
        </span>
      </div>
    </div>
  )

  return (
    <div className={`swap-page-interface ${theme}`}>
      {/*<div className='header hidden-on-phone'>*/}
      {/*  <span className='text-gradient title'>Swap 2.0</span>*/}
      {/*  <span className='text-gradient beta-title'>(Beta)</span>*/}
      {/*</div>*/}
      <div className='swap-page-content'>
        <Card
          className={
            'chart__container ' + (isPhone && tab !== 'chart' ? 'hidden' : '')
          }
        >
          {tabs}
          <Chart />
        </Card>
        <Card
          className={
            'swap__container ' + (isPhone && tab !== 'swap' ? 'hidden' : '')
          }
        >
          {tabs}
          <Swap />
        </Card>
        <Card
          className={
            'info__container ' + (isPhone && tab !== 'summary' ? 'hidden' : '')
          }
        >
          {tabs}
          <Summary />
        </Card>
        <Card
          className={
            'reward__container ' + (isPhone && tab !== 'reward' ? 'hidden' : '')
          }
        >
          {tabs}
          <RewardTable />
        </Card>
        <Card className='tx-history__container'>
          <TransactionHistory />
        </Card>
      </div>
      <ToastContainer
        position='top-right'
        autoClose={5000}
        rtl={false}
        closeOnClick={false}
        draggable
        theme={theme === 'dark' ? 'dark' : 'light'}
      />
    </div>
  )
}
