import { BigNumber } from 'ethers'
import { useWeb3React } from '../../customWeb3React/hook'
import { getBep20Contract } from '../../../utils/contractHelpers'
import config from '../../../config'
import { useDispatch, useSelector } from 'react-redux'
import { setAgtAllowance } from '../reducer'
import { State } from '../../types'

export const useWalletAllowance = () => {
  const { aggregatorAllowance } = useSelector((state: State) => {
    return {
      aggregatorAllowance: state.wallet.aggregatorAllowance
    }
  })

  const { account } = useWeb3React()

  const dispatch = useDispatch()

  const fetchAggregatorAllowance = async (tokenAddress: string) => {
    try {
      const tokenContract = getBep20Contract(tokenAddress)
      const res = await tokenContract.functions.allowance(
        account,
        config.chain[56].addresses.aggregator
      )
      setAggregatorAllowance(tokenAddress, res[0] || BigNumber.from(0))
    } catch (e) {
      setAggregatorAllowance(tokenAddress, BigNumber.from(0))
    }
  }

  const setAggregatorAllowance = (
    tokenAddress: string,
    allowance: BigNumber
  ) => {
    dispatch(
      setAgtAllowance({
        allowance,
        tokenAddress
      })
    )
  }

  return {
    fetchAggregatorAllowance,
    aggregatorAllowance,
    setAggregatorAllowance
  }
}
