import React from 'react'
import './style.scss'
import { CheckedIcon, NotCheckedIcon } from '../Icons'

export const Checkbox = (props: React.HTMLAttributes<HTMLInputElement>) => {
  return (
    <span className={`swap-checkbox ${props.className}`}>
      <input {...props} type='checkbox' className='swap-checkbox__checkbox' />
      <CheckedIcon className='checked-icon' />
      <NotCheckedIcon className='not-checked-icon' />
    </span>
  )
}
