import { ethers } from 'ethers'

// Addresses
// ABI
import bep20Abi from '../assets/abi/IERC20.json'
import WbnbAbi from '../assets/abi/Wbnb.json'
import aggregatorAbi from '../assets/abi/Aggregator.json'
import config from '../config'
import { getConfigAddress } from './addressHelper'

const getContract = (
  abi: any,
  address: string,
  signer?: ethers.Signer | ethers.providers.Provider
) => {
  const signerOrProvider = signer ?? config.provider
  return new ethers.Contract(address, abi, signerOrProvider)
}

export const getBep20Contract = (
  address: string,
  signer?: ethers.Signer | ethers.providers.Provider
) => {
  return getContract(bep20Abi, address, signer)
}

export const getWbnbContract = (
  signer?: ethers.Signer | ethers.providers.Provider
) => {
  return getContract(WbnbAbi, getConfigAddress('wbnb'), signer)
}

export const getAggregatorContract = (
  signer?: ethers.Signer | ethers.providers.Provider
) => {
  return getContract(
    aggregatorAbi,
    config.chain[56].addresses.aggregator,
    signer
  )
}
