import React, { useEffect, useState } from 'react'
import { ArrowRightIcon, ExchangeIcon, LoadingIcon } from '../../ui/Icons'
import Modal from '../../ui/Modal'
import { useListTokens } from '../../../state/token/hook'
import { useCurrentPair } from '../../../state/currentPair/hook'
import { useSwapInfo } from '../../../state/marketSwap/hooks/useSwapInfo'
import {
  bn,
  formatAddress,
  getAmmNameFromRoute,
  numberToWei
} from '../../../utils/helpers'
import { formatWeiToDisplayNumber } from '../../../utils/formatBalance'
import { TokenIcon } from '../../ui/Image'

export const AmmRoutes = ({
  distribution,
  steps
}: {
  distribution: any
  steps: any
}) => {
  const [maps, setMaps] = useState<any>([])
  const [visible, setVisible] = useState<any>(false)
  const { inputTokenAddress, outputTokenAddress } = useCurrentPair()
  const { routes, routesCap, getAmountOutFinished } = useSwapInfo()

  useEffect(() => {
    if (
      !distribution ||
      distribution.length === 0 ||
      routesCap.length === 0 ||
      routes.value.length === 0 ||
      distribution.length > routesCap.length
    )
      return
    const newMaps = []

    if (!distribution && steps && steps.length > 0) {
      // newMaps.push({
      //   percent: bn(numberToWei(1)).mul(100),
      //   steps: steps.map((s: any) => {
      //     return {
      //       path: [s.ai, s.ao],
      //       router: s.c
      //     }
      //   })
      // })
    } else {
      for (let i = 0; i < distribution.length; i++) {
        if (distribution[i] > 0) {
          let stepsData = routesCap[i].steps
          stepsData = stepsData.map((s: any) => {
            return {
              path: [s.ai, s.ao],
              router: s.c
            }
          })
          newMaps.push({
            dist: distribution[i],
            percent: bn(numberToWei(distribution[i])).mul(100),
            steps: stepsData
          })
        }
      }
    }
    setMaps(newMaps)
  }, [distribution, routes, steps])

  return (
    <div>
      {[
        <div
          key={1}
          className='routes'
          style={{
            cursor:
              (steps.length || distribution) && maps && maps.length
                ? 'pointer'
                : 'not-allowed'
          }}
          onClick={() => {
            if ((steps.length || distribution) && maps && maps.length > 0) {
              setVisible(true)
            }
          }}
        >
          <span className='routes__title'>Route</span>
          <div className='routes__box'>
            <TokenIconAndName
              address={inputTokenAddress}
              size={20}
              showName={false}
            />
            <ArrowRightIcon className='ml-1 mr-1' />
            <TokenIconAndName
              address={outputTokenAddress}
              showName={false}
              size={20}
            />
          </div>
        </div>,
        <Modal
          key={2}
          visible={visible}
          setVisible={setVisible}
          title={
            <div className='routing-modal-title'>
              <span>Routing</span>
              {!getAmountOutFinished && <LoadingIcon />}
            </div>
          }
          width='auto'
          minWidth='360px'
        >
          <div className='routing-modal'>
            <div className='routing-modal__routing'>
              {maps.length > 0 &&
                maps
                  .sort((a: any, b: any) => {
                    return a.dist && b.dist ? b.dist - a.dist : 1
                  })
                  .map((data: any, key: number) => {
                    const stepsData = data.steps
                    return (
                      <div className='routing-modal__step-box' key={key}>
                        <div
                          className={`routing-modal__step ${
                            stepsData.length === 1 && 'justify-content-center'
                          }`}
                        >
                          {stepsData.map((step: any, subKey: number) => {
                            const amm: any = getAmmNameFromRoute(step.router)
                            const path = step.path
                            return [
                              <div
                                key={1}
                                className='routing-modal__step--element'
                              >
                                <div className='routing-modal__step--element-label '>
                                  <TokenIconAndName
                                    address={formatAddress(path[0])}
                                    key={1}
                                  />
                                </div>
                              </div>,
                              <ExchangeIcon
                                key={2}
                                exchange={amm}
                                className='exchange-icon'
                              />,
                              subKey === stepsData.length - 1 && (
                                <div
                                  key={3}
                                  className='routing-modal__step--element'
                                >
                                  <div className='routing-modal__step--element-label '>
                                    <TokenIconAndName
                                      address={formatAddress(path[1])}
                                    />
                                  </div>
                                </div>
                              )
                            ]
                          })}
                        </div>
                        <div className='routing-modal__step--element-value'>
                          {formatWeiToDisplayNumber(data.percent, 4)}%
                        </div>
                      </div>
                    )
                  })}
            </div>
          </div>
        </Modal>
      ]}
    </div>
  )
}

const TokenIconAndName = ({
  address,
  size,
  showName = true
}: {
  address: string
  size?: number
  showName?: boolean
}) => {
  const { tokens } = useListTokens()

  return (
    <div
      style={{ display: 'flex', alignItems: 'center' }}
      className='token-icon-and-name'
    >
      <TokenIcon
        src={
          tokens[address]?.icon
            ? tokens[address].icon
            : address
            ? `https://farm.army/bsc/token/${address.toLowerCase()}.webp`
            : null
        }
        className='token-icon'
        size={size || 50}
        alt={address}
      />
      {showName && <span>{tokens[address]?.symbol}</span>}
    </div>
  )
}
